(function (angular) {
  'use strict';

  /**
   * @ngdoc overview
   * @name coyo.admin.api-clients
   *
   * @description
   * # Admin API clients management module #
   * The admin API clients management module provides views to manage API clients.
   */
  angular
      .module('coyo.admin.apiClients', [
        'coyo.base',
        'commons.auth',
        'coyo.domain'
      ])
      .config(ModuleConfig)
      .constant('adminApiClientsConfig', {
        templates: {
          apiClientsList: 'app/modules/admin/api-clients/views/api-clients-list/admin.api-clients-list.html',
          apiClientsDetails: 'app/modules/admin/api-clients/views/api-clients-details/admin.api-clients-details.html'
        }
      });

  /**
   * Module configuration
   */
  function ModuleConfig($stateProvider, adminApiClientsConfig) {
    $stateProvider.state('admin.api-clients', {
      abstract: true,
      url: '/api-clients',
      template: '<ui-view></ui-view>',
      data: {
        globalPermissions: 'MANAGE_API_CLIENTS',
        pageTitle: 'ADMIN.MENU.API_CLIENTS'
      }
    }).state('admin.api-clients.list', {
      url: '',
      views: {
        '@admin.api-clients': {
          templateUrl: adminApiClientsConfig.templates.apiClientsList,
          controller: 'AdminApiClientsListController',
          controllerAs: '$ctrl'
        }
      }
    }).state('admin.api-clients.create', {
      url: '/create',
      views: {
        '@admin.api-clients': {
          templateUrl: adminApiClientsConfig.templates.apiClientsDetails,
          controller: 'AdminApiClientsDetailsController',
          controllerAs: '$ctrl'
        }
      },
      resolve: {
        client: function (ApiClientModel) {
          return new ApiClientModel();
        }
      }
    });
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.admin.apps-widgets', [
        'coyo.domain',
        'coyo.apps.api',
        'coyo.widgets.api'
      ])
      .config(ModuleConfig)
      .constant('adminAppsWidgetsConfig', {
        templates: {
          home: 'app/modules/admin/apps-widgets/admin.apps-widgets.html',
          appList: 'app/modules/admin/apps-widgets/views/app-list/admin.app-list.html',
          widgetList: 'app/modules/admin/apps-widgets/views/widget-list/admin.widget-list.html'
        }
      });

  /**
   * @ngdoc overview
   * @name coyo.admin.apps-widgets
   *
   * @description
   * This module contains controllers for managing apps and widgets in the admin backend.
   *
   * @requires $stateProvider
   * @requires adminAppsWidgetsConfig
   */
  function ModuleConfig($stateProvider, adminAppsWidgetsConfig) {
    $stateProvider.state('admin.apps-widgets', {
      url: '/apps-widgets',
      templateUrl: adminAppsWidgetsConfig.templates.home,
      redirect: 'admin.apps-widgets.apps',
      data: {
        pageTitle: 'ADMIN.MENU.APPS_WIDGETS',
        globalPermissions: 'MANAGE_APPS_WIDGETS'
      }
    }).state('admin.apps-widgets.apps', {
      url: '/apps',
      resolve: {
        appConfigs: function (AppConfigurationModel) {
          return AppConfigurationModel.query();
        }
      },
      views: {
        '@admin.apps-widgets': {
          templateUrl: adminAppsWidgetsConfig.templates.appList,
          controller: 'AdminAppListController',
          controllerAs: '$ctrl'
        }
      }
    }).state('admin.apps-widgets.widgets', {
      url: '/widgets',
      resolve: {
        widgetConfigs: function (WidgetConfigurationModel) {
          return WidgetConfigurationModel.query();
        }
      },
      views: {
        '@admin.apps-widgets': {
          templateUrl: adminAppsWidgetsConfig.templates.widgetList,
          controller: 'AdminWidgetListController',
          controllerAs: '$ctrl'
        }
      }
    });
  }

})(angular);

