(function (angular) {
  'use strict';

  angular
      .module('coyo.domain')
      .factory('DocumentModel', DocumentModel);

  /**
   * @ngdoc service
   * @name coyo.domain.DocumentModel
   *
   * @description
   * Provides the Coyo document model.
   *
   * @requires restResourceFactory
   * @requires commons.config.coyoEndpoints
   */
  function DocumentModel(restResourceFactory, coyoEndpoints) {
    var DocumentModel = restResourceFactory({
      url: coyoEndpoints.sender.documents
    });

    // class members
    angular.extend(DocumentModel, {

      /**
       * @ngdoc method
       * @name coyo.domain.DocumentModel#setDescription
       * @methodOf coyo.domain.DocumentModel
       *
       * @description
       * Sets a description for the given document.
       *
       * @param {string} senderId
       * The id of the sender the document belongs to.
       *
       * @param {object} documentId
       * The document ID of the document to set the description for
       *
       * @param {string} description
       * The description to set
       *
       * @returns {object} A promise of the operation
       */
      setDescription: function (senderId, documentId, description) {
        var url = DocumentModel.$url({id: documentId, senderId: senderId}, 'description');
        var params = DocumentModel.applyPermissions(['manage']);
        return DocumentModel.$put(url, {description: description}, {}, params);
      }
    });

    angular.extend(DocumentModel.prototype, {
      /**
       * Returns the versions of a file.
       *
       * @param {object} queryParams
       * The query parameters
       *
       * @returns {string}
       * The paged versions
       */
      getVersions: function (queryParams) {
        return DocumentModel.pagedQuery(null, queryParams, {id: this.id, senderId: this.senderId}, 'versions');
      },

      /**
       * Returns the relative download URL for the file's preview image.
       *
       * @returns {string}
       * The download URL
       */
      getDownloadUrl: function () {
        return this.$url();
      },

      /**
       * Returns the relative download URL for the file's preview image of the given version.
       *
       * @param {object} versionId
       * The version ID
       *
       * @returns {string}
       * The download URL
       */
      getDownloadUrlForVersion: function (versionId) {
        return this.$url('/versions/' + versionId);
      },

      /**
       * Returns the URL for the file's preview image of the given or the latest version (if no version was passed).
       *
       * @param {object} version
       * The DocumentVersion instance
       *
       * @returns {string}
       * The preview image URL
       */
      getVersionPreviewUrl: function (version) {
        var versionPart = version && version.id ? '/versions/' + version.id : '';
        return this.$url(versionPart);
      },

      /**
       * @ngdoc function
       * @name coyo.domain.DocumentModel#restore
       * @methodOf coyo.domain.DocumentModel
       *
       * @description
       * Restores the given version of the document.
       *
       * @param {object} versionId
       * The version's id to be restored
       *
       * @returns {promise} An $http promise
       */
      restoreVersion: function (versionId) {
        return DocumentModel.$put(this.$url('versions/' + versionId + '/restore'));
      }
    });

    return DocumentModel;
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.domain')
      .factory('EventModel', EventModel);

  /**
   * @ngdoc service
   * @name coyo.domain.EventModel
   *
   * @description
   * Provides the Coyo event model
   *
   * @requires $httpParamSerializer
   * @requires restResourceFactory
   * @requires commons.config.coyoEndpoints
   * @requires commons.resource.Page
   * @requires commons.resource.Pageable
   * @requires moment
   * @requires $http
   */
  function EventModel($httpParamSerializer, restResourceFactory, coyoEndpoints, Page, Pageable, moment, $http) {
    var EventModel = restResourceFactory({
      url: coyoEndpoints.event.events
    });

    // class members
    angular.extend(EventModel, {

      /**
       * @ngdoc function
       * @name coyo.domain.EventModel#searchWithFilter
       * @methodOf coyo.domain.EventModel
       *
       * @description
       * Event elastic search with filter.
       *
       * @param {string} term search term
       * @param {string} [from] the start date to filter the search (formatted as 'YYYY-MM-DDTHH:mm:ss')
       * @param {string} [to] the end date to filter the search (formatted as 'YYYY-MM-DDTHH:mm:ss')
       * @param {object} pageable The paging information. If not set an offset of 0 and a page size of 20 will be used.
       * @param {object} [filters] search filters
       * @param {string} [searchFields] list of fields to search in
       * @param {object} [aggregations] search aggregations
       *
       * @returns {promise} An $http promise
       */
      searchWithFilter: function (term, from, to, pageable, filters, searchFields, aggregations) {
        var url = EventModel.$url();
        var params = angular.extend({
          term: term ? term.toLowerCase() : undefined,
          from: from ? from : moment().format('YYYY-MM-DDTHH:mm:ss'),
          to: to ? to : undefined,
          filters: filters ? $httpParamSerializer(filters) : undefined,
          searchFields: searchFields ? searchFields.join(',') : undefined,
          aggregations: aggregations ? $httpParamSerializer(aggregations) : undefined
        }, pageable.getParams());
        return EventModel.getWithPermissions(null, params, ['manage', 'canParticipate']).then(function (response) {
          return new Page(response, params, {
            url: url,
            resultMapper: function (item) {
              return new EventModel(item);
            }
          });
        });
      },

      /**
       * @ngdoc function
       * @name coyo.domain.EventModel#getMembershipsWithFilter
       * @methodOf coyo.domain.EventModel
       *
       * @description
       * Returns the participants of an event
       *
       * @param {string} term search term
       * @param {object} pageable The paging information
       * @param {object} [filters] search filters
       * @param {string} [searchFields] list of fields to search in
       * @param {object} [aggregations] search aggregations
       * @param {string} slug The slug of the Event
       *
       * @return {promise} An $http promise
       */
      getMembershipsWithFilter: function (term, pageable, filters, searchFields, aggregations, slug) {
        var url = this.$url(slug + '/memberships');
        var params = angular.extend({
          term: term ? term.toLowerCase() : undefined,
          filters: filters ? $httpParamSerializer(filters) : undefined,
          searchFields: searchFields ? searchFields.join(',') : undefined,
          aggregations: aggregations ? $httpParamSerializer(aggregations) : undefined
        }, pageable.getParams());
        return $http.get(url, {params: params, autoHandleErrors: false}).then(function (response) {
          return new Page(response.data, params, {
            url: url,
            resultMapper: function (item) {
              return new EventModel(item);
            }
          });
        });
      }
    });

    // instance members
    angular.extend(EventModel.prototype, {

      /**
       * @ngdoc function
       * @name coyo.domain.EventModel#isMultiDay
       * @methodOf coyo.domain.EventModel
       *
       * @description
       * Checks if this event spans over multiple days.
       *
       * @returns {boolean} true if this event spans over multiple days, otherwise false.
       */
      isMultiDay: function () {
        return !moment(this.startDate).isSame(this.endDate, 'day');
      },

      /**
       * @ngdoc function
       * @name coyo.domain.EventModel#updateEvent
       * @methodOf coyo.domain.EventModel
       *
       * @description
       * Updates the event.
       *
       * @returns {promise} An $http promise
       */
      updateEvent: function () {
        return this.update();
      },

      /**
       * @ngdoc function
       * @name coyo.domain.EventModel#setStatus
       * @methodOf coyo.domain.EventModel
       *
       * @description
       * Sets the participation status for an event.
       *
       * @param {string} [status] The participation status to be set
       *
       * @returns {promise} An $http promise
       */
      setStatus: function (status) {
        return EventModel.$put(this.$url('status'), {status: status});
      },

      /**
       * @ngdoc function
       * @name coyo.domain.EventModel#inviteMembers
       * @methodOf coyo.domain.EventModel
       *
       * @description
       * Invites additional members to a event. Users can also invited via their group.
       *
       * @param {array} userAndGroupIds.userIds
       * An array of string with user ids to invite. Can be empty but not null.
       *
       * @param {array} userAndGroupIds.groupIds
       * An array of string with group ids. All users belonging to those groups are invited. Can be empty but not null.
       *
       * @returns {promise} An $http promise
       */
      inviteMembers: function (userAndGroupIds) {
        return EventModel.$put(this.$url('/memberships'), userAndGroupIds);
      },

      /**
       * @ngdoc function
       * @name coyo.domain.EventModel#removeMember
       * @methodOf coyo.domain.EventModel
       *
       * @description
       * Removes a member from an event.
       *
       * @param {string} userId
       * The identifier of the user to remove from the list of event memberships.
       *
       * @returns {promise} An $http promise
       */
      removeMember: function (userId) {
        return EventModel.$delete(this.$url('/memberships/' + userId));
      },

      /**
       * @ngdoc function
       * @name coyo.domain.EventModel#deleteEvent
       * @methodOf coyo.domain.EventModel
       *
       * @description
       * Delete an event.
       *
       * @returns {promise} An $http promise
       */
      deleteEvent: function () {
        return this.$delete(this.$url());
      },

      /**
       * @ngdoc function
       * @name coyo.domain.EventModel#canParticipate
       * @methodOf coyo.domain.EventModel
       *
       * @description
       * Returns true if the event can be participated by the current user.
       *
       * @returns {boolean} true if the event can be participated
       */
      canParticipate: function () {
        return this._permissions && this._permissions.canParticipate;
      },

      /**
       * @ngdoc function
       * @name coyo.domain.EventModel#canManage
       * @methodOf coyo.domain.EventModel
       *
       * @description
       * Returns true if the event can be managed by the current user.
       *
       * @returns {boolean} true if the current user can manage the event
       */
      canManage: function () {
        return this._permissions && this._permissions.manage;
      },

      /**
       * @ngdoc function
       * @name coyo.domain.EventModel#getHosts
       * @methodOf coyo.domain.EventModel
       *
       * @description
       * Returns the hosts of an event
       *
       * @return {promise} An $http promise
       */
      getHosts: function () {
        var senderId = this.sender.id;
        var url = this.$url('/hosts');
        var sort = ['_score,DESC', 'displayName'];
        var pageable = new Pageable(0, 100, sort);
        return EventModel.$get(url, pageable.getParams()).then(function (response) {
          return _.reject(response.content, {'userId': senderId});
        });
      }
    });

    return EventModel;
  }

})(angular);

(function () {
  'use strict';

  angular
      .module('coyo.domain')
      .factory('FilePublicLinkModel', FilePublicLinkModel);

  /**
   * @ngdoc service
   * @name coyo.domain.FilePublicLinkModel
   *
   * @description
   * Provides the Coyo model for the public link dialog.
   *
   * @requires commons.config.coyoEndpoints
   * @requires $http
   */
  function FilePublicLinkModel(coyoEndpoints, $http) {

    return {
      getLink: getLink,
      create: create,
      recreate: recreate,
      activate: activate,
      deactivate: deactivate
    };

    /**
     * @ngdoc method
     * @name coyo.domain.FilePublicLinkModel#getLink
     * @methodOf coyo.domain.FilePublicLinkModel
     *
     * @description
     * Retrieves a public file link.
     *
     * @param {string} senderId The ID of the sender the file belongs to
     * @param {string} fileId The ID of the file
     * @returns {object} Promise resolving to the public link
     */
    function getLink(senderId, fileId) {
      var url = coyoEndpoints.publicLink.getLink.replace('{{senderId}}', senderId).replace('{{fileId}}', fileId);
      return $http.get(url, {
        autoHandleErrors: false
      }).then(_onSuccess).catch(function () {
        return null;
      });
    }

    /**
     * @ngdoc method
     * @name coyo.domain.FilePublicLinkModel#create
     * @methodOf coyo.domain.FilePublicLinkModel
     *
     * @description
     * Creates a new public link.
     *
     * @param {string} senderId The ID of the sender the file belongs to
     * @param {string} fileId The ID of the file
     * @returns {object} Promise resolving to the new public link
     */
    function create(senderId, fileId) {
      var url = coyoEndpoints.publicLink.create.replace('{{senderId}}', senderId).replace('{{fileId}}', fileId);
      return $http.post(url).then(_onSuccess);
    }

    /**
     * @ngdoc method
     * @name coyo.domain.FilePublicLinkModel#recreate
     * @methodOf coyo.domain.FilePublicLinkModel
     *
     * @description
     * Recreates a the given public link.
     *
     * @param {object} link The public link
     * @returns {object} Promise resolving to the new public link
     */
    function recreate(link) {
      var url = coyoEndpoints.publicLink.recreate.replace('{{token}}', link.token);
      return $http.put(url).then(_onSuccess);
    }

    /**
     * @ngdoc method
     * @name coyo.domain.FilePublicLinkModel#activate
     * @methodOf coyo.domain.FilePublicLinkModel
     *
     * @description
     * Activates the given public link.
     *
     * @param {object} link The public link
     * @returns {object} Promise resolving to the public link
     */
    function activate(link) {
      var url = coyoEndpoints.publicLink.activate.replace('{{token}}', link.token);
      return $http.put(url).then(function () {
        link.active = true;
        return link;
      });
    }

    /**
     * @ngdoc method
     * @name coyo.domain.FilePublicLinkModel#deactivate
     * @methodOf coyo.domain.FilePublicLinkModel
     *
     * @description
     * Deactivates the given public link.
     *
     * @param {object} link The public link
     * @returns {object} Promise resolving to the public link
     */
    function deactivate(link) {
      var url = coyoEndpoints.publicLink.deactivate.replace('{{token}}', link.token);
      return $http.put(url).then(function () {
        link.active = false;
        return link;
      });
    }

    function _onSuccess(response) {
      return response.data;
    }
  }

})(angular);

(function () {
  'use strict';

  angular
      .module('coyo.domain')
      .factory('FileModel', FileModel);

  /**
   * @ngdoc service
   * @name coyo.domain.FileModel
   *
   * @description
   * Provides the Coyo file model.
   *
   * @requires restResourceFactory
   * @requires commons.config.coyoEndpoints
   */
  function FileModel(restResourceFactory, coyoEndpoints) {
    var FileModel = restResourceFactory({
      url: coyoEndpoints.sender.files
    });

    // class members
    angular.extend(FileModel, {

      /**
       * @ngdoc method
       * @name coyo.domain.FileModel#move
       * @methodOf coyo.domain.FileModel
       *
       * @description
       * Moves a file to another folder.
       *
       * @param {string} senderId The ID of the sender the source's file library belongs to
       * @param {string} sourceId The ID of the file to be moved
       * @param {string=} destinationId The ID of the target folder
       *
       * @returns {object} Promise resolving to the new file
       */
      move: function (senderId, sourceId, destinationId) {
        return FileModel.$put(FileModel.$url({
          senderId: senderId,
          id: sourceId
        }, '/move'), null, null, {
          'destinationId': destinationId || ''
        });
      },

      /**
       * @ngdoc method
       * @name coyo.domain.FileModel#rename
       * @methodOf coyo.domain.FileModel
       *
       * @description
       * Renames the given file of the given sender with the passed name.
       *
       * @param {string} senderId
       * The id of the sender the file belongs to.
       *
       * @param {object} file
       * The file to rename
       *
       * @param {string} filename
       * The new filename to rename the file to.
       *
       * @returns {object} A promise of the renaming operation
       */
      rename: function (senderId, file, filename) {
        var url = FileModel.$url({id: file.id, senderId: senderId}, '/name');
        var params = FileModel.applyPermissions(['manage', 'publicLink'], {name: filename});
        return FileModel.$put(url, {}, {}, params);
      }
    });

    return FileModel;
  }
})();

