(function (angular) {
  'use strict';

  angular
      .module('coyo.apps.forum')
      .controller('ForumThreadCreateController', ForumThreadCreateController);

  /**
   * Controller for creating a forum thread.
   *
   * @requires $scope
   * @requires $state
   * @requires $q
   * @requires $timeout
   * @requires commons.resource.tempUploadService
   * @requires app
   * @requires thread
   * @constructor
   */
  function ForumThreadCreateController($scope, $state, $q, $timeout, tempUploadService, app, thread) {
    var vm = this;

    vm.$onInit = onInit;

    vm.cancel = cancel;
    vm.save = save;
    vm.addAttachments = addAttachments;
    vm.removeAttachment = removeAttachment;

    function cancel() {
      vm.saving = false;
      $state.go('^');
    }

    function save() {
      var deferred = $q.defer();
      if (_isUploading()) {
        deferred.resolve();
      } else {
        vm.saving = true;

        vm.thread.attachments = _.map(vm.newItemAttachments, function (attachment) {
          return {
            name: attachment.name,
            uid: attachment.uid,
            contentType: attachment.contentType
          };
        });

        vm.thread.create().then(function (savedThread) {
          vm.newItemAttachments = [];
          vm.thread = savedThread;

          $state.go('^.view', {id: vm.thread.id}).then(function () {
            deferred.resolve();
          });
        }).catch(function () {
          deferred.reject();
        });
      }

      return deferred.promise;
    }

    function addAttachments(files) {
      vm.newItemAttachments.push.apply(vm.newItemAttachments, files);
      vm.uploadChange = new Date();

      angular.forEach(files, function (file) {
        tempUploadService.upload(file, 300).then(function (blob) {
          angular.extend(file, blob);
        }).catch(function () {
          removeAttachment(file);
        }).finally(function () {
          vm.uploadChange = new Date();
        });
      });
    }

    function removeAttachment(file) {
      vm.newItemAttachments = _.reject(vm.newItemAttachments, file);
      vm.uploadChange = new Date();
    }

    function _isUploading() {
      return _.filter(vm.newItemAttachments, {uploading: true}).length > 0;
    }

    function _updateFormLoading() {
      $timeout(function () {
        _.set(vm, 'form.loading', _isUploading());
      });
    }

    function onInit() {
      vm.app = app;
      vm.thread = thread;
      vm.newItemAttachments = [];

      vm.saving = false;
      vm.options = {
        rte: {
          height: 150
        }
      };

      _updateFormLoading();
      $scope.$watch(function () {
        return vm.uploadChange;
      }, function () {
        _updateFormLoading();
      });
    }

  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.apps.forum')
      .controller('ForumThreadListController', ForumThreadListController);

  /**
   * Controller for the forum app.
   *
   * @requires $rootScope
   * @requires $scope
   * @requires $state
   * @requires app
   * @requires coyo.apps.forum.ForumThreadModel
   * @requires coyo.apps.forum.forumAppConfig
   * @requires coyo.apps.forum.forumThreadService
   * @constructor
   */
  function ForumThreadListController($rootScope, $scope, $state, app, ForumThreadModel, forumAppConfig, forumThreadService) {
    var vm = this;

    vm.$onInit = onInit;
    vm.pin = pinThread;
    vm.unpin = unpinThread;
    vm.close = closeThread;
    vm.reopen = reopenThread;
    vm.delete = deleteThread;
    vm.statusFilterActive = statusFilterActive;
    vm.searchTermFilterActive = searchTermFilterActive;
    vm.toggleIncludeOpen = toggleIncludeOpen;
    vm.toggleIncludeClosed = toggleIncludeClosed;
    vm.search = search;

    function pinThread(thread) {
      forumThreadService.pin(thread)
          .then(function () {
            _reloadState();
          });
    }

    function unpinThread(thread) {
      forumThreadService.unpin(thread)
          .then(function () {
            _reloadState();
          });
    }

    function closeThread(thread) {
      forumThreadService.close(thread)
          .then(function () {
            _reloadState();
          });
    }

    function reopenThread(thread) {
      forumThreadService.reopen(thread)
          .then(function () {
            _reloadState();
          });
    }

    function deleteThread(thread) {
      forumThreadService.delete(thread)
          .then(function () {
            _reloadState();
          });
    }

    function statusFilterActive() {
      return !vm.forumThreads._queryParams.includeOpen || !vm.forumThreads._queryParams.includeClosed;
    }

    function searchTermFilterActive() {
      return !_.isEmpty(vm.forumThreads._queryParams.searchTerm);
    }

    function toggleIncludeOpen() {
      vm.forumThreads._queryParams.includeOpen = !vm.forumThreads._queryParams.includeOpen;
      if (!vm.forumThreads._queryParams.includeOpen && !vm.forumThreads._queryParams.includeClosed) {
        vm.forumThreads._queryParams.includeClosed = true;
      }
      _loadThreads();
    }

    function toggleIncludeClosed() {
      vm.forumThreads._queryParams.includeClosed = !vm.forumThreads._queryParams.includeClosed;
      if (!vm.forumThreads._queryParams.includeOpen && !vm.forumThreads._queryParams.includeClosed) {
        vm.forumThreads._queryParams.includeOpen = true;
      }
      _loadThreads();
    }

    function search(searchTerm) {
      vm.forumThreads._queryParams.searchTerm = searchTerm;
      _loadThreads();
    }

    function _reloadState() {
      $state.reload($state.current.name);
    }

    function _loadThreads() {
      ForumThreadModel.pagedQueryWithPermissions(
          undefined, vm.forumThreads._queryParams, {senderId: app.senderId, appId: app.id}, ['close', 'pin', 'delete']
      ).then(function (result) {
        vm.forumThreads = result;
        vm.threadCount = result.totalElements;
      }).finally(function () {
        vm.loading = false;
      });
    }

    function onInit() {
      vm.app = app;
      vm.loading = true;
      vm.threadCount = 0;
      vm.contextMenuTemplate = forumAppConfig.templates.contextMenuList;
      vm.forumThreads = {
        content: [],
        _queryParams: {
          _page: 0,
          _pageSize: forumAppConfig.paging.threads.pageSize,
          includeOpen: true,
          includeClosed: true,
          _orderBy: 'lastAnswerDate,desc',
          searchTerm: ''
        }
      };

      var unsubscribe = $rootScope.$on('screenSize:changed', function (event, screenSize) {
        vm.isMobile = screenSize.isXs || screenSize.isSm;
      });
      $scope.$on('$destroy', unsubscribe);

      vm.isMobile = $rootScope.screenSize.isXs || $rootScope.screenSize.isSm;
      _loadThreads();
    }
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.apps.forum')
      .controller('ForumThreadViewController', ForumThreadViewController);

  /**
   * Controller for viewing a forum thread.
   *
   * @requires $scope
   * @requires $state
   * @requires $timeout
   * @requires $element
   * @requires $compile
   * @requires commons.resource.backendUrlService
   * @requires coyo.apps.forum.forumThreadService
   * @requires app
   * @requires thread
   * @requires forumAppConfig
   * @constructor
   */
  function ForumThreadViewController($scope, $state, $timeout, $element, $compile,
                                     backendUrlService, forumThreadService, app, thread, forumAppConfig) {
    var vm = this;

    vm.$onInit = onInit;

    vm.pin = pinThread;
    vm.unpin = unpinThread;
    vm.close = closeThread;
    vm.reopen = reopenThread;
    vm.delete = deleteThread;

    function pinThread() {
      forumThreadService.pin(vm.thread);
    }

    function unpinThread() {
      forumThreadService.unpin(vm.thread);
    }

    function closeThread() {
      forumThreadService.close(vm.thread);
    }

    function reopenThread() {
      forumThreadService.reopen(vm.thread);
    }

    function deleteThread() {
      forumThreadService.delete(vm.thread)
          .then(function () {
            $state.go('^');
          });
    }

    function onInit() {
      vm.app = app;
      vm.thread = thread;
      vm.backendUrl = backendUrlService.getUrl();
      vm.contextMenuTemplate = forumAppConfig.templates.contextMenuView;
      vm.options = {
        rte: {
          height: 150
        }
      };

      $timeout(function () {
        // re-compile coyo-download directives to update file URLs after exit of edit mode
        $element.find('.note-file-link[coyo-download]').replaceWith(function () {
          var elem = angular.element(this); // eslint-disable-line angular/controller-as-vm
          elem.attr('coyo-download', '\'' + elem.attr('coyo-download') + '\'');
          return $compile(elem)($scope);
        });
      });
    }
  }

})(angular);

