(function (angular) {
  'use strict';

  angular
      .module('coyo.apps.list')
      .controller('ListSettingsController', ListSettingsController);

  /**
   * The list settings controller
   *
   * @constructor
   */
  function ListSettingsController($scope) {
    var vm = this;
    var PERMISSIONS = ['ALL', 'OWN', 'NONE'];

    vm.$onInit = initController;
    vm.isEnabled = isEnabled;
    vm.handleReadPermissionChange = handleReadPermissionChange;


    function initController() {
      var defaults = {
        permissionCreate: 'YES',
        permissionRead: 'ALL',
        permissionEdit: 'OWN',
        permissionDelete: 'OWN',
        notification: 'NONE',
        maxEntries: 0
      };
      vm.app = $scope.model;
      vm.app.settings = angular.extend(defaults, vm.app.settings);
      vm.permissions = PERMISSIONS;
      vm.permissionIcons = {
        'NONE': 'zmdi-block',
        'OWN': 'zmdi-lock-outline',
        'ALL': 'zmdi-globe'
      };
    }

    function isEnabled(permission) {
      var readPermission = vm.app.settings.permissionRead;
      return !_isPermissionLessThan(readPermission, permission);
    }

    function handleReadPermissionChange() {
      var readPermission = vm.app.settings.permissionRead;
      if (_isPermissionLessThan(readPermission, vm.app.settings.permissionEdit)) {
        vm.app.settings.permissionEdit = readPermission;
      }
      if (_isPermissionLessThan(readPermission, vm.app.settings.permissionDelete)) {
        vm.app.settings.permissionDelete = readPermission;
      }
    }

    function _isPermissionLessThan(permission, other) {
      return PERMISSIONS.indexOf(permission) > PERMISSIONS.indexOf(other);
    }

  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.apps.list')
      .factory('listSortService', listSortService);

  /**
   * @ngdoc service
   * @name coyo.apps.list.listSortService
   *
   * @description
   * Service for sorting functions of the list that must be shared between different controllers
   */
  function listSortService(fieldTypeRegistry, $localStorage) {

    var sortConfig = {
      query: 'created',
      dir: 'DESC'
    };

    return {
      updateSortConfig: updateSortConfig,
      getSortConfig: getSortConfig,
      storeSortConfig: storeSortConfig,
      createSortQuery: createSortQuery,
      initSortConfig: initSortConfig
    };


    /**
     * @ngdoc method
     * @name coyo.apps.list.listSortService#updateSortConfig
     * @methodOf coyo.apps.list.listSortService
     *
     * @description
     * Updates the sort config by the given field defining the field to sort by and the sorting direction
     *
     * @param {object} field
     * List Field
     *
     * @returns {object}
     * Sort config
     */
    function updateSortConfig(field) {
      // set sort column
      var fieldType = fieldTypeRegistry.get(field.key);
      var sortDirection = sortConfig.dir;
      var sortedField = sortConfig.field;
      var sortQuery = 'values.' + field.id;
      sortQuery += (fieldType.sortOn) ? '.' + fieldType.sortOn : '';

      // set sort direction
      if (sortedField && sortedField.id === field.id && sortDirection === 'ASC') {
        sortDirection = 'DESC';
      } else if (!sortedField || sortedField.id !== field.id) {
        sortedField = field;
        sortDirection = 'ASC';
      } else {
        sortQuery = 'created';
        sortedField = undefined;
        sortDirection = 'DESC';
      }

      // update sort config
      sortConfig = {
        field: sortedField,
        query: sortQuery,
        dir: sortDirection
      };

      return sortConfig;
    }

    /**
     * @ngdoc method
     * @name coyo.apps.list.listSortService#initSortConfig
     * @methodOf coyo.apps.list.listSortService
     *
     * @description
     * Initializes the sort config. Retrieves config from local storage if it exists. Otherwise it takes the first one from the given field list
     *
     * @param {array} fields
     * List field array
     *
     * @param {string} appId
     * App id
     */
    function initSortConfig(fields, appId) {
      var field = fields[0],
          storedSortConfig = $localStorage['listApp-' + appId + '-sortConfig'];
      if (angular.isUndefined(storedSortConfig) || _.findIndex(fields, function (field) {
        return angular.isDefined(storedSortConfig.field) && field.id === storedSortConfig.field.id;
      }) === -1) {
        updateSortConfig(field);
      } else {
        sortConfig = storedSortConfig;
      }
    }

    /**
     * @ngdoc method
     * @name coyo.apps.list.listSortService#storeSortConfig
     * @methodOf coyo.apps.list.listSortService
     *
     * @description
     * Stores sort config in local storage
     *
     * @param {string} appId
     * App id
     */
    function storeSortConfig(appId) {
      $localStorage['listApp-' + appId + '-sortConfig'] = sortConfig;
    }

    /**
     * @ngdoc method
     * @name coyo.apps.list.listSortService#getSortConfig
     * @methodOf coyo.apps.list.listSortService
     *
     * @description
     * Returns the sort config
     *
     * @returns {Object} Sort config
     */
    function getSortConfig() {
      return sortConfig;
    }

    /**
     * @ngdoc method
     * @name coyo.apps.list.listSortService#createSortQuery
     * @methodOf coyo.apps.list.listSortService
     *
     * @description
     * Creates sort query from sort config
     *
     * @returns {String} Sort query
     */
    function createSortQuery() {
      return sortConfig.query + ',' + sortConfig.dir;
    }

  }
})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.apps.list')
      .controller('ListController', ListController);

  function ListController($scope, $state, app, fields, createFieldModalService, addEntryModalService, ListEntryModel,
                          Pageable, coyoNotification, socketService, modalService, listService, listSortService, fieldTypeRegistry) {

    var vm = this;
    var sortQuery = 'created,DESC';

    vm.app = app;
    vm.fields = fields;
    vm.loading = false;
    vm.search = '';

    vm.$onInit = init;
    vm.createField = createField;
    vm.addEntry = addEntry;
    vm.viewEntry = viewEntry;
    vm.editEntry = editEntry;
    vm.getValue = getValue;
    vm.getConfig = getConfig;
    vm.sort = sort;
    vm.save = save;
    vm.searchKeyPressed = searchKeyPressed;
    vm.showCreateEntryForm = showCreateEntryForm;
    vm.deleteEntry = deleteEntry;

    function init() {
      if (vm.fields && vm.fields.length) {
        listSortService.initSortConfig(vm.fields, vm.app.id);
        sort();
      }
      var unsubscribe = socketService.subscribe('/topic/app.list.' + app.senderId + '.' + app.id + '.entry.updated', listEntryUpdated);
      $scope.$on('$destroy', function () {
        unsubscribe();
      });
    }

    function listEntryUpdated($event) {
      var context = {
        senderId: app.senderId,
        appId: app.id,
        id: $event.content
      };

      var index = _.findIndex(vm.currentPage.content, function (entry) {
        return $event.content === entry.id;
      });

      if (index >= 0) {
        ListEntryModel.getWithPermissions(context, {}, ['edit', 'delete', 'comment']).then(function (result) {
          vm.currentPage.content[index] = result;
        });
      }
    }

    function searchKeyPressed() {
      _loadData();
    }

    function sort() {
      sortQuery = listSortService.createSortQuery();
      listSortService.storeSortConfig(vm.app.id);
      _loadData();
    }

    function createField() {
      createFieldModalService.open(app).then(function () {
        $state.go('^.configure');
      });
    }

    function addEntry() {
      addEntryModalService.open(app).then(function () {
        _loadData();
        _updateAppPermissions();
      });
    }

    function viewEntry(entry) {
      $state.go('.details', {
        id: entry.id
      });
    }

    function editEntry(entry) {
      $state.go('.details', {
        id: entry.id,
        mode: 'edit'
      });
    }

    function getValue(entry, fieldId) {
      return listService.getFieldValue(entry, fieldId);
    }

    function getConfig(key) {
      return fieldTypeRegistry.get(key).render;
    }

    function save(form) {
      vm.entry.senderId = vm.app.senderId;
      vm.entry.appId = vm.app.id;
      vm.entry.save().then(function () {
        coyoNotification.success('APP.LIST.ADD_ENTRY.SUCCESS');
        vm.entry = {};
        form.$setPristine();
        _updateAppPermissions();
      }).catch(function () {
        coyoNotification.error('APP.LIST.ADD_ENTRY.ERROR');
      });
    }

    function deleteEntry(entry) {
      vm.entry = entry;
      vm.entry.appId = vm.app.id;
      vm.entry.senderId = vm.app.senderId;
      modalService.confirm({
        title: 'APP.LIST.MODAL.DELETE.TITLE',
        text: 'APP.LIST.MODAL.DELETE.TEXT',
        translationContext: {title: vm.entry.name},
        close: {
          title: 'YES',
          style: 'btn-danger'
        },
        dismiss: {title: 'NO'}
      }).result.then(function () {
        vm.entry.delete().then(function () {
          _loadData();
        });
      });
    }

    function showCreateEntryForm() {
      return !vm.app._permissions.readEntries && vm.app._permissions.createEntry && vm.fields.length > 0;
    }

    function _loadData() {
      if (!vm.app._permissions.readEntries) {
        return;
      }

      vm.loading = true;
      var context = {
        senderId: app.senderId,
        appId: app.id
      };
      var pageable = new Pageable((vm.currentPage ? vm.currentPage.number : 0), 20, sortQuery);
      var queryParams = angular.extend({query: vm.search, _permissions: 'edit,delete'}, pageable.getParams());
      ListEntryModel.pagedQuery(pageable, queryParams, context).then(function (result) {
        vm.currentPage = result;
        listService.initEntries(vm.currentPage.content, vm.fields);
      }).finally(function () {
        vm.loading = false;
      });
    }

    function _updateAppPermissions() {
      app.getWithPermissions('', {}, ['createEntry']).then(function (result) {
        if (result._permissions) {
          angular.extend(app._permissions, result._permissions);
        }
      });
    }

  }
})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.apps.list')
      .factory('listService', listService);

  /**
   * @ngdoc service
   * @name coyo.apps.list.listService
   *
   * @description
   * Service for common functions of the list that must be shared between different controllers
   */
  function listService(ListEntryModel) {

    return {
      createEntry: createEntry,
      initEntries: initEntries,
      initEntry: initEntry,
      getFieldValue: getFieldValue
    };

    /**
     * @ngdoc method
     * @name coyo.apps.list.listService#createEntry
     * @methodOf coyo.apps.list.listService
     *
     * @description
     * Create entry model with missing field values
     *
     * @returns {object}
     * List entry model
     */
    function createEntry() {
      return new ListEntryModel({
        values: []
      });
    }

    /**
     * @ngdoc method
     * @name coyo.apps.list.listService#initEntries
     * @methodOf coyo.apps.list.listService
     *
     * @description
     * Initializes a list of entries with missing field values
     *
     * @param {array} entries
     * List entries array
     *
     * @param {array} fields
     * List fields array
     *
     * @returns {array}
     * Array of initialized entries
     */
    function initEntries(entries, fields) {
      return entries.map(function (entry) {
        return initEntry(entry, fields);
      });
    }

    /**
     * @ngdoc method
     * @name coyo.apps.list.listService#initEntry
     * @methodOf coyo.apps.list.listService
     *
     * @description
     * Initializes an entry with missing field values
     *
     * @param {object} entry
     * List entry
     *
     * @param {array} fields
     * List fields array
     *
     * @returns {object}
     * Initialized entry
     */
    function initEntry(entry, fields) {
      if (_.isEmpty(entry)) {
        entry = createEntry();
      }

      // init field values
      fields.forEach(function (field) {
        var fieldValue = getFieldValue(entry, field.id);
        if (!fieldValue) {
          fieldValue = initFieldValue(field);
          entry.values.push(fieldValue);
        }
      });
      return entry;
    }

    /**
     * @ngdoc method
     * @name coyo.apps.list.listService#initFieldValue
     * @methodOf coyo.apps.list.listService
     *
     * @description
     * Initializes a field value with the fieldId
     *
     * @param {object} field
     * List field
     *
     * @returns {object}
     * Initialized field value
     */
    function initFieldValue(field) {
      return {
        fieldId: field.id,
        value: undefined
      };
    }

    /**
     * @ngdoc method
     * @name coyo.apps.list.listService#getFieldValue
     * @methodOf coyo.apps.list.listService
     *
     * @description
     * Returns a field value for the fieldId from the given entry
     *
     * @param {object} entry
     * List entry
     *
     * @param {string} fieldId
     * Field id
     *
     * @returns {object}
     * Field value
     */
    function getFieldValue(entry, fieldId) {
      return _.find(entry.values, {fieldId: fieldId});
    }

  }
})(angular);

