(function (angular) {
  'use strict';

  angular
      .module('coyo.apps.wiki')
      .directive('coyoSelectWikiArticle', selectWikiArticle);

  /**
   * @ngdoc directive
   * @name coyo.apps.wiki:coyoSelectWikiArticle
   * @element ANY
   * @restrict E
   * @scope
   *
   * @description
   * Renders a ui-select to select a specific wiki article.
   */
  function selectWikiArticle(selectFactoryModel, WikiArticleModel, Pageable, SenderModel, AppModel, appService) {
    return selectFactoryModel({
      refresh: _refresh,
      transform: _transform,
      emptyText: 'APP.WIKI.SELECT.PARENT.NO_ARTICLES_FOUND',
      pageSize: 30
    });

    function _refresh(pageable, search, parameters) {
      return SenderModel.get({id: SenderModel.getCurrentIdOrSlug()}).then(function (sender) {
        return AppModel.get({senderId: sender.id, id: appService.getCurrentAppIdOrSlug()}).then(function (app) {
          var params = angular.extend({
            title: search
          }, parameters);
          return WikiArticleModel.pagedQuery(
              new Pageable(pageable.page, pageable.size),
              params,
              {senderId: sender.id, appId: app.id}
          ).then(function (response) {
            return angular.extend(response.content, {
              meta: {
                last: response.last,
                number: response.number
              }
            });
          });
        });
      });
    }

    function _transform(article) {
      return article ? article.id : null;
    }
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.apps.wiki')
      .controller('WikiArticleCreateController', WikiArticleCreateController);

  /**
   * Controller for creating a wiki article.
   *
   * @constructor
   */
  function WikiArticleCreateController($q, $scope, $state, $timeout, widgetLayoutService, article, app, sender,
                                       wikiArticleTranslationService) {
    var vm = this;

    vm.$onInit = init;
    vm.article = article;
    vm.app = app;
    vm.originalArticle = angular.copy(vm.article);
    vm.editMode = true;
    vm.loading = true;
    vm.simpleMode = true;

    vm.cancel = cancel;
    vm.save = save;

    function cancel() {
      widgetLayoutService.cancel($scope);
      vm.loading = false;
      vm.editMode = false;

      $state.go('^');
    }

    function save() {
      wikiArticleTranslationService.prepareTranslations();

      var deferred = $q.defer();
      if (vm.loading) {
        deferred.resolve();
      } else {
        vm.loading = true;

        if (article.id === article.parentId) {
          article.parentId = null;
        }

        vm.article.create().then(function () {
          $timeout(function () { // wait to sync id to widget layout and slots
            widgetLayoutService.save($scope).then(function () {
              vm.editMode = false;
              $state.go('^.view', {id: vm.article.id}).then(function () {
                deferred.resolve();
              });
            }).catch(function () {
              widgetLayoutService.edit($scope);
              deferred.reject();
            }).finally(function () {
              vm.loading = false;
            });
          });
        }).catch(function () {
          deferred.reject();
          vm.loading = false;
        });
      }

      return deferred.promise;
    }

    /* ===== PRIVATE METHODS ===== */

    function init() {

      widgetLayoutService.onload($scope).then(function () {
        widgetLayoutService.edit($scope);
      }).finally(function () {
        vm.loading = false;
      });

      _initTranslations();
    }

    function _initTranslations() {
      wikiArticleTranslationService.init($scope, vm, sender);
    }
  }

})(angular);

(function () {
  'use strict';

  angular.module('coyo.apps.wiki')
      .factory('wikiArticleRevisionsModal', wikiArticleRevisionsModal)
      .controller('WikiArticleRevisionsModalController', WikiArticleRevisionsModalController);

  /**
   * @ngdoc service
   * @name coyo.apps.wiki.wikiArticleRevisionsModal
   *
   * @description
   * Displays a modal with all revisions of the given article. The revisions are displayed in a bulk of 10. If more
   * revisions exist, a "Load More" button is displayed. The user can click on each of the revisions to close the
   * modal and return the selected revision.
   *
   * @requires modalService
   */
  function wikiArticleRevisionsModal(modalService) {

    return {
      open: open
    };

    /**
     * @ngdoc method
     * @name coyo.apps.wiki.wikiArticleRevisionsModal#open
     * @methodOf coyo.apps.wiki.wikiArticleRevisionsModal
     *
     * @description
     * Opens the modal which displays a list of revisions. Supports pagination.
     *
     * @param {object} article
     * The article to display the revisions for.
     *
     * @returns {object} a promise which returns the selected revision.
     */
    function open(article) {
      return modalService.open({
        size: 'md',
        templateUrl: 'app/apps/wiki/wiki-article-revisions-modal.html',
        controller: 'WikiArticleRevisionsModalController',
        resolve: {
          article: function () {
            return article;
          }
        }
      }).result;
    }
  }

  function WikiArticleRevisionsModalController(Pageable, article) {
    var vm = this;
    vm.loading = false;
    vm.currentPage = {};
    vm.revisions = [];
    vm.currentRevisionNumber = article.revisionNumber;

    vm.loadMore = loadMore;

    function loadMore() {
      if (vm.loading) {
        return;
      }

      if (!vm.currentPage || !vm.currentPage.last) {
        vm.loading = true;

        var pageable = new Pageable((vm.currentPage ? vm.currentPage.number + 1 : 0), 10);
        article.getRevisions(pageable).then(function (page) {
          vm.currentPage = page;
          vm.revisions.push.apply(vm.revisions, page.content);
        }).finally(function () {
          vm.loading = false;
        });
      }
    }

    (function _init() {
      loadMore();
    })();
  }

})();

(function () {
  'use strict';

  angular
      .module('coyo.apps.wiki')
      .component('coyoWikiArticleTitle', wikiArticleTitle())
      .controller('WikiArticleTitleController', WikiArticleTitleController);

  /**
   * @ngdoc directive
   * @name coyo.app.wiki.coyoArticleTitle:coyoArticleTitle
   * @element OWN
   * @restrict E
   * @scope
   *
   * @description
   * Displays the title of an article along with a label showing its status (new, modified or nothing). An article is
   * displayed as "new" if it was created at least 5 days ago. It is displayed as modified, if it's last modification
   * was at least 5 days ago.
   *
   * @param {object} article
   * The article to display the title and the status of.
   */
  function wikiArticleTitle() {
    return {
      templateUrl: 'app/apps/wiki/wiki-article-title.html',
      bindings: {
        article: '<'
      },
      controller: 'WikiArticleTitleController'
    };
  }

  function WikiArticleTitleController() {
    var vm = this;

    var newArticleThreshold = 5 * 24 * 60 * 60 * 1000; // 5 days in ms
    var updatedArticleThreshold = 5 * 24 * 60 * 60 * 1000; // 5 days in ms

    vm.$onInit = init;

    function init() {
      // new when created date equals modified date and not older than newArticleThreshold
      vm.article.isNew = (vm.article.created === vm.article.modified)
          && (new Date().getTime() - vm.article.created < newArticleThreshold);

      // updated when not new and updated within updatedArticleThreshold
      vm.article.isUpdated = (vm.article.created !== vm.article.modified)
          && (new Date().getTime() - vm.article.modified < updatedArticleThreshold);
    }

  }

})();

(function () {
  'use strict';

  angular.module('coyo.apps.wiki')
      .factory('wikiArticleTranslationService', wikiArticleTranslationService);

  /**
   * @ngdoc service
   * @name coyo.apps.wiki.wikiArticleTranslationService
   *
   * @description
   * This service provides methods to handle translations.
   *
   * @requires $q
   * @requires $timeout
   * @requires coyo.widgets.api.widgetLayoutService
   * @requires coyo.domain.WidgetLayoutModel
   */
  function wikiArticleTranslationService($q, $timeout, widgetLayoutService, WidgetLayoutModel) {
    var $scope, vm, toBeDeleted;

    return {
      init: init,
      cleanup: cleanup,
      prepareTranslations: prepareTranslations,
      onLanguageDeleted: onLanguageDeleted,
      onLanguageChange: onLanguageChange,
      isTranslationRequired: isTranslationRequired,
      updateValidity: updateValidity
    };

    /**
     * @ngdoc method
     * @name coyo.apps.wiki.wikiArticleTranslationService#init
     * @methodOf coyo.apps.wiki.wikiArticleTranslationService
     *
     * @description
     * Setup the service for the current controller (prepare the language/translations).
     *
     * @param {object} scope
     * The current scope.
     *
     * @param {object} viewModel
     * The view model to operate on.
     *
     * @param {object} sender
     * The sender, that the article belongs to.
     */
    function init(scope, viewModel, sender) {
      $scope = scope;
      vm = viewModel;
      toBeDeleted = {};
      vm.languages = {};
      vm.languageInitialised = {};

      vm.isSenderTranslated = !!(sender.defaultLanguage) && Object.keys(sender.translations).length !== 0;
      vm.defaultLanguage = sender.defaultLanguage !== null ? sender.defaultLanguage : 'NONE';
      vm.currentLanguage = vm.defaultLanguage;

      if (!vm.article.defaultLanguage) {
        vm.article.defaultLanguage = vm.defaultLanguage;
      }

      // create translations with default values for all available languages
      var availableLanguages = _.concat(_.keys(sender.translations), vm.defaultLanguage);
      vm.languages = _.zipObject(availableLanguages, _.map(availableLanguages, function () {
        return {active: true, translations: {}};
      }));
      // add default translations of article or init, if article not already translated
      vm.languages[(vm.article.defaultLanguage) ? vm.article.defaultLanguage : vm.currentLanguage] = {
        'active': true,
        'translations': {
          'title': vm.article.title ? vm.article.title : ''
        }
      };
      if (!vm.article.isNew()) {
        vm.languageInitialised[vm.article.defaultLanguage] = true;
      }
      // add all remaining translations of the article
      _.forEach(vm.article.translations, function (value, key) {
        if (vm.languages[key]) {
          vm.languages[key].translations = value;
        }
      });

      vm.onLanguageChange = onLanguageChange;
      vm.onLanguageDeleted = onLanguageDeleted;
      vm.isTranslationRequired = isTranslationRequired;
      vm.updateValidity = updateValidity;
    }

    /**
     * @ngdoc method
     * @name coyo.apps.wiki.wikiArticleTranslationService#cleanup
     * @methodOf coyo.apps.wiki.wikiArticleTranslationService
     *
     * @description
     * Delete all marked layouts before storing new once.
     *
     * @returns {object} A promise which is resolved once all marked layouts are deleted.
     */
    function cleanup() {
      return $q.all(_.map(toBeDeleted, function (model) {
        return model.remove();
      }));
    }

    /**
     * @ngdoc method
     * @name coyo.apps.wiki.wikiArticleTranslationService#prepareTranslations
     * @methodOf coyo.apps.wiki.wikiArticleTranslationService
     *
     * @description
     * Prepare the translations before saving the article.
     */
    function prepareTranslations() {
      vm.article.defaultLanguage = (vm.defaultLanguage === 'NONE') ? null : vm.defaultLanguage;

      // -- add translations for default language to article model
      if (vm.languages[vm.defaultLanguage]) {
        angular.forEach(vm.languages[vm.defaultLanguage].translations, function (value, key) {
          vm.article[key] = value;
        });
      }
      // -- add additional translations to article model
      vm.article.translations = {};
      angular.forEach(vm.languages, function (value, key) {
        if (value.active) {
          vm.article.translations[key] = value.translations ? value.translations : {};
        }
      });
      delete vm.article.translations[vm.defaultLanguage];
    }

    /**
     * @ngdoc method
     * @name coyo.apps.wiki.wikiArticleTranslationService#onLanguageDeleted
     * @methodOf coyo.apps.wiki.wikiArticleTranslationService
     *
     * @description
     * Callback for when a language has been removed. The layout for the given language is being collected and then
     * marked to be deleted (will be deleted, once 'save' is triggered).
     *
     * @param {object} language
     * The language key.
     *
     * @returns {object} A promise which is resolved once the layout is being collected and marked as 'to be deleted'.
     */
    function onLanguageDeleted(language) {
      vm.languageInitialised[language] = false;
      return widgetLayoutService.collect($scope, vm.article.buildLayoutName(vm.app.id, language)).then(function (data) {
        if (!data.layout.isNew) {
          toBeDeleted[data.layout.name] = new WidgetLayoutModel({name: data.layout.name});
        }
      });
    }

    /**
     * @ngdoc method
     * @name coyo.apps.wiki.wikiArticleTranslationService#onLanguageChange
     * @methodOf coyo.apps.wiki.wikiArticleTranslationService
     *
     * @description
     * Callback for when a language has been added. If the param is true, the default layout & widgets are being
     * collected and copied to the new language. If the param is false, it starts with an empty layout.
     *
     * @param {boolean} copyFromDefault
     * Whether to copy the content from the default language or not.
     */
    function onLanguageChange(copyFromDefault) {
      vm.languageInitialised[vm.currentLanguage] = true;
      if (copyFromDefault) {
        var defaultLayoutName = vm.article.buildLayoutName(vm.app.id);
        widgetLayoutService.collect($scope, defaultLayoutName).then(function (data) {
          var layout = data.layout,
              slots = extractSlots(data);
          layout.name = defaultLayoutName + '-' + vm.currentLanguage;
          _.forEach(layout.rows, function (row) {
            _.forEach(row.slots, function (slot) {
              slot.name = translate(slot.name, defaultLayoutName);
            });
          });

          widgetLayoutService.fill($scope, 'layout', layout.name, {
            layout: layout
          }).then(function () {
            widgetLayoutService.fill($scope, 'slots', layout.name, {
              slots: _.map(slots, function (widget) {
                var copy = _.cloneDeep(widget);
                delete copy.$$hashKey;// eslint-disable-line angular/no-private-call
                delete copy.model.$snapshots;
                delete copy.model.id;
                copy.model.slot = translate(copy.model.slot, defaultLayoutName);
                return copy;
              })
            }).then(function () {
              widgetLayoutService.edit($scope, false, true);
            });
          });
        });
      } else {
        $timeout(function () {
          widgetLayoutService.edit($scope, false, true);
        });
      }
    }

    /**
     * @ngdoc method
     * @name coyo.apps.wiki.wikiArticleTranslationService#isTranslationRequired
     * @methodOf coyo.apps.wiki.wikiArticleTranslationService
     *
     * @description
     * Check whether translation is required for the given language.
     *
     * @param {object} language
     * The language key.
     *
     * @returns {boolean} A bool indicating whether translation is required or not.
     */
    function isTranslationRequired(language) {
      if (vm.defaultLanguage === 'NONE') {
        return language === 'NONE';
      } else if (vm.currentLanguage === language) {
        return true;
      } else {
        return (vm.languages[language].translations && Object.keys(vm.languages[language].translations).length !== 0);
      }
    }

    /**
     * @ngdoc method
     * @name coyo.apps.wiki.wikiArticleTranslationService#updateValidity
     * @methodOf coyo.apps.wiki.wikiArticleTranslationService
     *
     * @description
     * Update the validation state of the language for the given key.
     *
     * @param {string} key
     * The language key.
     *
     * @param {boolean} valid
     * The validation state.
     */
    function updateValidity(key, valid) {
      vm.languages[key].valid = valid;
    }

    /******************* PRIVATE METHODS *******************/

    function translate(text, layoutName) {
      return _.replace(text, new RegExp(layoutName, 'g'), layoutName + '-' + vm.currentLanguage);
    }

    function extractSlots(data) {
      var slotNames = _.flatten(_.map(data.layout.rows, function (row) {
        return _.map(row.slots, function (slot) {
          return slot.name;
        });
      }));
      return _.filter(data.slots, function (slot) {
        var name = slot.model.slot;
        return _.filter(slotNames, function (slotName) {
          return _.endsWith(name, slotName);
        }).length;
      });
    }
  }
})();

(function (angular) {
  'use strict';

  angular
      .module('coyo.apps.wiki')
      .controller('WikiArticleViewController', WikiArticleViewController);

  /**
   * Controller for viewing and inline-editing a wiki article.
   *
   * @requires $rootScope
   * @requires $scope
   * @requires $state
   * @requires $stateParams
   * @requires $log
   * @requires $timeout
   * @requires $q
   * @requires moment
   * @requires modalService
   * @requires coyoNotification
   * @requires coyo.apps.wiki.wikiArticleRevisionsModal
   * @requires commons.ui.alertConfirmModalService
   * @requires coyo.widgets.api.widgetLayoutService
   * @requires commons.sockets.socketService
   * @requires coyo.apps.wiki.WikiArticleModel
   * @requires coyo.apps.wiki.wikiArticleService
   * @requires app
   * @requires article
   * @requires editMode
   * @requires currentUser
   * @requires coyo.domain.SettingsModel
   * @requires sender
   * @requires coyo.apps.wiki.wikiArticleTranslationService
   */
  function WikiArticleViewController($rootScope, $scope, $state, $stateParams, $log, $timeout, $q, moment, modalService,
                                     coyoNotification, wikiArticleRevisionsModal, alertConfirmModalService,
                                     widgetLayoutService, socketService, WikiArticleModel, wikiArticleService, app,
                                     article, editMode, currentUser, SettingsModel, sender,
                                     wikiArticleTranslationService) {
    var vm = this;

    vm.$onInit = init;
    vm.app = app;
    vm.article = article;
    vm.editMode = editMode;
    vm.loading = true;
    vm.moment = moment;
    vm.simpleMode = true;

    vm.edit = edit;
    vm.setAsHomeArticle = setAsHomeArticle;
    vm.cancel = cancel;
    vm.save = save;
    vm.deleteArticle = deleteArticle;
    vm.removeLock = removeLock;
    vm.showRevisions = showRevisions;
    vm.openRevision = openRevision;

    function removeLock() {
      alertConfirmModalService.confirm({
        title: 'APP.WIKI.MODAL.UNLOCK.TITLE',
        text: 'APP.WIKI.MODAL.UNLOCK.TEXT',
        alertClass: 'alert-danger',
        translationContext: {
          lockHolder: vm.article.lockHolder.displayName
        }
      }).then(function () {
        wikiArticleService.unlock(vm.article).then(function () {
          _refreshLockInformation();
        });
      });
    }

    function deleteArticle(article) {
      wikiArticleService.deleteArticle(vm.app, article).then(function () {
        $state.go('^');
      });
    }

    function edit() {
      wikiArticleService.lock(vm.article, currentUser).then(function (lock) {
        vm.editMode = true;
        angular.extend(vm.article, lock);
        widgetLayoutService.edit($scope);
      }).catch(_refreshLockInformation);
    }

    function setAsHomeArticle() {
      var modaltext = vm.app.settings.home ? 'APP.WIKI.ARTICLE.HOME_ARTICLE.MODAL.TEXT.HOME_SET'
        : 'APP.WIKI.ARTICLE.HOME_ARTICLE.MODAL.TEXT.HOME_NOT_SET';
      modalService.confirm({
        title: 'APP.WIKI.ARTICLE.HOME_ARTICLE.MODAL.TITLE',
        text: modaltext,
        translationContext: {wikiName: vm.app.name},
        close: {title: 'YES'},
        dismiss: {title: 'NO'}
      }).result.then(function () {
        vm.app.settings.home = vm.article.id;
        vm.app.save().then(function (app) {
          $rootScope.$emit('app:updated', app);
          vm.app = app;
          coyoNotification.success('APP.WIKI.ARTICLE.HOME_ARTICLE.NOTIFICATION.SUCCESS');
        });
      });
    }

    function cancel() {
      vm.loading = true;
      wikiArticleService.releaseLock(vm.article, currentUser, false).finally(function () {
        widgetLayoutService.cancel($scope);
        _reloadInViewMode();
      });
    }

    function save() {
      wikiArticleTranslationService.prepareTranslations();

      var deferred = $q.defer();
      vm.loading = true;

      vm.article.update().then(function (savedArticle) {
        wikiArticleTranslationService.cleanup().then(function () {
          $timeout(function () { // wait to sync id to widget layout and slots
            widgetLayoutService.save($scope).then(function () {
              vm.editMode = false;
              wikiArticleService.releaseLock(savedArticle, currentUser, true).then(function () {
                deferred.resolve();
                _reloadInViewMode();
              });
            }).catch(function () {
              widgetLayoutService.edit($scope);
              deferred.reject();
            }).finally(function () {
              vm.loading = false;
            });
          });
        });
      }).catch(function () {
        deferred.reject();
        vm.loading = false;
      });

      return deferred.promise;
    }

    function showRevisions() {
      wikiArticleRevisionsModal.open(vm.article).then(openRevision);
    }

    function openRevision(revision) {
      $state.go($state.current, {revision: revision, editMode: false}, {reload: $state.current});
    }

    /******************* Helper methods *******************/

    function _refreshLockInformation() {
      var context = {
        senderId: vm.app.senderId,
        appId: vm.app.id,
        id: vm.article.id
      };
      return WikiArticleModel.getWithPermissions(context, {}, ['edit', 'delete']).then(function (article) {
        vm.article.locked = article.locked;
        vm.article.lockDate = article.lockDate;
        vm.article.lockHolder = article.lockHolder;
      });
    }

    function _reloadParentArticles() {
      vm.parentArticles = [];
      _loadParentArticles(article);
    }

    function _loadParentArticles(article) {
      if (!article.parentId || _.find(vm.parentArticles, {id: article.parentId})) {
        return;
      }
      var context = {
        senderId: app.senderId,
        appId: app.id,
        id: article.parentId
      };
      WikiArticleModel.getWithPermissions(context, {}, ['edit', 'delete']).then(function (article) {
        vm.parentArticles.unshift(article);
        _loadParentArticles(article);
      });
    }

    function _handleRemoved(event) {
      $log.debug('[WikiArticleService] Received event for wiki article lock removed', event);
      var previousHolder = event.content.previousHolder;

      // I had the lock (and I was in edit mode) -> show note that lock got removed
      if (vm.editMode && previousHolder === currentUser.id) {
        modalService.note({
          title: 'APP.WIKI.MODAL.LOCK.REMOVED.TITLE',
          text: 'APP.WIKI.MODAL.LOCK.REMOVED.TEXT'
        });
        _refreshLockInformation();
      }
    }

    function _handleReleased(event) {
      // someone else had the lock (and I wasn't in edit mode) -> update article
      $log.debug('[WikiArticleService] Received event for wiki article lock released', event);
      if (event.content.changed) {
        vm.article.latestRevision = false;
      }
      if (!vm.editMode) {
        _refreshLockInformation();
      }
    }

    function _handleLocked(event) {
      $log.debug('[WikiArticleService] Received event for wiki article lock set', event);
      if (!vm.editMode) {
        _refreshLockInformation();
      }
    }

    function _reloadInViewMode() {
      var stateParams = angular.extend(angular.copy($stateParams), {
        editMode: false
      });
      delete stateParams.revision;
      $state.transitionTo($state.current, stateParams, {
        reload: $state.current, inherit: false, notify: true
      });
    }

    /* ===== PRIVATE METHODS ===== */

    function init() {
      if (vm.article.wikiArticles > 0) {
        WikiArticleModel.getSubArticles(vm.app, vm.article.id).then(function (subArticles) {
          vm.subArticles = subArticles;
        });
      }

      widgetLayoutService.onload($scope).then(function () {
        if (vm.editMode && (!wikiArticleService.isLocked(vm.article)
                || wikiArticleService.hasLock(article, currentUser))) {
          edit();
        } else {
          vm.editMode = false;
        }
      }).finally(function () {
        vm.loading = false;
      });

      $scope.$watch(function () { return vm.article.parentId; }, function (newVal) {
        if (newVal === vm.article.id) {
          vm.article.parentId = null;
        }
      });

      _reloadParentArticles();

      var lock = '/topic/item.' + article.id + '.lock',
          unsubscribeSocketSubscriptionLocked = socketService.subscribe(lock, _handleLocked, 'set'),
          unsubscribeSocketSubscriptionUnlocked = socketService.subscribe(lock, _handleRemoved, 'removed'),
          unsubscribeSocketSubscriptionReleased = socketService.subscribe(lock, _handleReleased, 'released');

      $scope.$on('$destroy', function () {
        unsubscribeSocketSubscriptionLocked();
        unsubscribeSocketSubscriptionUnlocked();
        unsubscribeSocketSubscriptionReleased();
        if (vm.editMode) {
          wikiArticleService.releaseLock(vm.article, currentUser);
        }
      });

      _initTranslations();
    }

    function _initTranslations() {

      wikiArticleTranslationService.init($scope, vm, sender);

      var translations = [];
      _.forEach(vm.languages, function (value, key) {
        if (value.active && Object.keys(value.translations).length) {
          translations.push(key);
        }
      });

      currentUser.getBestSuitableLanguage(translations, SettingsModel.retrieve).then(function (language) {
        if (language !== 'NONE') {
          vm.currentLanguage = language;
          vm.languageInitialised[language] = true;
        }
      });
    }
  }
})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.apps.wiki')
      .factory('WikiArticleModel', WikiArticleModel);

  /**
   * @ngdoc service
   * @name coyo.domain.WikiArticleModel
   *
   * @description
   * Domain model representation of the wiki article configuration endpoint. Creates a new WikiArticleModel object.
   *
   * @requires coyo.apps.api.appResourceFactory
   */
  function WikiArticleModel(appResourceFactory) {
    var WikiArticle = appResourceFactory({
      appKey: 'wiki',
      url: '/articles/{{id}}',
      name: 'wikiArticle',
      extensions: ['snapshots']
    });

    // class members
    angular.extend(WikiArticle, {
      /**
       * @ngdoc function
       * @name coyo.domain.WikiArticleModel#count
       * @methodOf coyo.domain.WikiArticleModel
       *
       * @description
       * Counts all wiki articles for the given app.
       *
       * @returns {promise} An $http promise
       */
      count: function (app) {
        return this.$get(this.$url({
          senderId: app.senderId,
          appId: app.id
        }, 'count'));
      },

      /**
       * @ngdoc method
       * @name coyo.domain.WikiArticleModel#getSubArticles
       * @methodOf coyo.domain.WikiArticleModel
       *
       * @description
       * Returns the sub-articles of the given article.
       *
       * @param {object} app
       * The wiki app the given article belongs to.
       *
       * @param {string} parentId
       * The id of the article to return the sub-articles for.
       *
       * @returns {promise} an $http promise which resolves into an array of wiki articles.
       */
      getSubArticles: function (app, parentId) {
        return this.$get(this.$url({
          senderId: app.senderId,
          appId: app.id
        }) + '/children', {parentId: parentId});
      },

      /**
       * @ngdoc function
       * @name coyo.domain.WikiArticleModel#getSubArticlesWithPermissions
       * @methodOf coyo.domain.WikiArticleModel
       *
       * @description
       * Returns the sub articles of the given article, including the permissions.
       *
       * @param {object} app
       * The wiki app the given article belongs to.
       *
       * @param {string} parentId
       * The id of the article to return the sub-articles for.
       *
       * @returns {promise} an $http promise which resolves into an array of wiki articles including their permissions.
       */
      getSubArticlesWithPermissions: function (app, parentId) {
        var queryParams = this.applyPermissions(['edit', 'delete']);
        if (parentId) {
          queryParams.parentId = parentId;
        }
        return this.$get(this.$url({
          senderId: app.senderId,
          appId: app.id
        }) + '/children', queryParams);
      }
    });
    // instance members
    angular.extend(WikiArticle.prototype, {

      /**
       * @ngdoc function
       * @name coyo.domain.WikiArticleModel#buildLayoutName
       * @methodOf coyo.domain.WikiArticleModel
       *
       * @description
       * Build the localised layout name of a wiki article.
       *
       * @returns {string} The layout name
       */
      buildLayoutName: function (appId, key) {
        var name = 'app-wiki-' + appId + '-' + this.id + '-' + this.revisionNumber;
        if (!!key && !!this.defaultLanguage && key !== 'NONE' && this.defaultLanguage !== key) {
          name += '-' + key;
        }
        return name;
      },

      /**
       * @ngdoc function
       * @name coyo.domain.WikiArticleModel#lock
       * @methodOf coyo.domain.WikiArticleModel
       *
       * @description
       * Locks the article and returns it updated (with updated lock information).
       *
       * @returns {promise} An $http promise
       */
      lock: function () {
        return this.$post(this.$url('/lock'));
      },
      /**
       * @ngdoc function
       * @name coyo.domain.WikiArticleModel#unlock
       * @methodOf coyo.domain.WikiArticleModel
       *
       * @description
       * Unlocks the article and returns it updated (with updated lock information).
       *
       * @returns {promise} An $http promise
       */
      unlock: function (changed) {
        var params = (changed) ? {changed: changed} : {};
        return this.$delete(this.$url('/lock'), params);
      },

      /**
       * @ngdoc method
       * @name coyo.domain.WikiArticleModel#getRevisions
       * @methodOf coyo.domain.WikiArticleModel
       *
       * @description
       * Loads all revision information of the current article.
       *
       * @param {object=} pageable
       * The paging information. If not set an offset of 0 and a page size of 20 will be used.
       *
       * @returns {object[]} list of revision containing author, title and modification date
       */
      getRevisions: function (pageable) {
        return this.get('revisions', pageable.getParams());
      },

      /**
       * @ngdoc method
       * @name coyo.domain.WikiArticleModel#move
       * @methodOf coyo.domain.WikiArticleModel
       *
       * @description
       * Moves an article from one parent to another and / or changes the position within one parent.
       *
       * @param {string=} parentId
       * If set the article is moved to the parent with the given id. If not set the article is moved to the root of
       * the app. If you don't ant to move the article to another parent, you will have to pass the current parent ID.
       *
       * @param {number} sortOrder
       * The new index the article should be moved to.
       *
       * @returns {object} a promise which returns the updated article on success.
       */
      move: function (parentId, sortOrder) {
        var params = {
          parentId: parentId,
          sortOrder: sortOrder
        };
        return this.$put(this.$url('move'), {}, params);
      }
    });

    return WikiArticle;
  }

})(angular);

(function () {
  'use strict';

  angular.module('coyo.apps.wiki')
      .factory('wikiArticleService', wikiArticleService);

  /**
   * @ngdoc service
   * @name coyo.apps.wiki.wikiArticleService
   *
   * @description
   * This service provides a common way manage articles.
   *
   * @requires $log
   * @requires commons.ui.alertConfirmModalService
   */
  function wikiArticleService($log, $q, $interval, alertConfirmModalService) {
    var lockInterval = {};
    var lockIntervalMilliseconds = 5 * 60 * 1000;

    return {
      deleteArticle: deleteArticle,
      lock: lock,
      unlock: unlock,
      releaseLock: releaseLock,
      isLocked: isLocked,
      hasLock: hasLock
    };

    /**
     * @ngdoc method
     * @name coyo.apps.wiki.wikiArticleService#deleteArticle
     * @methodOf coyo.apps.wiki.wikiArticleService
     *
     * @description
     * Opens a modal to delete an article. It checks whether the article has subarticles or is currently the home
     * article of the app. If this is the case warnings are displayed.
     *
     * @param {object} app
     * The app the article belongs to
     *
     * @param {object} article
     * The article to delete
     *
     * @returns {object} a promise which is resolved after the article is deleted.
     */
    function deleteArticle(app, article) {
      var alerts = [];
      var translationContext = {
        title: article.title
      };

      if (article.wikiArticles > 0) {
        alerts.push({
          alertClass: 'alert-danger',
          alertTitle: 'APP.WIKI.ARTICLE.DELETE.WARNING.TITLE',
          alertText: 'APP.WIKI.ARTICLE.DELETE.MULTIPLE.TEXT'
        });
        translationContext.noOfArticles = article.wikiArticles;
      }

      if (app.settings.home === article.id) {
        alerts.push({
          alertClass: 'alert-danger',
          alertTitle: 'APP.WIKI.ARTICLE.DELETE.WARNING.TITLE',
          alertText: 'APP.WIKI.ARTICLE.DELETE.HOME.TEXT'
        });
      }

      return alertConfirmModalService.confirm({
        title: 'APP.WIKI.MODAL.DELETE.TITLE',
        text: 'APP.WIKI.MODAL.DELETE.TEXT',
        alerts: alerts,
        translationContext: translationContext
      }).then(function () {
        return article.delete().then(function () {
          if (app.settings.home === article.id) {
            $log.debug('Deleted home article. Removing article as home from wiki app.');
            app.settings.home = '';
            return app.save();
          }
          return undefined; // return undefined to have a consistent return value
        });
      });
    }

    /**
     * @ngdoc method
     * @name coyo.apps.wiki.wikiArticleService#lock
     * @methodOf coyo.apps.wiki.wikiArticleService
     *
     * @description
     * Locks the given article for the current user and renews the lock every five minutes. Locking fails if the user
     * does not have the permission to lock the article or if the article is already locked. If successfully locked an
     * `/topic/item.&lt;article-id&gt;.lock.set` event is broadcast via websockets.
     *
     * @param {object} article
     * The article to lock.
     *
     * @param {object} currentUser
     * The current user who locks the article.
     *
     * @returns {object} A promise which is resolved on success and rejected if the lock could not be acquired.
     */
    function lock(article, currentUser) {
      var deferred = $q.defer();
      article.lock().then(function (lock) {
        if (hasLock(lock, currentUser)) {
          _startLockRefreshInterval(lock);
          deferred.resolve(lock);
        } else {
          deferred.reject();
        }
      });
      return deferred.promise;
    }

    /**
     * @ngdoc method
     * @name coyo.apps.wiki.wikiArticleService#unlock
     * @methodOf coyo.apps.wiki.wikiArticleService
     *
     * @description
     * (Forcefully) unlocks the given article independently of who owns the lock right now and stops the renewing of
     * the lock. Unlocking fails if the user does not have the permission to unlock the article. If successfully
     * unlocked a `/topic/item.&lt;article-id&gt;.lock.removed` event is broadcast via websockets.
     *
     * @param {object} article The article to unlock.
     * @returns {object} A promise which is resolved if unlocking was successful and rejected if it failed.
     */
    function unlock(article) {
      return article.unlock().then(function () {
        _cancelLockRefreshInterval(article);
      });
    }

    /**
     * @ngdoc method
     * @name coyo.apps.wiki.wikiArticleService#releaseLock
     * @methodOf coyo.apps.wiki.wikiArticleService
     *
     * @description
     * Releases the lock from the given article if it is owned by the current user. If successfully released
     * a `/topic/item.&lt;article-id&gt;.lock.released` event is broadcast via websockets. The renewing of the lock is
     * stopped if the current user owns the lock - regardless if the actual unlocking was successful or not.
     *
     * @param {object} article
     * The article to unlock
     *
     * @param {object} currentUser
     * The current user who releases the article. This user needs to own the lock to the article. If not, nochting
     * happens.
     *
     * @param {boolean=false} changed
     * If the article was changed, this parameter should be set `true`. This way event recipients can see that they need
     * to refresh the article.
     *
     * @returns {object} A promise which is resolved if releasing the lock was successful and rejected in any other
     * case.
     */
    function releaseLock(article, currentUser, changed) {
      var deferred = $q.defer();
      if (isLocked(article) && hasLock(article, currentUser)) {
        return article.unlock(changed).finally(function () {
          _cancelLockRefreshInterval(article);
        });
      }
      deferred.reject();
      return deferred.promise;
    }

    /**
     * @ngdoc method
     * @name coyo.apps.wiki.wikiArticleService#isLocked
     * @methodOf coyo.apps.wiki.wikiArticleService
     *
     * @description
     * Returns whether the given article is locked.
     *
     * @param {object} lockable
     * The lockable entity to check.
     *
     * @returns {boolean} `true` if the given article is locked, `false` otherwise.
     */
    function isLocked(lockable) {
      return lockable.locked;
    }

    /**
     * @ngdoc method
     * @name coyo.apps.wiki.wikiArticleService#hasLock
     * @methodOf coyo.apps.wiki.wikiArticleService
     *
     * @description
     * Returns whether the given lock is owned by the given user.
     *
     * @param {object} lockable
     * The lockable entity to check
     *
     * @param {object} user
     * The user to check
     *
     * @returns {boolean} `true` if the given article is locked by the given user, `false` otherwise.
     */
    function hasLock(lockable, user) {
      return (!!lockable.lockHolder && (lockable.lockHolder.id === user.id));
    }

    /******************* PRIVATE METHODS *******************/

    function _startLockRefreshInterval(lockable) {
      lockInterval[lockable.id] = $interval(function () {
        _refreshLock(lockable);
      }, lockIntervalMilliseconds);
    }

    function _cancelLockRefreshInterval(lockable) {
      $interval.cancel(lockInterval[lockable.id]);
      delete lockInterval[lockable.id];
    }

    function _refreshLock(article) {
      article.lock().then(function (lock) {
        $log.debug('[WikiArticleService] Refreshed lock', lock);
      }).catch(function () {
        _cancelLockRefreshInterval(article);
      });
    }

  }

})();

(function (angular) {
  'use strict';

  angular
      .module('coyo.apps.wiki')
      .controller('WikiListController', WikiListController);

  /**
   * Controller for wiki article listing.
   *
   * @requires $log
   * @requires $state
   * @requires coyo.apps.wiki.WikiArticleService
   * @requires coyo.apps.wiki.WikiArticleModel
   * @requires app
   * @constructor
   */
  function WikiListController($log, $state, wikiArticleService, WikiArticleModel, app) {
    var vm = this;
    vm.app = app;
    vm.articleCount = 0;
    vm.initialLoading = true;
    vm.loading = true;
    vm.wikiArticles = [];
    vm.oldParent = undefined;

    vm.toggle = toggle;
    vm.loadSubArticles = loadSubArticles;
    vm.deleteArticle = deleteArticle;
    vm.openView = openView;
    vm.treeOptions = {
      dropped: dropped,
      dragMove: dragMove
    };

    _init();

    function toggle(article) {
      article.collapsed = !article.collapsed;
    }

    function loadSubArticles(article) {
      if (vm.initialLoading || !vm.loading) {
        vm.loading = true;
        if (angular.isDefined(article)) {
          article.loading = true;
        }
        return WikiArticleModel
            .getSubArticlesWithPermissions(app, _.get(article, 'id'))
            .then(function (result) {
              if (vm.wikiArticles.length === 0) {
                vm.wikiArticles = result;
                _initializeNodes(vm.wikiArticles);
              } else {
                article.nodesLoaded = true;
                article.collapsed = false;
                article.nodes = result;
                _initializeNodes(article.nodes);
              }
            }).finally(function () {
              vm.loading = false;
              vm.initialLoading = false;
              if (angular.isDefined(article)) {
                article.loading = true;
              }
            });
      }

      return null;
    }

    function deleteArticle(article) {
      wikiArticleService.deleteArticle(vm.app, article).then(function () {
        $state.reload($state.current.name);
      });
    }

    function openView(article) {
      if (!vm.loading) {
        $state.go('.view', {id: article.id});
      }
    }

    function dragMove(event) {
      // Add class to current parent element for highlighting
      var destNode = _(event.dest.nodesScope.$nodeScope).get('$element[0].firstElementChild');
      if (destNode) {
        if (!vm.oldParent) {
          vm.oldParent = destNode;
        } else if (destNode !== vm.oldParent) {
          angular.element(vm.oldParent).removeClass('ui-tree-is-parent');
        }
        angular.element(destNode).addClass('ui-tree-is-parent');
        vm.oldParent = destNode;
      } else if (!destNode && vm.oldParent) {
        angular.element(vm.oldParent).removeClass('ui-tree-is-parent');
        vm.oldParent = undefined;
      }
    }

    function dropped(event) {
      if (!vm.loading) {
        vm.loading = true;

        // Remove highlight-class from current parent element
        if (vm.oldParent) {
          angular.element(vm.oldParent).removeClass('ui-tree-is-parent');
          vm.oldParent = undefined;
        }

        var sourceNode = event.source.nodeScope.$modelValue;
        var destNode = event.dest.nodesScope.$nodeScope ? event.dest.nodesScope.$nodeScope.$modelValue : null;
        var destIndex = event.dest.index;
        var parentId = destNode ? destNode.id : null;
        var oldParentId = (sourceNode.parentId) ? sourceNode.parentId : null;

        if (destNode) {

          // Check Parent Node to dragg new Child into closed Parent
          if (!destNode.nodesLoaded && destNode.wikiArticles > 0) {
            destNode.collapsed = true;
          // Check Parent Node to toggle arrow when pull in first child
          } else if (destNode.wikiArticles === 0) {
            destNode.nodesLoaded = true;
          }
        }

        // if the article wasn't moved, do nothing
        if (parentId === oldParentId && destIndex === sourceNode.sortOrder) {
          vm.loading = false;
          return;
        }

        sourceNode.move(parentId, destIndex).catch(function () {
          $log.error('[WikiList] Could not re-order wiki article source node',
              sourceNode, 'into destination node', destNode, 'at index', destIndex);
          $state.go($state.current, {}, {reload: $state.current});
        }).finally(function () {
          vm.loading = false;
        });
      }
    }

    /******************* Helper methods *******************/
    function _init() {
      WikiArticleModel.count(vm.app).then(function (data) {
        vm.articleCount = data;
      });
      loadSubArticles();
    }

    function _initializeNodes(nodes) {
      // initialize nodes to be able to drop nodes as sub-nodes into this one
      _.forEach(nodes, function (node) {
        node.nodes = [];
        node.nodesLoaded = false;
        node.collapsed = false;
      });
    }
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.apps.wiki')
      .controller('WikiMainController', WikiMainController);

  function WikiMainController($state, $timeout, app) {
    /*
     * We need a timeout here because the state has to be written into the browsers history first before we proceed
     * with the redirect. Otherwise we would overwrite the last history entry.
     */
    $timeout(function () {
      if (app.settings.home) {
        $state.go('^.list.view', {id: app.settings.home}, {location: 'replace'});
      } else {
        $state.go('^.list', {}, {location: 'replace'});
      }
    });
  }

})(angular);

