(function (angular) {
  'use strict';

  angular
      .module('coyo.widgets.downloads')
      .controller('DownloadsWidgetSettingsController', DownloadsWidgetSettingsController);

  function DownloadsWidgetSettingsController($scope, SenderModel, FileModel, $translate, utilService, $q) {
    var vm = this;

    vm.model = $scope.model;

    $scope.$watch(function () { return vm.model.selectedFiles; }, function (newVal, oldVal) {
      if (oldVal !== newVal) {
        vm.model.settings._files = _.map(vm.model.selectedFiles, function (file) {
          return {
            senderId: file.senderId,
            id: file.id,
            title: file.title,
            name: file.originalName || file.name
          };
        });
      }
    });

    function getFile(file) {
      // Set empty file ids to false, because otherwise it will get all files of the sender
      var fileId = file.id ? file.id : false;
      return FileModel.get({senderId: file.senderId, id: fileId}).then(function (loadedFile) {
        loadedFile.senderId = file.senderId;
        loadedFile.title = file.title;
        return loadedFile;
      }).catch(function () {
        return $translate('WIDGETS.DOWNLOADS.UNABLE_TO_LOAD').then(function (msg) {
          return {
            title: file.title,
            name: ('<' + msg + ' ' + file.name + '>'),
            originalName: file.name,
            id: fileId || utilService.uuid(),
            senderId: file.senderId
          };
        });
      });
    }

    (function _init() {
      vm.loading = true;

      var senderIdOrSlug = SenderModel.getCurrentIdOrSlug();
      if (angular.isDefined(senderIdOrSlug)) {
        SenderModel.getWithPermissions(senderIdOrSlug, {}, ['manage']).then(function (sender) {
          vm.model.settings._sender = sender;
        });
      }

      if (vm.model.settings._files) {
        $q.all(_.map(vm.model.settings._files, getFile)).then(function (loadedFiles) {
          vm.model.selectedFiles = loadedFiles;
          vm.loading = false;
        });
      } else {
        vm.loading = false;
      }
    })();
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.widgets.downloads')
      .directive('coyoDownloadsWidget', downloadsWidget)
      .controller('DownloadsWidgetController', DownloadsWidgetController);

  /**
   * @ngdoc directive
   * @name coyo.widgets.downloads:coyoDownloadsWidget
   * @element ANY
   * @restrict E
   * @scope
   *
   * @description
   * Renders the widget to show a list of download files
   *
   * @param {object} widget
   * The widget configuration
   */
  function downloadsWidget() {
    return {
      restrict: 'E',
      templateUrl: 'app/widgets/downloads/downloads-widget.html',
      scope: {},
      bindToController: {
        widget: '<',
        editMode: '<'
      },
      controller: 'DownloadsWidgetController',
      controllerAs: '$ctrl'
    };
  }

  function DownloadsWidgetController($scope, DocumentModel, errorService, FileModel, widgetStatusService, backendUrlService) {
    var vm = this;
    vm.loading = true;
    var numberOfFilesToBeChecked = 0;

    vm.getDownloadUrl = getDownloadUrl;

    widgetStatusService.refreshOnSettingsChange($scope);

    function getDownloadUrl(senderId, id) {
      // Deliver false as id in response body instead of an empty fileId,
      // because otherwise it will get all files of the sender
      var fileId = id ? id : false;
      return FileModel.get({senderId: senderId, id: fileId}).then(function () {
        return backendUrlService.getUrl() + DocumentModel.$url({
          senderId: senderId,
          id: fileId
        });
      });
    }

    function _getFile(element, index, array) {
      // check whether file can be load
      FileModel.get({senderId: element.senderId, id: element.id}).then(function () {
        --numberOfFilesToBeChecked;
        vm.widget.settings._files.push(array[index]);
        if (numberOfFilesToBeChecked <= 0) {
          vm.loading = false;
        }
      }).catch(function (errorResponse) {
        --numberOfFilesToBeChecked;
        if (errorResponse.status === 404) {
          errorService.suppressNotification(errorResponse);
        }
        if (numberOfFilesToBeChecked <= 0) {
          vm.loading = false;
        }
      });
    }

    (function _init() {
      if (vm.widget.settings._files) {
        var arrayFiles = vm.widget.settings._files;
        numberOfFilesToBeChecked = arrayFiles.length;
        vm.widget.settings._files = [];
        _.forEach(arrayFiles, _getFile);
      } else {
        vm.loading = false;
      }
    })();
  }
})(angular);

