(function (angular) {
  'use strict';

  angular
      .module('coyo.widgets.poll')
      .controller('PollWidgetVotersController', PollWidgetVotersController);

  function PollWidgetVotersController(PollWidgetModel, Pageable, widget, optionId) {
    var vm = this;
    vm.voters = [];
    vm.loadMore = loadMore;
    vm.$onInit = _init;

    function loadMore() {
      if (!vm.currentPage || !vm.currentPage.last) {
        vm.loading = true;

        var pageable = new Pageable((vm.currentPage ? vm.currentPage.number + 1 : 0), 20, 'created,desc');
        PollWidgetModel.loadVoters(widget._id, optionId, pageable).then(function (page) {
          vm.currentPage = page;
          vm.voters = vm.voters.concat(page.content);
        }).finally(function () {
          vm.loading = false;
        });
      }
    }

    function _init() {
      loadMore();
    }
  }
})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.widgets.poll')
      .controller('PollWidgetSettingsController', PollWidgetSettingsController);

  function PollWidgetSettingsController($scope, PollWidgetModel, $q) {
    var vm = this;
    vm.model = $scope.model;
    vm.focusedAnswer = '';

    vm.addOption = addOption;
    vm.deleteOption = deleteOption;
    vm.optionKeyPressed = optionKeyPressed;

    vm.editable = true;
    vm.optionsCount = 1;

    function addOption() {
      var newAnswer = {
        id: vm.model.settings.nextOptionId++,
        answer: '',
        newAnswer: true
      };
      vm.model.settings.options.push(newAnswer);
      vm.focusedAnswer = newAnswer.id;
    }

    function deleteOption(index) {
      vm.model.settings.options.splice(index, 1);
    }

    function optionKeyPressed($event) {
      if ($event.keyCode === 13 && !$event.ctrlKey && !$event.shiftKey) {
        $event.preventDefault();
        addOption();
      }
    }

    function onBeforeSave() {
      vm.model.settings.options = _getNonBlankOptions();
      vm.model.settings.options.forEach(function (option) {
        option.newAnswer = false;
      });
      return $q.resolve();
    }

    function _getNonBlankOptions() {
      return _.filter(vm.model.settings.options, function (elem) { return elem.answer; });
    }

    (function activate() {
      $scope.saveCallbacks.onBeforeSave = onBeforeSave;
      vm.tab = 1;

      //if this is a new widget, set the default values
      if (!vm.model.id && !vm.model.settings.options) {
        vm.model.settings = {
          nextOptionId: 2,
          options: [{answer: '', id: 1}],
          showResults: true,
          maxAnswers: 1
        };
      } else if (vm.model.id) {
        PollWidgetModel.getVotes(vm.model.id).then(function (result) {
          if (result.length !== 0) {
            vm.editable = false;
          }
        });
      }

      $scope.$watch(function () {
        return vm.model.settings.options;
      }, function () {
        vm.optionsCount = _getNonBlankOptions().length;
      }, true);

    })();
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.widgets.poll')
      /**
       * @ngdoc directive
       * @name coyo.widgets.poll.coyoPollWidget
       * @restrict 'E'
       *
       * @description
       * A widget which contains a poll
       *
       * @param {object} widget - poll widget
       * @param {object} editMode - true when widget editing is activated
       * @param {object} settingsMode - true when widget is shown in settings dialog
       */
      .component('coyoPollWidget', {
        templateUrl: 'app/widgets/poll/poll-widget.html',
        bindings: {
          widget: '=',
          editMode: '<',
          settingsMode: '<'
        },
        controller: 'pollWidgetController'
      })
      .controller('pollWidgetController', pollWidgetController);

  function pollWidgetController(PollWidgetModel, modalService, $scope) {
    var vm = this;
    vm.answers = [];
    vm.overallAmount = 0;
    vm.loading = false;
    vm.checkableOptions = [];
    vm.getNumOfVotesForOption = 0;
    vm.getPercentageForOption = 0;
    vm.toggleOption = toggleOption;
    vm.canVote = canVote;
    vm.showVoters = showVoters;

    function toggleOption(option) {
      if (vm.loading || !canVote(option)) {
        return;
      }
      if (!option.checked) {
        _selectAnswer(option);
      } else {
        _deleteAnswer(option);
      }
    }

    function getNumOfVotesForOption(option) {
      if (!vm.votes) {
        return 0;
      }
      var voteForAnswer = _.find(vm.votes, function (elem) {
        return elem.optionId === String(option.id);
      });
      option.votes = voteForAnswer ? voteForAnswer.count : 0;
      return option.votes;
    }

    function calculatePercentageForOption(option) {
      option.percentage = vm.overallAmount ? (getNumOfVotesForOption(option) / vm.overallAmount) * 100 : 0;
      return option.percentage;
    }

    function canVote(option) {
      return !vm.editMode && !vm.widget.settings.frozen && (option.checked || vm.widget.settings.maxAnswers > vm.answers.length);
    }

    function selectAnswers() {
      vm.answers.forEach(function (elem) {
        var answerOption = _.find(vm.checkableOptions, function (option) { return '' + option.id === elem.optionId; });
        if (answerOption) {
          answerOption.checked = true;
        }
      });
    }

    function refreshVotes() {
      if (vm.widget._id && (vm.settingsMode || vm.widget.settings.showResults)) {
        PollWidgetModel.getVotes(vm.widget._id).then(function (votes) {
          vm.votes = votes;
          vm.overallAmount = _.sumBy(votes, function (vote) { return vote.count; });
          vm.checkableOptions.forEach(getNumOfVotesForOption);
          vm.checkableOptions.forEach(calculatePercentageForOption);
        });
      }
    }

    function showVoters(option) {
      if (vm.loading || vm.widget.settings.anonymous) {
        return; // currently loading
      }

      modalService.open({
        templateUrl: 'app/widgets/poll/poll-voters.modal.html',
        controller: 'PollWidgetVotersController',
        resolve: {
          widget: function () {
            return vm.widget;
          },
          optionId: function () {
            return option.id;
          }
        }
      });
    }

    function _refreshWidget() {
      vm.checkableOptions = vm.widget.settings.options.map(function (option) {
        var checkableOption = _.clone(option);
        checkableOption.votes = 0;
        checkableOption.percentage = 0;
        return checkableOption;
      });
      refreshVotes();
      if (vm.widget._id) {
        PollWidgetModel.getSelectedAnswers(vm.widget._id).then(function (answers) {
          vm.answers = answers;
          selectAnswers();
        });
      }
    }

    function _selectAnswer(option) {
      vm.loading = true;
      PollWidgetModel.selectAnswer(vm.widget._id, option.id).then(function (userAnswer) {
        vm.answers.push(userAnswer);
        refreshVotes();
        option.checked = true;
      }).finally(function () { vm.loading = false; });
    }

    function _deleteAnswer(option) {
      vm.loading = true;
      var index = _.findIndex(vm.answers, function (answer) {
        return answer.optionId === String(option.id);
      });
      PollWidgetModel.deleteAnswer(vm.widget._id, vm.answers[index].id).then(function () {
        refreshVotes();
        option.checked = false;
        vm.answers.splice(index, 1);
      }).finally(function () { vm.loading = false; });
    }

    vm.$onInit = function () {
      $scope.$watch(function () { return vm.widget.settings; }, function (before, after) {
        if (before !== after) {
          _refreshWidget();
        }
      });

      _refreshWidget();
    };
  }

})(angular);

(function (angular) {
  'use strict';

  angular.module('coyo.widgets.poll')
      .factory('PollWidgetModel', PollWidgetModel);

  /**
   * @ngdoc service
   * @name coyo.widgets.poll.PollWidgetModel
   *
   * @description
   * Domain model representation for a poll shown in the poll widget
   *
   * @requires restResourceFactory
   */
  function PollWidgetModel(restResourceFactory) {
    var Widget = restResourceFactory({
      url: '/web/widgets/poll',
      httpConfig: {
        autoHandleErrors: false
      }
    });

    angular.extend(Widget, {

      /**
       * @ngdoc function
       * @name coyo.widgets.poll.PollWidgetModel#getSelectedAnswer
       * @methodOf coyo.widgets.poll.PollWidgetModel
       *
       * @description
       * Returns the selected answer(s) of the current user
       *
       * @param {string} widgetId ID of the poll.
       *
       * @returns {promise} An $http promise resolving the selected answers.
       */
      getSelectedAnswers: function (widgetId) {
        return Widget.get(widgetId + '/selected-answers');
      },

      /**
       * @ngdoc function
       * @name coyo.widgets.poll.PollWidgetModel#selectAnswer
       * @methodOf coyo.widgets.poll.PollWidgetModel
       *
       * @description
       * Posts an Answer of the current user
       *
       * @param {string} widgetId ID of the poll.
       * @param {string} optionId of the option (unique for the poll).
       *
       * @returns {promise} An $http promise resolving the newly created answer.
       */
      selectAnswer: function (widgetId, optionId) {
        return this.$post(this.$url() + '/' + widgetId + '/selected-answers', {optionId: optionId});
      },

      /**
       * @ngdoc function
       * @name coyo.widgets.poll.PollWidgetModel#getVotes
       * @methodOf coyo.widgets.poll.PollWidgetModel
       *
       * @description
       * Returns the number of votes for each option
       *
       * @param {string} widgetId ID of the poll.
       *
       * @returns {promise} An $http promise resolving a list of objects
       * containing the optionId and the number of votes.
       */
      getVotes: function (widgetId) {
        return Widget.get(widgetId + '/votes');
      },

      /**
       * @ngdoc function
       * @name coyo.widgets.poll.PollWidgetModel#deleteAnswer
       * @methodOf coyo.widgets.poll.PollWidgetModel
       *
       * @description
       * Deletes an answer of a user
       *
       * @param {string} widgetId ID of the poll.
       * @param {string} answerId the unique id of the answer to be deleted.
       *
       * @returns {promise} An $http promise resolving nothing.
       */
      deleteAnswer: function (widgetId, answerId) {
        return Widget.$delete(Widget.$url() + '/' + widgetId + '/selected-answers/' + answerId);
      },

      /**
       * @ngdoc function
       * @name coyo.widgets.poll.PollWidgetModel#loadVoters
       * @methodOf coyo.widgets.poll.PollWidgetModel
       *
       * @description
       * Returns a list of users who has voted for the given option
       *
       * @param {string} widget ID of the poll.
       * @param {string} optionId of the option (unique for the poll).
       * @param {Pageable} pageable pageination information.
       *
       * @returns {promise} An $http promise resolving a list of users.
       */
      loadVoters: function (widgetId, optionId, pageable) {
        return Widget.pagedQuery(pageable, {}, {}, widgetId + '/' + optionId + '/voters');
      }
    });

    return Widget;
  }

})(angular);

