(function (angular) {
  'use strict';

  angular
      .module('coyo.widgets.rss')
      .controller('RssWidgetSettingsController', RssWidgetSettingsController);

  function RssWidgetSettingsController($scope, RssWidgetModel) {
    var vm = this;
    vm.verifyUrl = verifyUrl;
    vm.$onInit = onInit;

    function verifyUrl() {
      if (vm.loading) {
        return;
      }
      vm.valid = undefined;
      vm.validationMessage = 'loading';
      vm.loading = true;

      RssWidgetModel.verifyUrl(vm.model.settings.rssUrl, vm.model.settings.userName,
          vm.model.settings._encrypted_password)
          .then(handleVerification).finally(function () {
            vm.loading = false;
          });

      function handleVerification(verified) {
        vm.valid = verified.valid;
        vm.validationMessage = verified.validationMessage;

        if (!verified.valid && verified.validationMessage === 'unauthorized' && vm.unauthorized === true) {
          vm.validationMessage = 'invalidCredentials';
        } else if (!verified.valid && verified.validationMessage === 'unauthorized') {
          vm.unauthorized = true;
        }
      }
    }

    function onInit() {
      vm.tab = 1;
      vm.valid = true;
      vm.model = $scope.model;

      if (!vm.model.settings.maxCount && !vm.model.settings.displayImage) {
        $scope.model.settings.displayImage = true;
        $scope.model.settings.maxCount = 5;
      }
    }
  }
})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.widgets.rss')
      /**
       * @ngdoc directive
       * @name coyo.widgets.rss.coyoRssWidget
       * @restrict 'E'
       *
       * @description
       * A widget which contains a rss feed
       *
       * @param {object} widget - rss widget
       * @param {object} editMode - true when widget editing is activated
       * @param {object} settingsMode - true when widget is shown in settings dialog
       */
      .component('coyoRssWidget', {
        templateUrl: 'app/widgets/rss/rss-widget.html',
        bindings: {
          widget: '=',
          editMode: '<',
          settingsMode: '<'
        },
        controller: 'rssWidgetController'
      })
      .controller('rssWidgetController', rssWidgetController);

  function rssWidgetController($scope, RssWidgetModel) {
    var vm = this;
    vm.$onInit = init;
    vm.loadEntries = loadEntries;

    vm.emptyEntries = false;
    vm.loadData = undefined;

    function loadEntries() {
      if (!vm.widget.id || vm.editMode) {
        vm.loadData = RssWidgetModel
            .getEntriesForPreview(vm.widget.settings.rssUrl, vm.widget.settings.displayImage, vm.widget.settings.maxCount)
            .then(handleFeed);
      } else {
        vm.loadData = RssWidgetModel.getEntries(vm.widget.id).then(handleFeed);
      }

      return vm.loadData;
    }

    function handleFeed(entries) {
      vm.entries = entries;
      return entries;
    }

    function init() {
      var unregisterWatch = $scope.$watch(function () {
        return vm.widget.settings;
      }, function (before, after) {
        if (before !== after) {
          loadEntries();
        }
      });
      $scope.$on('$destroy', unregisterWatch);
      loadEntries();
    }
  }
})(angular);

(function (angular) {
  'use strict';

  angular.module('coyo.widgets.rss')
      .factory('RssWidgetModel', RssWidgetModel);

  /**
   * @ngdoc service
   * @name coyo.widgets.rss.RssWidgetModel
   *
   * @description
   * Domain model representation for a rss feed shown in the rss widget
   *
   * @requires restResourceFactory
   */
  function RssWidgetModel(restResourceFactory) {
    var Widget = restResourceFactory({
      url: '/web/widgets/rss',
    });

    angular.extend(Widget, {

      /**
       * @ngdoc function
       * @name coyo.widgets.rss.RssWidgetModel#verifyUrl
       * @methodOf coyo.widgets.rss.RssWidgetModel
       *
       * @description
       * Returns a boolean whether the Url is verified rss feed url
       *
       * @param {string} rssUrl Url of the rss feed.
       *
       * @returns {promise} An $http promise resolving the rss url.
       */
      verifyUrl: function (rssUrl, userName, password) {
        return this.$post(this.$url() + '/verify', {rssUrl: rssUrl, userName: userName, password: password});
      },

      /**
       * @ngdoc function
       * @name coyo.widgets.rss.RssWidgetModel#getEntries
       * @methodOf coyo.widgets.rss.RssWidgetModel
       *
       * @description
       * Returns the entries for the clarified rss url
       *
       * @param {string} widgetId ID of the rss feed.
       *
       * @returns {promise} An $http promise resolving the entries.
       */
      getEntries: function (id) {
        return Widget.get(id + '/entries');
      },

      /**
       * @ngdoc function
       * @name coyo.widgets.rss.RssWidgetModel#getEntriesForPreview
       * @methodOf coyo.widgets.rss.RssWidgetModel
       *
       * @description
       * Returns the preview entries for the clarified rss url
       *
       * @returns {promise} An $http promise resolving the entries.
       */
      getEntriesForPreview: function (url, image, size) {
        return Widget.get('/entries', {url: url, images: image, size: size});
      }
    });
    return Widget;
  }
})(angular);

