(function (angular) {
  'use strict';

  angular.module('coyo.widgets.suggestpages')
      .directive('coyoSelectPages', selectPages);

  /**
   * @ngdoc directive
   * @name coyo.widgets.suggestpages:coyoSelectPages
   * @element ANY
   * @restrict E
   * @scope
   *
   * @description
   * Renders a ui-select to select multiple pages.
   */
  function selectPages(selectFactoryModel, PageModel, Pageable) {
    return selectFactoryModel({
      refresh: refresh,
      multiple: true,
      pageSize: 50,
      limit: 10,
      emptyText: 'WIDGETS.SUGGEST_PAGES.NO_PAGE_FOUND'
    });

    function refresh(pageable, search) {
      return PageModel.pagedQuery(new Pageable(pageable.page, pageable.size), {name: search})
          .then(function (response) {
            return angular.extend(_.map(response.content, function (page) {
              return {
                id: page.id,
                displayName: page.name
              };
            }), {
              meta: {
                last: response.last,
                number: response.number
              }
            });
          });
    }
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.widgets.suggestpages')
      .controller('SuggestPagesWidgetSettingsController', SuggestPagesWidgetSettingsController);

  function SuggestPagesWidgetSettingsController($scope, PageModel) {
    var vm = this;
    vm.model = $scope.model;
    vm.ableToLoadAllPages = true;
    vm.loading = true;

    $scope.$watch(function () { return vm.model.selectedPages; }, function (newVal, oldVal) {
      if (oldVal !== newVal) {
        vm.model.settings._pageIds = _.map(vm.model.selectedPages, function (page) {
          return '' + page.id;
        });
      }
    });

    (function _init() {
      if (!vm.model.settings._pageIds) {
        vm.loading = false;
        return;
      }

      PageModel.$get(PageModel.$url(), {pageIds: vm.model.settings._pageIds}).then(function (res) {
        vm.model.selectedPages = res;
        vm.ableToLoadAllPages = vm.model.settings._pageIds.length === vm.model.selectedPages.length;
      }).finally(function () {
        vm.loading = false;
      });
    })();
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.widgets.suggestpages')
      .directive('coyoSuggestPagesWidget', suggestPagesWidget)
      .controller('SuggestPageWidgetController', SuggestPageWidgetController);

  /**
   * @ngdoc directive
   * @name coyo.widgets.suggestpages:coyoSuggestPagesWidget
   * @element ANY
   * @restrict E
   * @scope
   *
   * @description
   * Renders the widget to display a selection of pages
   *
   * @param {object} widget
   * The widget configuration
   */
  function suggestPagesWidget() {
    return {
      restrict: 'E',
      templateUrl: 'app/widgets/suggest-pages/suggest-pages-widget.html',
      scope: {},
      bindToController: {
        widget: '<'
      },
      controller: 'SuggestPageWidgetController',
      controllerAs: '$ctrl'
    };
  }

  function SuggestPageWidgetController($scope, $q, PageModel, authService, widgetStatusService, subscriptionsService) {
    var vm = this;

    vm.loading = false;

    vm.loadPages = loadPages;

    widgetStatusService.refreshOnSettingsChange($scope);

    function _getSubscribedPageIds() {
      return authService.getUser().then(function (user) {
        return subscriptionsService.getSubscriptionsByType(user.id, 'page').then(function (subscriptions) {
          return _.map(subscriptions, 'targetId');
        });
      });
    }

    function loadPages() {
      vm.loading = true;

      return _getSubscribedPageIds().then(function (subscribedIds) {
        var pageIds = _.difference(vm.widget.settings._pageIds, subscribedIds);
        var pagePromise = pageIds.length ? PageModel.$get(PageModel.$url(), {pageIds: pageIds}) : $q.resolve([]);
        return pagePromise.then(function (pages) {
          vm.pages = pages;
          return pages;
        }).finally(function () {
          vm.lastUpdate = new Date().getTime();
          vm.loading = false;
        });
      });
    }
  }

})(angular);

