(function (angular) {
  'use strict';

  angular
      .module('coyo.widgets.video')
      .controller('VideoWidgetSettingsController', VideoWidgetSettingsController);

  function VideoWidgetSettingsController($scope, $http, $q, $timeout, coyoEndpoints) {
    var vm = this;

    vm.model = $scope.model;
    vm.safeUrl = true;

    vm.checkUrl = checkUrl;

    function checkUrl() {
      // use s.substring() because s.startsWith() is not supported by older IE versions
      vm.safeUrl = !vm.model.settings.url || vm.model.settings.url.substring(0, 8) === 'https://';
    }

    function onBeforeSave() {
      vm.model._loading = true;
      return $http.get(coyoEndpoints.webPreviews.generate, {
        params: {url: vm.model.settings.url},
        autoHandleErrors: false
      }).then(function (response) {
        var data = response.data;
        var videoUrl = data.videoUrl;
        var ratio = (data.width && data.height) ? (data.height / data.width * 100) : 56.25;
        if (videoUrl && ratio) {
          vm.model.settings.backendData = {
            videoUrl: videoUrl,
            ratio: ratio
          };
        } else {
          // do not cache invalid data
          delete vm.model.settings.backendData;
        }
      }).catch(function () {
        // do not cache invalid data
        delete vm.model.settings.backendData;
        return $q.resolve();
      }).finally(function () {
        $timeout(function () {
          vm.model._loading = false;
        });
      });
    }

    (function _init() {
      $scope.saveCallbacks.onBeforeSave = onBeforeSave;
      checkUrl();
    })();
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.widgets.video')
      .component('coyoVideoWidget', videoWidget())
      .controller('VideoWidgetController', VideoWidgetController);

  /**
   * @ngdoc directive
   * @name coyo.widgets.video:coyoVideoWidget
   * @element OWN
   * @restrict E
   * @scope
   *
   * @description
   * Renders the widget to display a single video.
   *
   * @param {object} widget
   * The widget configuration
   */
  function videoWidget() {
    return {
      templateUrl: 'app/widgets/video/video-widget.html',
      bindings: {
        widget: '<'
      },
      controller: 'VideoWidgetController',
      controllerAs: '$ctrl'
    };
  }

  function VideoWidgetController($element, $sce, oembedVideoService) {
    var vm = this;
    vm.onInit = onInit;

    function onInit() {
      vm.fromBackend = angular.isDefined(vm.widget.settings.backendData);
      if (vm.fromBackend) {
        vm.videoUrl = vm.widget.settings.backendData.videoUrl;
        vm.ratio = vm.widget.settings.backendData.ratio;
      } else {
        var url = vm.widget.settings.url;
        var container = $element.parent().parent();
        var videoHtml = oembedVideoService.createByUrl(url, container[0]);
        if (videoHtml) {
          vm.videoHtml = $sce.trustAsHtml(videoHtml.prop('outerHTML'));
          vm.ratio = 56.25;
        } else {
          vm.error = true;
        }
      }
    }
  }

})(angular);

