(function (angular) {
  'use strict';

  angular
      .module('commons.resource')
      .factory('csrfService', csrfService);

  /**
   * @ngdoc service
   * @name commons.resource.csrfService
   *
   * @description
   * Service for getting and setting the CSRF token.
   *
   * @requires $log
   * @requires $http
   * @requires $q
   * @requires $rootScope
   * @requires commons.config.coyoEndpoints
   */
  function csrfService($log, $http, $q, $rootScope, coyoEndpoints) {
    $rootScope.$on('authService:logout:success', clearToken);
    $rootScope.$on('authService:logout:failed', clearToken);
    $rootScope.$on('backendUrlService:url:updated', clearToken);
    $rootScope.$on('backendUrlService:url:cleared', clearToken);

    return {
      isSet: isSet,
      getToken: getToken,
      clearToken: clearToken
    };

    /**
     * @ngdoc method
     * @name commons.resource.csrfService#isSet
     * @methodOf commons.resource.csrfService
     *
     * @description
     * Checks if a CSRF token is set.
     */
    function isSet() {
      return angular.isDefined($rootScope.csrfToken) && $rootScope.csrfToken !== null;
    }

    /**
     * @ngdoc method
     * @name commons.resource.csrfService#getToken
     * @methodOf commons.resource.csrfService
     *
     * @description
     * Resolves the token in a promise. Loads a new token if none is available.
     *
     * @return {object} Promise that resolves to a token.
     */
    function getToken() {
      if (isSet()) {
        return $q.resolve($rootScope.csrfToken);
      } else {
        return $http.get(coyoEndpoints.csrf).then(function (response) {
          var token = response.data.token;
          $log.debug('[csrfService] Obtained CSRF token:', token);
          $rootScope.csrfToken = token;
          $rootScope.$emit('csrfService:token:updated');
          return $q.resolve(token);
        }).catch(function (error) {
          $log.error('[csrfService] Failed to obtain CSRF token.');
          return $q.reject(error);
        });
      }
    }

    /**
     * @ngdoc method
     * @name commons.resource.csrfService#clearToken
     * @methodOf commons.resource.csrfService
     *
     * @description
     * Clears the current CSRF token.
     */
    function clearToken() {
      $log.debug('[csrfService] Clearing CSRF token');
      delete $rootScope.csrfToken;
      $rootScope.$emit('csrfService:token:cleared');
    }
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('commons.auth')
      .config(httpRequestInterceptor);

  /**
   * @ngdoc object
   * @name commons.auth.httpRequestInterceptor
   *
   * @description
   * HTTP traffic interceptor for intercepting every request and prepending the backend URL. Also enriches requests
   * with a CSRF token.
   *
   * @requires $provide
   * @requires $httpProvider
   * @requires $injector
   * @requires $log
   * @requires $q
   * @requires commons.resource.backendUrlService
   */
  function httpRequestInterceptor($provide, $httpProvider) {

    function _httpRequestInterceptor($injector, $log, $q, backendUrlService, $localStorage, utilService) {

      var canceller = $q.defer();

      var csrfService, authService, $state;

      return {
        request: request,
        requestError: requestError,
        response: response,
        responseError: responseError
      };

      /**
       * @ngdoc method
       * @name commons.auth.httpRequestInterceptor#request
       * @methodOf commons.auth.httpRequestInterceptor
       *
       * @description
       * Request handler.
       *
       * Called before every request made with angular.
       *
       * @param {object} config The $http configuration
       * @returns {object} The configuration
       */
      function request(config) {
        if (config.url.indexOf('/web') === 0) {
          if (!backendUrlService.isSet()) {
            $log.error('Aborting XHR backend request to "' + config.url + '" because no backend URL is defined.');

            config.timeout = canceller.promise;
            canceller.resolve();
          } else {
            // Prepend backend URL
            config.originalUrl = config.url;
            config.url = backendUrlService.getUrl() + config.url;

            if (!$localStorage.clientId) {
              $localStorage.clientId = utilService.uuid();
            }
            config.headers = config.headers || {};
            config.headers['X-Coyo-Client-ID'] = $localStorage.clientId;

            // Add CSRF token for non 'GET' requests
            if (config.method !== 'GET') {
              if (angular.isUndefined(csrfService)) {
                csrfService = $injector.get('csrfService');
              }

              var deferred = $q.defer();

              csrfService.getToken().then(function (token) {
                config.headers[$httpProvider.defaults.xsrfHeaderName] = token;
                deferred.resolve(config);
              }).catch(function (error) {
                var buttons = backendUrlService.isConfigurable() ? ['RETRY', 'CONFIGURE_BACKEND'] : ['RETRY'];
                var $translate = $injector.get('$translate');
                var errorService = $injector.get('errorService');
                $translate('ERRORS.CSRF_TOKEN.FAILED_OBTAINING').then(function (translation) {
                  errorService.showErrorPage(translation, null, buttons);
                });
                deferred.reject(error);
              });

              return deferred.promise;
            }
          }
        }
        return config;
      }

      /**
       * @ngdoc method
       * @name commons.auth.httpRequestInterceptor#requestError
       * @methodOf commons.auth.httpRequestInterceptor
       *
       * @description
       * Request error handler.
       *
       * @param {object} response The response
       * @returns {object} The rejected response
       */
      function requestError(response) {
        return $q.reject(response);
      }

      /**
       * @ngdoc method
       * @name commons.auth.httpRequestInterceptor#response
       * @methodOf commons.auth.httpRequestInterceptor
       *
       * @description
       * Response handler.
       *
       * @param {object} response The response
       * @returns {object} The response
       */
      function response(response) {
        return $q.resolve(response);
      }

      /**
       * @ngdoc method
       * @name commons.auth.httpRequestInterceptor#responseError
       * @methodOf commons.auth.httpRequestInterceptor
       *
       * @description
       * Response error handler.
       *
       * @param {object} response The rejected response
       * @returns {object} The rejected response
       */
      function responseError(response) {
        // Check for invalid authentication
        if (response.status === 401) {
          if (angular.isUndefined(authService)) {
            authService = $injector.get('authService');
          }
          if (angular.isUndefined($state)) {
            $state = $injector.get('$state');
          }

          if (authService.isAuthenticated()) {
            authService.clearSession().then(function () {
              $state.go('front.login');
            });
          }
        }

        return $q.reject(response);
      }
    }

    $provide.factory('httpRequestInterceptor', _httpRequestInterceptor);
    $httpProvider.interceptors.push('httpRequestInterceptor');
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('commons.resource')
      .factory('mobileService', mobileService);

  /**
   * @ngdoc service
   * @name commons.resource.mobileService
   *
   * @description
   * Service for getting mobile information.
   * Checks for the cordova wrapper and tries to identify iOS and Android devices via its device plugin.
   * The following information is available afterwards:
   * info: {
   *  mobile  : true/false,
   *  ios     : true/false,
   *  android : true/false
   * }
   *
   * @requires $window
   */
  function mobileService($window) {

    var info = {
      mobile: angular.isDefined($window.cordova),
      ios: false,
      android: false
    };

    _intialize();

    return {
      getInfo: getInfo,
      isMobile: isMobile,
      isIOS: isIOS,
      isAndroid: isAndroid
    };

    /**
     * @ngdoc method
     * @name commons.resource.mobileService#getInfo
     * @methodOf commons.resource.mobileService
     *
     * @description
     * Gets the complete mobile information.
     */
    function getInfo() {
      return info;
    }

    /**
     * @ngdoc method
     * @name commons.resource.mobileService#isMobile
     * @methodOf commons.resource.mobileService
     *
     * @description
     * Gets the information whether the device is a mobile device.
     */
    function isMobile() {
      return info.mobile;
    }

    /**
     * @ngdoc method
     * @name commons.resource.mobileService#isIOS
     * @methodOf commons.resource.mobileService
     *
     * @description
     * Gets the information whether the device is an iOS device.
     */
    function isIOS() {
      return info.ios;
    }

    /**
     * @ngdoc method
     * @name commons.resource.mobileService#isAndroid
     * @methodOf commons.resource.mobileService
     *
     * @description
     * Gets the information whether the device is an Android device.
     */
    function isAndroid() {
      return info.android;
    }

    /***************************************************************************/

    function _intialize() {
      if (info.mobile) {
        var devicePlatform = _.get($window, 'device.platform', '').toLowerCase();
        info.ios = devicePlatform.indexOf('ios') !== -1;
        info.android = devicePlatform.indexOf('android') !== -1;
      }
    }
  }

})(angular);

