(function (angular) {
  'use strict';

  angular
      .module('coyo.notifications')
      .config(registerToCurtain)
      .directive('oyocNotificationsDialog', notificationsDialog)
      .controller('NotificationsDialogController', NotificationsDialogController);

  function registerToCurtain(curtainServiceProvider) {
    curtainServiceProvider.register('notificationsDialog');
  }

  /**
   * @ngdoc directive
   * @name coyo.notifications.oyocNotificationsDialog:oyocNotificationsDialog
   * @element ANY
   *
   * @description
   * Displays the notifications dialog.
   */
  function notificationsDialog() {
    return {
      templateUrl: 'app/modules/notifications/components/notifications-dialog.html',
      replace: true,
      scope: {},
      controller: 'NotificationsDialogController',
      controllerAs: '$ctrl'
    };
  }

  function NotificationsDialogController($log, $scope, curtainService, notificationsMainService, coyoConfig,
                                         targetService, socketService, authService, browserNotificationsService, tabNotificationsService) {
    var vm = this;

    vm.show = false;
    vm.categories = ['DISCUSSION', 'ACTIVITY'];
    vm.data = {};
    vm.status = {};

    vm.toggle = toggle;
    vm.close = close;
    vm.switchCategory = switchCategory;
    vm.unsubscribe = unsubscribe;
    vm.subscribe = subscribe;
    vm.open = open;
    vm.getIcon = getIcon;
    vm.markAllClicked = markAllClicked;
    vm.loadMore = loadMore;

    // ----------------------------------------------------------------

    function toggle() {
      vm.show = !vm.show;
      if (vm.show) {
        vm.data = {};
        vm.switchCategory(vm.category || vm.categories[0]);
      }
    }

    function close() {
      vm.show = false;
    }

    function switchCategory(category) {
      vm.category = category;

      // mark all seen
      if (vm.status.unseen[category] > 0) {
        vm.status.unseen[category] = 0;
        notificationsMainService.markAllSeen(category);
      }
    }

    function unsubscribe(notification) {
      $log.error('Missing implementation for unsubscribe', notification);
    }

    function subscribe(notification) {
      $log.error('Missing implementation for subscribe', notification);
    }

    function open(notification) {
      targetService.go(notification.target);
      vm.show = false;
      if (!notification.clicked) {
        notificationsMainService.markClicked(notification).then(function () {
          notification.clicked = true;
          vm.status.unclicked[notification.category]--;
        });
      }
    }

    function getIcon(notificationTypeName) {
      var config = coyoConfig.notificationTypeIcons[notificationTypeName];
      return config ? config.icon : coyoConfig.notificationTypeIcons.default.icon;
    }

    function markAllClicked(category) {
      notificationsMainService.markAllClicked(category).then(function () {
        angular.forEach(vm.data[category].items, function (item) {
          item.clicked = true;
        });
      });
    }

    function loadMore(category) {
      if (!vm.data[category]) {
        vm.data[category] = {};
      }

      var data = vm.data[category];
      if (!data.loading && !_.get(data.currentPage, 'last', false)) {
        data.loading = true;

        var pageNumber = data.currentPage ? data.currentPage.number + 1 : 0;
        var pageSize = 10;
        notificationsMainService.getNotifications(category, pageNumber, pageSize).then(function (response) {
          data.currentPage = response;
          data.items = _.concat(data.items || [], response.content);
        }).finally(function () {
          data.loading = false;
        });
      }
    }

    // ----------------------------------------------------------------

    function _handleNewNotification(event) {
      $log.debug('Receiving event for new notification', event);

      if (event.content.status) {
        vm.status = event.content.status;
        tabNotificationsService.setCounter('notifications-ACTIVITY', event.content.status.unseen.ACTIVITY);
        tabNotificationsService.setCounter('notifications-DISCUSSION', event.content.status.unseen.DISCUSSION);
        $scope.$apply();
      }
      if (vm.show && event.content.notification) {
        vm.data[event.content.notification.category] = {};
        vm.loadMore(event.content.notification.category);
      }

      browserNotificationsService.notifyEvent(event);
    }

    function _handleStatusChanged(event) {
      $log.debug('Receiving event for notification status update', event);

      if (event.content.status) {
        vm.status = event.content.status;
        $scope.$apply();
      }
    }

    // ----------------------------------------------------------------

    (function _init() {
      authService.onGlobalPermissions('ACCESS_NOTIFICATIONS', function (permission) {
        vm.visible = permission;

        if (permission) {
          authService.getUser().then(function (user) {
            notificationsMainService.getStatus(user).then(function (status) {
              vm.status = status;
            });
          }).finally(function () {
            curtainService.loaded('notificationsDialog');
          });

          var unsubscribeRaisedFn = socketService.subscribe('/user/topic/notification', _handleNewNotification, 'raised');
          var unsubscribeStatusFn = socketService.subscribe('/user/topic/notification', _handleStatusChanged, 'statusChanged');
          $scope.$on('$destroy', unsubscribeRaisedFn);
          $scope.$on('$destroy', unsubscribeStatusFn);
        } else {
          curtainService.loaded('notificationsDialog');
        }
      });
    })();
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.notifications')
      .factory('notificationsMainService', notificationsMainService);

  /**
   * @ngdoc service
   * @name coyo.notifications.notificationsMainService
   *
   * @description
   * Notifications service to retrieve notification information.
   *
   * @requires $http
   * @requires commons.config.coyoEndpoints
   */
  function notificationsMainService($http, coyoEndpoints, tabNotificationsService) {

    return {
      getNotifications: getNotifications,
      getStatus: getStatus,
      markAllSeen: markAllSeen,
      markClicked: markClicked,
      markAllClicked: markAllClicked
    };

    /**
     * @ngdoc method
     * @name coyo.notifications.notificationsMainService#getNotifications
     * @methodOf coyo.notifications.notificationsMainService
     *
     * @description
     * Retrieves notifications.
     *
     * @param {string} category The category
     * @param {string} page The page
     * @param {string} pageSize The page size
     *
     * @returns {promise} An http promise
     */
    function getNotifications(category, page, pageSize) {
      return $http.get(coyoEndpoints.notification.notifications.replace('{category}', category)
        + '&_page=' + page + '&_pageSize=' + pageSize + '&_orderBy=created,DESC').then(function (response) {
        return response.data;
      });
    }

    /**
     * @ngdoc method
     * @name coyo.notifications.notificationsMainService#getStatus
     * @methodOf coyo.notifications.notificationsMainService
     *
     * @description
     * Retrieves the notification status (e.g. unseen count) for current user.
     *
     * @returns {promise} An http promise
     */
    function getStatus() {
      return $http.get(coyoEndpoints.notification.status).then(function (response) {
        tabNotificationsService.setCounter('notifications-DISCUSSION', response.data.unseen.DISCUSSION);
        tabNotificationsService.setCounter('notifications-ACTIVITY', response.data.unseen.ACTIVITY);
        return response.data;
      });
    }

    /**
     * @ngdoc method
     * @name coyo.notifications.notificationsMainService#markAllSeen
     * @methodOf coyo.notifications.notificationsMainService
     *
     * @description
     * Marks all notifications of given category for current user as seen (sets count = 0).
     *
     * @param {string} category The category
     *
     * @returns {promise} An http promise
     */
    function markAllSeen(category) {
      return $http.put(coyoEndpoints.notification.markAllSeen.replace('{category}', category))
          .then(function (response) {
            tabNotificationsService.resetCounter('notifications-' + category);
            return response.data;
          });
    }

    /**
     * @ngdoc method
     * @name coyo.notifications.notificationsMainService#markClicked
     * @methodOf coyo.notifications.notificationsMainService
     *
     * @description
     * Marks the given notification for current user as clicked.
     *
     * @param {object} notification The notification
     *
     * @returns {promise} An http promise
     */
    function markClicked(notification) {
      return $http.put(coyoEndpoints.notification.update.replace('{id}', notification.id), {clicked: true})
          .then(function (response) {
            return response.data;
          });
    }

    /**
     * @ngdoc method
     * @name coyo.notifications.notificationsMainService#markAllClicked
     * @methodOf coyo.notifications.notificationsMainService
     *
     * @description
     * Marks all notifications of given category for current user as clicked (sets count = 0).
     *
     * @param {string} category The category
     *
     * @returns {promise} An http promise
     */
    function markAllClicked(category) {
      return $http
          .put(coyoEndpoints.notification.markAllClicked.replace('{category}', category))
          .then(function (response) {
            return response.data;
          });
    }
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.pages')
      .controller('PagesCreateController', PagesCreateController);

  function PagesCreateController($q, $state, $timeout, currentUser, PageModel, coyoNotification,
                                 settings) {
    var vm = this;

    vm.languages = {};

    vm.back = back;
    vm.next = next;
    vm.translationIsRequired = translationIsRequired;
    vm.checkValidity = checkValidity;
    vm.$onInit = _init;

    function back() {
      vm.wizard.active = Math.max(0, vm.wizard.active - 1);
    }

    function next(form) {
      if (form && form.$valid) {
        if (vm.wizard.active < vm.wizard.states.length - 1) {
          return $q.resolve(vm.wizard.active++);
        } else {
          vm.page.categoryIds = _.map(vm.page.categories, 'id');

          // Add translations for default language to page model
          angular.forEach(vm.languages[vm.page.defaultLanguage].translations, function (value, key) {
            vm.page[key] = value;
          });

          // Add additional translations to page model
          angular.forEach(vm.languages, function (value, key) {
            if (value.active) {
              vm.page.translations[key] = value.translations ? value.translations : {};
            }
          });
          delete vm.page.translations[vm.page.defaultLanguage];
          if (vm.page.defaultLanguage === 'NONE') {
            vm.page.defaultLanguage = null;
          }

          // show help text if auto-subscribe + saving takes more than a few seconds
          var cancel;
          if (vm.page.autoSubscribeType !== 'NONE') {
            cancel = $timeout(function () {
              vm.showAutoSubscribeInfo = true;
            }, 5000);
          }

          return vm.page.create().then(function (page) {
            $state.go('main.page.show', {idOrSlug: page.slug});
            coyoNotification.success('MODULE.PAGES.CREATE.SUCCESS');
          }).finally(function () {
            if (cancel) {
              $timeout.cancel(cancel);
            }
            vm.showAutoSubscribeInfo = false;
          });
        }
      }
      return $q.reject();
    }

    function translationIsRequired(language) {
      if (vm.page.defaultLanguage === 'NONE') {
        return language === 'NONE';
      } else {
        return (vm.languages[language].translations && Object.keys(vm.languages[language].translations).length) ||
          (vm.currentLanguage === language);
      }
    }

    function checkValidity(key, valid) {
      vm.languages[key].valid = valid;
    }

    function _init() {
      vm.wizard = {
        states: ['MODULE.PAGES.CREATE.GENERAL', 'MODULE.PAGES.CREATE.ACCESS'],
        active: 0
      };
      if (currentUser.hasGlobalPermissions('AUTO_SUBSCRIBE_PAGE')) {
        vm.wizard.states.push('MODULE.PAGES.AUTO_SUBSCRIBE.LABEL');
      }

      vm.page = new PageModel({
        visibility: (currentUser.hasGlobalPermissions('CREATE_PUBLIC_PAGE') && settings.defaultVisibilityPages === 'PUBLIC') ? 'PUBLIC' : 'PRIVATE',
        adminIds: [currentUser.id],
        adminGroupIds: [],
        memberIds: [],
        memberGroupIds: [],
        translations: {},
        autoSubscribeType: 'NONE'
      });

      vm.page.defaultLanguage = 'NONE';
      vm.currentLanguage = 'NONE';
      vm.languages[vm.currentLanguage] = {
        active: true
      };
    }
  }
})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.pages')
      .controller('PagesListController', PagesListController);

  function PagesListController($rootScope, $scope, $q, $sessionStorage, $state, $stateParams, $timeout, PageModel,
                               PageCategoryModel, Pageable, currentUser, categories, appService, authService,
                               modalService, subscriptionsService, pagesConfig) {
    var vm = this;

    vm.filterStatus = {
      ALL: 'ALL',
      ADMIN: 'ADMIN',
      SUBSCRIBED: 'SUBSCRIBED'
    };
    vm.currentUser = currentUser;
    vm.treeOptions = _buildTreeOptions();
    vm.categories = categories;
    vm.subscriptions = [];
    vm.subscriptionsLoaded = false;
    vm.languages = {};

    vm.openPage = openPage;
    vm.openApp = openApp;
    vm.isAutoSubscribe = isAutoSubscribe;
    vm.search = search;
    vm.getTotalCount = getTotalCount;
    vm.isCategoryActive = isCategoryActive;
    vm.isCategoryEdited = isCategoryEdited;
    vm.toggleCategory = toggleCategory;
    vm.addCategory = addCategory;
    vm.editCategory = editCategory;
    vm.saveCategory = saveCategory;
    vm.onCategoryKeypress = onCategoryKeypress;
    vm.onCategoryClickOutside = onCategoryClickOutside;
    vm.deleteCategory = deleteCategory;
    vm.setFilterStatus = setFilterStatus;
    vm.isStatus = isStatus;
    vm.getStatusCount = getStatusCount;

    function openPage(page) {
      $state.go('main.page.show', {idOrSlug: page.slug}, {inherit: false});
    }

    function openApp(page, app) {
      appService.redirectToApp(page, app);
    }

    function _loadPages() {
      if (vm.loading) {
        return;
      }
      vm.subscriptionsLoaded = false;

      // write params to URL
      $state.transitionTo('main.page', _.omitBy({
        term: _.get(vm.query, 'term', ''),
        'categories[]': _.get(vm.query, 'filters.categories', [])
      }, _.isEmpty), {notify: false});

      // perform search
      $sessionStorage.pageQuery = vm.query;
      vm.loading = true;

      var term = vm.query.term;
      var sort = term ? ['_score,DESC', 'displayName.sort'] : 'displayName.sort';
      var pageable = new Pageable(0, pagesConfig.paging.pageSize, sort);
      var filters = vm.query.filters;
      var aggregations = {categories: 0};
      PageModel.searchWithFilter(term, pageable, filters, null, aggregations).then(function (page) {
        vm.currentPage = page;

        _.forEach(vm.categories, function (category) {
          var data = _.find(page.aggregations.categories, {key: category.id});
          category.count = _.get(data, 'count', 0);
        });
        var missingData = _.find(page.aggregations.categories, {key: 'N/A'});
        vm.missingCount = _.get(missingData, 'count', null);
        vm.admin = _.find(page.aggregations.status, {key: 'admin'});
        vm.subscribed = _.find(page.aggregations.status, {key: 'subscribed'});

        // fetch subscriptions to check for auto subscribe
        subscriptionsService.getSubscriptions(vm.currentUser.id, _.map(page.content, 'id')).then(function (subscriptions) {
          vm.subscriptions.push.apply(vm.subscriptions, subscriptions);
          vm.subscriptionsLoaded = true;
        });

      }).finally(function () {
        vm.loading = false;
      });
    }

    function isAutoSubscribe(page) {
      return _.some(vm.subscriptions, {targetId: page.id, autoSubscribe: true}) || undefined;
    }

    function search(searchTerm) {
      if (!vm.editingCategory) {
        vm.query.term = searchTerm;
        vm.query.filters = {categories: []};
        _loadPages();
      }
    }

    function setFilterStatus(status) {
      _setStatus(status);
      _loadPages();
    }

    function _setStatus(status) {
      $sessionStorage.pageStatusFilter = status;
      _.set(vm.query, 'filters.status', status);
    }

    function isStatus(status) {
      return status === _getQueryFilterStatus();
    }

    function _getQueryFilterStatus() {
      var status = _.get(vm.query, 'filters.status');
      return !_.isUndefined(status) ? status : vm.filterStatus.ALL;
    }

    function getStatusCount(status) {
      var adminCount = 0; // _.get(vm.admin, 'count', 0); // temporary disabled
      var subscribedCount = 0; // _.get(vm.subscribed, 'count', 0); // temporary disabled
      switch (status) {
        case vm.filterStatus.ALL:
          return getTotalCount();
        case vm.filterStatus.ADMIN:
          return adminCount;
        case vm.filterStatus.SUBSCRIBED:
          return subscribedCount;
      }
      return 0;
    }

    /* ===== Categories ===== */

    vm.editingCategory = null;
    vm.savingCategory = false;

    function getTotalCount() {
      return _.sumBy(vm.categories, 'count') + (vm.missingCount || 0);
    }

    function isCategoryActive(category) {
      var active = _.get(vm.query, 'filters.categories', []);
      return category ? active.indexOf(category.id) !== -1 : active.length === 0;
    }

    function isCategoryEdited(category) {
      return vm.editingCategory && (vm.editingCategory.isNew() ? category.isNew() : vm.editingCategory.id === category.id);
    }

    function toggleCategory(category) {
      if (!vm.editingCategory) {
        var status = _.get(vm.query, 'filters.status');
        vm.query.filters = {
          categories: category ? [category.id] : [],
          status: status
        };
        _loadPages();
      }
    }

    function addCategory($event) {
      if (vm.editingCategory || vm.savingCategory) {
        return;
      }
      vm.editingCategory = new PageCategoryModel();
      vm.categories.push(vm.editingCategory);

      $timeout(function () {
        var target = angular.element($event.currentTarget);
        target.parent().find('input').select();
      });
    }

    function editCategory(category, $event) {
      $event.preventDefault();
      $event.stopImmediatePropagation();

      if (vm.editingCategory || vm.savingCategory) {
        return;
      }
      vm.editingCategory = angular.copy(category);

      $timeout(function () {
        var target = angular.element($event.currentTarget);
        target.parent().parent().find('input').select();
      });
    }

    function saveCategory(category, $event) {
      $event.preventDefault();
      $event.stopImmediatePropagation();

      if (!category.name || vm.savingCategory) {
        return $q.reject();
      }

      vm.savingCategory = true;
      return category.save().then(function (result) {
        vm.categories.splice(_.findIndex(vm.categories, {id: category.id}), 1, result);
      }).finally(function () {
        vm.savingCategory = false;
        vm.editingCategory = null;
      });
    }

    function onCategoryKeypress(category, $event) {
      if ($event.keyCode === 13) {
        saveCategory(category, $event);
      }
    }

    function onCategoryClickOutside(category) {
      if (!vm.editingCategory || !category) {
        return; // not in edit mode
      }

      if (category.isNew()) {
        vm.categories.pop();
      } else {
        category.name = vm.editingCategory.name;
      }
      vm.editingCategory = null;
    }

    function deleteCategory(category, $event) {
      $event.preventDefault();
      $event.stopImmediatePropagation();

      if (vm.editingCategory || vm.savingCategory) {
        return $q.reject();
      }

      return modalService.confirm({
        title: 'PAGE.CATEGORY.DELETE.MODAL.TITLE',
        text: 'PAGE.CATEGORY.DELETE.MODAL.TEXT',
        translationContext: {category: category.name},
        close: {icon: 'delete', title: 'DELETE', style: 'btn-danger'},
        dismiss: {title: 'CANCEL'}
      }).result.then(function () {
        vm.savingCategory = true;
        return category.delete().then(function (result) {
          _.remove(vm.categories, {id: category.id});
          if (category.id === _.get(vm.query, 'filters.categories[0]', null)) {
            vm.query.filters = {categories: []};
            _loadPages();
          }
          return result;
        });
      }).finally(function () {
        vm.savingCategory = false;
      });
    }

    /* ==================== */

    function _buildTreeOptions() {
      return {
        dropped: function (event) {
          // persist new sort order
          if (event.source.index !== event.dest.index) {
            PageCategoryModel.order(_.map(vm.categories, 'id'));
          }
        }
      };
    }

    (function _init() {
      // extract search from URL / storage
      vm.query = $sessionStorage.pageQuery || {};
      if ($stateParams.term || $stateParams['categories[]']) {
        angular.extend(vm.query, {
          term: $stateParams.term,
          filters: {categories: $stateParams['categories[]']}
        });
      }

      // register ESC callback
      var unsubscribeEscFn = $rootScope.$on('keyup:esc', function () {
        if (vm.editingCategory) {
          var category = _.find(vm.categories, isCategoryEdited);
          $timeout(function () {
            onCategoryClickOutside(category);
          });
        }
      });

      // register permission callback
      authService.onGlobalPermissions('MANAGE_PAGE_CATEGORIES', function (canManage) {
        vm.canManagePageCategories = canManage;
      });

      // unregister callbacks on $scope death
      $scope.$on('$destroy', function () {
        unsubscribeEscFn();
      });

      $timeout(function () {
        _loadPages();
      });
    })();
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.pages')
      .controller('PageSettingsController', PageSettingsController);

  /**
   * Controller for the page edit view
   */
  function PageSettingsController($state, modalService, coyoNotification, page, members, $timeout) {
    var vm = this;

    vm.save = savePage;
    vm.delete = deletePage;
    vm.translationIsRequired = translationIsRequired;
    vm.checkValidity = checkValidity;
    vm.$onInit = onInit;

    function savePage() {
      vm.page.categoryIds = _.map(vm.page.categories, 'id');

      // Add translations for default language to page model
      vm.page.defaultLanguage = page.defaultLanguage !== null ? page.defaultLanguage : 'NONE';
      angular.forEach(vm.languages[vm.page.defaultLanguage].translations, function (value, key) {
        vm.page[key] = value;
      });

      // Add additional translations to page model
      vm.page.translations = {};
      angular.forEach(vm.languages, function (value, key) {
        if (value.active) {
          vm.page.translations[key] = value.translations ? value.translations : {};
        }
      });
      delete vm.page.translations[vm.page.defaultLanguage];
      if (vm.page.defaultLanguage === 'NONE') {
        vm.page.defaultLanguage = null;
      }

      // show help text if auto-subscribe + saving takes more than a few seconds
      var cancel;
      if (vm.page.autoSubscribeType !== 'NONE' || vm.page.autoSubscribeType !== vm.oldAutoSubscribeType) {
        cancel = $timeout(function () {
          vm.showAutoSubscribeInfo = true;
        }, 5000);
      }

      return vm.page.update().then(function (page) {
        coyoNotification.success('MODULE.PAGES.EDIT.SUCCESS');

        if ($state.is('main.page.show.settings')) {
          $state.go('main.page.show', {idOrSlug: page.slug}, {reload: 'main.page.show'});
        }
      }).finally(function () {
        if (cancel) {
          $timeout.cancel(cancel);
        }

        vm.showAutoSubscribeInfo = false;
      });
    }

    function deletePage() {
      modalService.confirm({title: 'PAGE.DELETE', text: 'PAGE.DELETE.CONFIRM'}).result.then(function () {
        vm.page.delete().then(function () {
          $state.go('main.page');
          coyoNotification.success('PAGE.DELETE.SUCCESS');
        });
      });
    }

    function translationIsRequired(language) {
      if (vm.page.defaultLanguage === 'NONE') {
        return language === 'NONE';
      } else {
        return (vm.languages[language].translations && Object.keys(vm.languages[language].translations).length) ||
               (vm.currentLanguage === language);
      }
    }

    function checkValidity(key, valid) {
      vm.languages[key].valid = valid;
    }

    function onInit() {
      vm.page = angular.extend(page, members);
      vm.baseUrl = $state.href('main.page', {}) + '/';
      vm.oldName = page.displayName;
      vm.oldSlug = page.slug;
      vm.oldAutoSubscribeType = page.autoSubscribeType;

      vm.currentLanguage = page.defaultLanguage !== null ? page.defaultLanguage : 'NONE';
      vm.page.defaultLanguage = page.defaultLanguage !== null ? page.defaultLanguage : 'NONE';
      vm.languages = {};
      vm.languages[vm.currentLanguage] = {
        'active': true,
        'translations': {
          'name': page.name,
          'description': page.description
        }
      };
      _.forEach(page.translations, function (value, key) {
        vm.languages[key] = {
          'active': true,
          'translations': value
        };
      });

      _.forEach(vm.page.categories, function (category) {
        category.displayName = category.name;
      });
    }
  }
})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.pages')
      .controller('PageController', PageController);

  /**
   * Controller for the pages details/show view
   */
  function PageController($scope, $rootScope, page, apps, senderService, appService, backendUrlService, SenderModel) {
    var vm = this;
    var senderModel = new SenderModel({id: page.id});

    vm.page = page;
    vm.apps = apps;
    vm.appsStatus = {busy: false};
    vm.backendUrl = backendUrlService.getUrl();
    vm.displayMobileNavigation = vm.apps.length || (vm.page._permissions.manage) || vm.page._permissions.manageApps;

    vm.changeCover = senderService.changeCover({title: 'MODULE.PAGES.MODALS.CHANGE_BG_IMAGE.TITLE'});
    vm.changeAvatar = function (sender) {
      senderService.changeAvatar({title: 'MODULE.PAGES.MODALS.CHANGE_AVATAR.TITLE'})(sender).then(function () {
        $scope.$broadcast('pageAvatar:changed');
      });
    };
    vm.addApp = addApp;
    vm.addGroup = addGroup;

    function addApp(sender, apps) {
      if (!vm.appsStatus.busy) {
        appService.addApp(sender, apps);
      }
    }

    function addGroup() {
      if (vm.appsStatus.busy) {
        return;
      }
      vm.appsStatus.busy = true;

      var appNavigation = vm.page.appNavigation ? angular.copy(vm.page.appNavigation) : [];
      appNavigation.push({name: '', apps: []});
      senderModel.updateNavigation(appNavigation).then(function (result) {
        vm.page.appNavigation = result;
      }).finally(function () {
        vm.appsStatus.busy = false;
      });
    }

    function _getCurrentApp(appIdOrSlug) {
      return _.find(vm.apps, function (app) {
        return appIdOrSlug === app.slug || appIdOrSlug === app.id;
      });
    }

    (function _init() {
      vm.currentApp = _getCurrentApp(appService.getCurrentAppIdOrSlug());
      if (!vm.currentApp) {
        appService.redirectToSender(vm.page, vm.apps);
      }

      // Subscribe to events (and unsubscribe if not needed anymore)
      var unsubscribeAppChanged = appService.onAppChanged(function (appId) {
        vm.currentApp = _getCurrentApp(appId);
      });
      var unsubscribeAppUpdated = $rootScope.$on('app:updated', function (event, app) {
        appService.updateApp(app, vm.apps);
        // redirect to app if slug of current app changed
        if (vm.currentApp && vm.currentApp.id === app.id && vm.currentApp.slug !== app.slug) {
          appService.redirectToApp(vm.page, app, false);
        }
      });
      var unsubscribeAppDeleted = $rootScope.$on('app:deleted', function (event, appId) {
        appService.deleteApp(appId, vm.apps);
        if (vm.currentApp && vm.currentApp.id === appId) {
          appService.redirectToSender(vm.page, vm.apps);
        }
      });

      $scope.$on('$destroy', unsubscribeAppChanged);
      $scope.$on('$destroy', unsubscribeAppUpdated);
      $scope.$on('$destroy', unsubscribeAppDeleted);
    })();
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.profile')
      .factory('userService', userService);

  /**
   * @ngdoc service
   * @name coyo.profile.userService
   *
   * @description
   * User service to retrieve user information.
   *
   * @requires $http
   * @requires $localStorage
   * @requires commons.i18n.i18nService
   * @requires commons.config.coyoEndpoints
   * @requires commons.sockets.socketService
   */
  // TODO: Migrate to UserModel class/instance methods
  function userService($q, $http, $timeout, $localStorage, i18nService, coyoEndpoints, socketService, UserProfileGroupModel) {

    var pendingPresenceStatusRequests = {};

    return {
      getUserInfo: getUserInfo,
      setUserName: setUserName,
      setUserLanguage: setUserLanguage,
      setUserTimeZone: setUserTimeZone,
      changeEmail: changeEmail,
      activateEmail: activateEmail,
      setUserPassword: setUserPassword,
      setProfileFields: setProfileFields,
      getPresenceStatus: getPresenceStatus,
      getUserOnlineCount: getUserOnlineCount
    };

    /**
     * @ngdoc method
     * @name coyo.profile.userService#getUserInfo
     * @methodOf coyo.profile.userService
     *
     * @description
     * Retrieves information of a user.
     *
     * @param {object} user The user
     *
     * @returns {promise} An http promise
     */
    function getUserInfo(user) {
      var url = coyoEndpoints.user.user.replace('{id}', user.id);
      return $http.get(url);
    }

    /**
     * @ngdoc method
     * @name coyo.profile.userService#getUserOnlineCount
     * @methodOf coyo.profile.userService
     *
     * @description
     * Returns the number of currently online Users (not presenceStatus, but connected)
     *
     * @returns {promise} An http promise
     */
    function getUserOnlineCount() {
      var url = coyoEndpoints.user.getUserOnlineCount;
      return $http.get(url).then(function (response) {
        return response.data;
      });
    }

    /**
     * @ngdoc method
     * @name coyo.profile.userService#setUserName
     * @methodOf coyo.profile.userService
     *
     * @description
     * Saves the first and last name of the user.
     *
     * @param {object} user The user to set the first and last name for
     * @param {object} firstName The (new) first name
     * @param {object} lastName The (new) last name
     *
     * @returns {promise} An http promise
     */
    function setUserName(user, firstName, lastName) {
      var url = coyoEndpoints.user.setUserName.replace('{id}', user.id);
      return $http.put(url, {
        firstName: firstName,
        lastName: lastName
      }).then(function (response) {
        return response.data;
      });
    }

    /**
     * @ngdoc method
     * @name coyo.profile.userService#setUserLanguage
     * @methodOf coyo.profile.userService
     *
     * @description
     * Saves the preferred language in the backend and sets it for a user globally for $translate.
     *
     * @param {object} user The user to set the language for
     * @param {object} language The language to set
     *
     * @returns {promise} An http promise
     */
    function setUserLanguage(user, language) {
      var url = coyoEndpoints.user.setLanguage.replace('{id}', user.id);
      return $http.put(url, {
        language: language.toUpperCase()
      }).then(function (response) {
        i18nService.setInterfaceLanguage(language);
        $localStorage.userLanguage = language;
        return response.data;
      });
    }

    /**
     * @ngdoc method
     * @name coyo.profile.userService#setUserTimeZone
     * @methodOf coyo.profile.userService
     *
     * @description
     * Sets the time zone of a user in the backend.
     *
     * @param {object} user The user to set the time zone for
     * @param {object} timeZoneId The id of the time zone to set
     *
     * @returns {promise} An http promise
     */
    function setUserTimeZone(user, timeZoneId) {
      var url = coyoEndpoints.user.setTimeZone.replace('{id}', user.id);
      return $http.put(url, {
        timezone: timeZoneId
      });
    }

    /**
     * @ngdoc method
     * @name coyo.profile.userService#changeEmail
     * @methodOf coyo.profile.userService
     *
     * @description
     * Requests the change of a user's email address.
     *
     * @param {object} user The user to change the email for
     * @param {string} newEmail The new email address

     * @returns {promise} An http promise
     */
    function changeEmail(user, newEmail) {
      var url = coyoEndpoints.user.email.change.replace('{id}', user.id);
      return $http.post(url, {
        email: newEmail
      });
    }

    /**
     * @ngdoc method
     * @name coyo.profile.userService#activateEmail
     * @methodOf coyo.profile.userService
     *
     * @description
     * Requests the email address activation.
     *
     * @param {object} user The user to activate the email for
     * @param {string} token The token for the activation

     * @returns {promise} An http promise
     */
    function activateEmail(user, token) {
      var url = coyoEndpoints.user.email.activate.replace('{id}', user.id);
      return $http.put(url, {
        token: token
      });
    }

    /**
     * @ngdoc method
     * @name coyo.profile.userService#setUserPassword
     * @methodOf coyo.profile.userService
     *
     * @description
     * Sets the password of a user.
     *
     * @param {object} user The user to set the password for
     * @param {object} oldPassword The old password
     * @param {object} newPassword The new password
     *
     * @returns {promise} An http promise
     */
    function setUserPassword(user, oldPassword, newPassword) {
      var url = coyoEndpoints.user.changePassword.replace('{id}', user.id);
      return $http.post(url, {
        oldPassword: oldPassword,
        newPassword: newPassword
      });
    }

    /**
     * @ngdoc method
     * @name coyo.profile.userService#setProfileFields
     * @methodOf coyo.profile.userService
     *
     * @description
     * Sets the profile fields of a user.
     *
     * @param {object} user The user to set the profile fields for
     * @param {object} group The profile group
     * @param {object} fields The profile fields
     *
     * @returns {promise} An http promise
     */
    function setProfileFields(user, group, fields) {
      return UserProfileGroupModel.get().then(function (config) {
        var groupConfig = _.find(config, {id: group.id});
        if (groupConfig) {
          var validFields = _.pickBy(fields, function (value, key) {
            var field = _.find(groupConfig.fields, {name: key});
            return field && !field.immutable;
          });

          if (_.keys(validFields).length > 0) {
            return $http({
              method: 'POST',
              url: coyoEndpoints.user.profile.fields.replace('{id}', user.id),
              data: validFields
            });
          }
        }

        return $q.resolve();
      });
    }

    /**
     * @ngdoc method
     * @name coyo.profile.userService#getPresenceStatus
     * @methodOf coyo.profile.userService
     *
     * @description
     * Retrieves the presence status of a user. Optionally, you may provide a $scope, which will result in an ongoing
     * subscription to changes of the presence status and additional calls to the provided callback method. We need
     * a $scope so that we can terminate the subscription when the $scope is destroyed.
     * Internally, the requests are collected for a digest cycle and then a bulk request is sent to the backend.
     *
     * @param {string} user The user to get the presence status for
     * @param {function} callback Callback that will be called with the presence status as first argument
     * @param {object} $scope Optional $scope to future to additional presence status changes
     */
    function getPresenceStatus(user, callback, $scope) {
      if (user.active) {
        // get initial status
        var callbackList = (pendingPresenceStatusRequests[user.id] || []);
        callbackList.push(callback);
        pendingPresenceStatusRequests[user.id] = callbackList;
        $timeout(function () {
          if (_.keys(pendingPresenceStatusRequests).length > 0) {
            var ids = _.keys(pendingPresenceStatusRequests).join(',');
            var requestsInProgress = pendingPresenceStatusRequests;
            pendingPresenceStatusRequests = {};
            $http.get(coyoEndpoints.user.presenceStatusList + '?userIds=' + ids).then(function (response) {
              _.forIn(response.data, function (status, id) {
                _.forIn(requestsInProgress[id], function (callback) {
                  callback(status);
                });
              });
            });
          }
        });

        // subscribe to further status changes
        if ($scope) {
          $scope.$on('$destroy',
              socketService.subscribe('/topic/user.' + user.id + '.presenceStatusChanged', function (event) {
                $scope.$apply(function () {
                  callback(event.content);
                });
              })
          );
        }
      }
    }
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.profile')
      .controller('ProfileMainController', ProfileMainController);

  /**
   * Controller for the users profile
   */
  function ProfileMainController($rootScope, $scope, authService, messagingService, coyoNotification, senderService, userService, UserModel,
                                 currentUser, user, profileFieldGroups, linkPattern, emailPattern, phonePattern) {
    var vm = this;
    vm.currentUser = currentUser;
    vm.linkPattern = linkPattern;
    vm.emailPattern = emailPattern;
    vm.phonePattern = phonePattern;

    vm.onSubmit = onSubmit;
    vm.onCancel = onCancel;
    vm.changeAvatar = function (sender) {
      senderService.changeAvatar({title: 'MODULE.PROFILE.MODALS.CHANGE_AVATAR.TITLE', areaType: 'circle'})(sender).then(function () {
        if (sender.id === currentUser.id) {
          authService.getUser(true);
        }
      });
    };

    vm.changeCover = senderService.changeCover({title: 'MODULE.PROFILE.MODALS.CHANGE_BG_IMAGE.TITLE'});
    vm.startConversation = messagingService.start;

    // ---------------------- PUBLIC METHODS ------------------------

    function onSubmit(group) {
      group.saving = true;

      // just send the fields of the changed group
      var fields = {};
      _.forEach(group.fields, function (field) {
        var fieldName = field.name;
        fields[fieldName] = vm.user.properties[fieldName];
      });

      return userService.setProfileFields(vm.user, group, fields).then(function () {
        $scope.$broadcast('coyo.base:coyoDelayForm-refreshModel', vm.user.properties);
        group.editMode = false;
      }).catch(function () {
        coyoNotification.error('MODULE.PROFILE.NOTIFICATIONS.SAVE_PROFILE_FIELDS_FAILED');
      }).finally(function () {
        group.saving = false;
      });
    }

    function onCancel() {
      return UserModel.getWithPermissions({id: vm.user.id}, {}, ['manage']).then(_initUserData);
    }

    // ---------------------------- INIT ----------------------------

    function _initFieldData(profileFieldGroups) {
      vm.profileGroups = [];

      // prepare the groups and its fields
      _.forEach(profileFieldGroups, function (group) {
        group.editMode = false;
        group.saving = false;
        vm.profileGroups.push(group);
      });

      // sort profile groups
      vm.profileGroups.sort(function (group1, group2) {
        if ((!group1.hasOwnProperty('sortOrder') || !group2.hasOwnProperty('sortOrder')) ||
            group1.sortOrder === group2.sortOrder) {
          return 0;
        }
        return group1.sortOrder < group2.sortOrder ? -1 : 1;
      });

      // group the groups for the template
      vm.groupList = [];
      var innerGroupList = [];
      _.forEach(vm.profileGroups, function (group, g) {
        innerGroupList.push(group);
        if ((g % 2 !== 0) || (g === vm.profileGroups.length - 1)) {
          vm.groupList.push(innerGroupList);
          innerGroupList = [];
        }
      });
    }

    function _initUserData(user) {
      vm.user = user;
      vm.user.onlineStatus = true;
      if (!vm.user.properties) {
        vm.user.properties = {};
      }

      // prepare the groups and its fields
      _.forEach(vm.profileGroups, function (group) {
        _.forEach(group.fields, function (field) {
          vm.user.properties[field.name] = _.getNullUndefined(vm.user.properties, field.name, '');
        });
      });

      return vm.user;
    }

    (function _init() {
      _initFieldData(profileFieldGroups);
      _initUserData(user);

      // refresh current user on event
      var unsubscribe = $rootScope.$on('currentUser:updated', function (event, currentUser) {
        if (vm.user.id === currentUser.id) {
          _initUserData(currentUser);
        }
      });
      $scope.$on('$destroy', unsubscribe);
    })();
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.reports')
      .controller('ReportsListController', ReportsListController);

  function ReportsListController($scope, ReportModel, Pageable, targetService) {
    var vm = this;

    vm.loadMore = loadMore;
    vm.open = open;
    vm.resolve = resolve;

    function loadMore(reset) {
      if (vm.loading) {
        return;
      }

      if (!vm.reports || reset) {
        vm.reports = [];
      }
      if (reset) {
        delete vm.currentPage;
      }

      if (!vm.currentPage || !vm.currentPage.last) {
        vm.loading = true;

        var pageable = new Pageable((vm.currentPage ? vm.currentPage.number + 1 : 0), 20);

        ReportModel.pagedQuery(pageable).then(function (page) {
          vm.currentPage = page;

          // permission checks
          _.forEach(page.content, function (report) {
            targetService.onCanLinkTo(report.target, function (canLink) {
              report.target.canLinkTo = canLink;
            });
          });

          vm.reports.push.apply(vm.reports, page.content);
        }).finally(function () {
          vm.loading = false;
        });
      }
    }

    function open(report) {
      if (report.canLinkTo) {
        targetService.go(report.target);
      }
    }

    function resolve(report) {
      if (vm.loading || vm.loadingResolve) {
        return;
      }

      vm.loadingResolve = true;
      report.delete().then(function () {
        vm.loadMore(true);
      }).finally(function () {
        vm.loadingResolve = false;
      });
    }
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.search')
      .directive('oyocSearchBar', searchBar)
      .controller('SearchBarController', SearchBarController);

  /**
   * @ngdoc directive
   * @name coyo.search.oyocSearchBar:oyocSearchBar
   * @restrict 'E'
   * @element OWN
   * @scope
   *
   * @description
   * Displays and handles the search box in the main nav bar.
   *
   * While the search box is always visible in mobile view, it can be expanded and hidden in desktop view.
   *
   * @requires $rootScope
   * @requires $element
   * @requires $state
   */
  function searchBar() {
    return {
      restrict: 'E',
      replace: true,
      templateUrl: 'app/modules/search/components/search-bar.html',
      controller: 'SearchBarController',
      controllerAs: 'searchBar'
    };
  }

  function SearchBarController($rootScope, $element, $state, $transitions, $scope) {
    var vm = this;
    vm.$onInit = onInit;

    function setIsSearchResult() {
      if ($state.is('main.search')) {
        vm.isSearchResult = true;
      }
    }

    function toggle() {
      $rootScope.search = angular.extend($state.is('main.search') ? $rootScope.search : {}, {
        visible: !$rootScope.search.visible
      });
      if ($rootScope.search.visible) {
        $element.find('input').focus();
      }
    }

    function search(force) {
      if (!$rootScope.search.visible) {
        toggle();
      }
      if (force || $rootScope.search.term) {
        $state.transitionTo('main.search', angular.extend({
          term: $rootScope.search.term
        }, _.mapKeys($rootScope.search.filter, function (value, key) {
          return key + '[]';
        })));
      }
    }

    function onInit() {
      vm.toggle = toggle;
      vm.search = search;
      vm.isSearchResult = false;

      if (!$rootScope.search) {
        $rootScope.search = {};
      }

      var deregisterHook = $transitions.onSuccess({}, setIsSearchResult);
      $scope.$on('$destroy', deregisterHook);
      setIsSearchResult();
    }
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.search')
      .controller('SearchController', SearchController);

  /**
   * Controller for the full search
   */
  function SearchController($rootScope, $scope, $http, $httpParamSerializer, $q, $state, $stateParams, $translate,
                            coyoConfig, coyoEndpoints, targetService, Page) {
    var vm = this;

    vm.isEmpty = isEmpty;
    vm.getType = getType;
    vm.openResult = openResult;
    vm.openSender = openSender;
    vm.toggleFilter = toggleFilter;
    vm.clearFilter = clearFilter;
    vm.search = search;

    function isEmpty(object) {
      return _.size(object) === 0;
    }

    function getType(typeName) {
      return coyoConfig.entityTypes[typeName] || coyoConfig.entityTypes.default;
    }

    function openResult(result) {
      if (result.canLinkToResult) {
        targetService.go(result.target);
      }
    }

    function openSender(result) {
      if (result.sender && result.canLinkToSender) {
        targetService.go(result.sender.target);
      }
    }

    function toggleFilter(key, value, singleSelect) {
      var filter = $rootScope.search.filter[key];
      if (singleSelect || !filter) {
        $rootScope.search.filter[key] = [value];
      } else if (filter.indexOf(value) < 0) {
        $rootScope.search.filter[key] = _.concat(filter, value);
      } else if (filter.length > 1) {
        $rootScope.search.filter[key] = _.without(filter, value);
      } else {
        delete $rootScope.search.filter[key];
      }
      return search(0, true);
    }

    function clearFilter(key) {
      delete $rootScope.search.filter[key];
      return search(0, true);
    }

    function search(page, writeToUrl) {
      if (vm.loading || !$rootScope.search.term) {
        return $q.reject();
      }
      vm.loading = true;

      // write params to URL
      if (writeToUrl) {
        $state.transitionTo('main.search', angular.extend({
          term: $rootScope.search.term
        }, _.mapKeys($rootScope.search.filter, function (value, key) {
          return key + '[]';
        })), {notify: false});
      }

      // execute search
      return $http({
        method: 'GET',
        url: coyoEndpoints.search.query.replace('{page}', page).replace('{pageSize}', 20),
        params: {
          term: $rootScope.search.term,
          filters: $httpParamSerializer($rootScope.search.filter),
          aggregations: $httpParamSerializer({type: 10, modified: '', sender: 5})
        }
      }).then(function (result) {
        vm.currentPage = new Page(result.data, {
          _page: page,
          _pageSize: 20
        });

        // permission checks
        _.forEach(result.data.content, function (result) {
          targetService.onCanLinkTo(result.target, function (canLink) {
            result.canLinkToResult = canLink;
          });
          if (result.sender) {
            targetService.onCanLinkTo(result.sender.target, function (canLink) {
              result.canLinkToSender = canLink;
            });
          }
        });

        vm.typeAggregationTotal = _.sumBy(vm.currentPage.aggregations.type, 'count');

        // set sender labels
        _.forEach(vm.currentPage.aggregations.sender, function (aggregation) {
          aggregation.label = aggregation.data.displayName;
        });
        // set modified labels
        _.forEach(vm.currentPage.aggregations.modified, function (aggregation) {
          aggregation.label = aggregation.data.total;
        });
        // set type labels
        _.forEach(vm.currentPage.aggregations.type, function (aggregation) {
          aggregation.label = $translate.instant(getType(aggregation.key).label);
        });

        vm.loading = false;
      });
    }

    // ================================

    (function _init() {
      // extract search params from URL
      $rootScope.search = angular.extend($rootScope.search || {}, {
        visible: true,
        term: $stateParams.term,
        filter: _.chain($stateParams).pickBy(function (value, key) {
          return _.endsWith(key, '[]') && !!value;
        }).mapKeys(function (value, key) {
          return key.slice(0, -2);
        }).value()
      });

      // execute first search request
      search(0, false);
    })();
  }

})(angular);

