(function (angular) {
  'use strict';

  angular
      .module('commons.layout')
      .directive('oyocMainSidebar', mainSidebar)
      .controller('MainSidebarController', MainSidebarController);

  /**
   * @ngdoc directive
   * @name commons.layout.oyocMainSidebar:oyocMainSidebar
   * @element OWN
   * @restrict E
   * @scope
   *
   * @description
   * Displays the sidebar navigation menu (only on mobile devices). The sidebar is registered to the sidebarService by
   * registering with its API.
   *
   * @requires $rootScope
   * @requires $localStorage
   * @requires $state
   * @requires commons.resource.Pageable
   * @requires coyo.domain.UserSubscriptionModel
   * @requires coyo.domain.UserNotificationSettingModel
   * @requires coyo.admin.adminStates
   * @requires commons.config.coyoConfig
   * @requires commons.auth.authService
   * @requires commons.ui.curtainService
   * @requires commons.ui.sidebarService
   * @requires commons.target.targetService
   * @requires commons.terms.termsService
   * @requires commons.tour.tourService
   * @requires commons.ui.userGuideService
   */
  function mainSidebar() {
    return {
      restrict: 'E',
      templateUrl: 'app/commons/layout/components/main-sidebar/main-sidebar.html',
      replace: true,
      scope: {},
      bindToController: {
        landingPages: '<'
      },
      controller: 'MainSidebarController',
      controllerAs: '$ctrl'
    };
  }

  function MainSidebarController($rootScope, $localStorage, $state, Pageable, UserSubscriptionModel,
                                 UserNotificationSettingModel, adminStates, coyoConfig, authService,
                                 sidebarService, targetService, termsService, tourService, userGuideService,
                                 aboutCoyoService) {
    var vm = this;
    vm.navState = {};
    vm.showSidebar = false;
    vm.allAdminPermissions = _.map(adminStates, 'globalPermission').join(',');

    vm.open = open;
    vm.close = close;
    vm.help = help;
    vm.about = about;
    vm.restartTour = restartTour;
    vm.hasTourSteps = hasTourSteps;
    vm.logout = authService.logout;
    vm.showTerms = termsService.showModal;
    vm.loadMoreSubscriptions = loadMoreSubscriptions;
    vm.openSender = openSender;
    vm.navCollapse = navCollapse;

    // ----------------------------------------------------------------

    function open() {
      vm.showSidebar = true;
      $rootScope.showBackdrop = true;
    }

    function close() {
      vm.showSidebar = false;
      $rootScope.showBackdrop = false;
    }

    function help() {
      close();

      if ($state.current && $state.current.data && $state.current.data.guide) {
        userGuideService.open($state.current.data.guide);
      } else {
        userGuideService.notFound();
      }
    }

    function about() {
      close();
      aboutCoyoService.open();
    }

    function hasTourSteps() {
      return tourService.getTopics().length > 0;
    }

    function restartTour() {
      close();
      tourService.restart('mobile');
    }

    function openSender(sender) {
      targetService.go(sender.target);
    }

    function navCollapse(section) {
      vm.navState[section] = !vm.navState[section];
      $localStorage.navState[section] = !$localStorage.navState[section];
    }

    // -------------------------
    // My Subscriptions
    // -------------------------

    var pageSize = 5;
    vm.mySubscriptions = {
      page: {
        icon: coyoConfig.entityTypes.page.icon,
        items: [],
        loading: false,
        currentPage: null
      },
      workspace: {
        icon: coyoConfig.entityTypes.workspace.icon,
        items: [],
        loading: false,
        currentPage: null
      }
    };

    function loadMoreSubscriptions(type) {
      var data = vm.mySubscriptions[type];
      if (data.loading) {
        return;
      }

      if (!data.currentPage || !data.currentPage.last) {
        data.loading = true;
        var pageable = new Pageable((data.currentPage ? data.currentPage.number + 1 : 0), pageSize, 'displayName.sort');
        UserSubscriptionModel.searchWithFilter(vm.user.id, '', pageable, {type: [type]}, [], {}).then(function (page) {
          data.currentPage = page;
          data.items.push.apply(data.items, page.content);
        }).finally(function () {
          data.loading = false;
        });
      }
    }

    // ----------------------------------------------------------------

    function _checkNotificationSettings() {
      UserNotificationSettingModel.query({}, {userId: vm.user.id}).then(function (result) {
        vm.hasNotificationSettings = result.length > 0;
      });
    }

    function _checkTermsActive() {
      termsService.termsActive().then(function (active) {
        vm.termsActive = active;
      });
    }

    (function _init() {
      // register sidebar
      sidebarService.register({
        name: 'menu',
        open: open,
        close: close,
        isOpen: function () {
          return vm.showSidebar;
        }
      });

      // init navigation states
      if (angular.isUndefined($localStorage.navState)) {
        $localStorage.navState = {main: true};
      }
      vm.navState = angular.copy($localStorage.navState);

      // init user
      authService.getUser().then(function (user) {
        vm.user = user;
        _checkTermsActive();
        _checkNotificationSettings();
        loadMoreSubscriptions('page');
        loadMoreSubscriptions('workspace');
      });
    })();
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('commons.layout')
      .directive('oyocSuppressScroll', suppressScroll)
      .controller('SuppressScrollController', SuppressScrollController);

  /**
   * @ngdoc directive
   * @name coyo.app.oyocSuppressScroll:oyocSuppressScroll
   * @element ANY
   * @restrict A
   * @scope
   *
   * @description
   * Suppress the scrolling of the given container when a modal or sidebar is open.
   *
   * @requires $scope
   * @requires $rootScope
   * @requires $element
   * @requires $window
   */
  function suppressScroll() {
    return {
      restrict: 'A',
      controller: 'SuppressScrollController'
    };
  }

  function SuppressScrollController($scope, $rootScope, $element, $window) {
    var currentScroll = 0;
    var bodyElement = angular.element('body');
    var bodyWrapper = angular.element('.body');

    $scope.$watch(function () {
      return $rootScope.showBackdrop || bodyElement.hasClass('modal-open');
    }, function (newValue, oldValue) {
      if (newValue === oldValue) {
        return;
      }
      if (newValue) {
        currentScroll = $window.scrollY;
        bodyWrapper.css('position', 'fixed');
        $element.css({'position': 'absolute', 'top': -currentScroll});
      } else {
        bodyWrapper.css('position', 'static');
        $element.css({'position': 'static', 'top': 0});
        bodyElement.scrollTop(currentScroll);
      }
    });
  }
})(angular);

(function (angular) {
  'use strict';

  angular
      .module('commons.messaging')
      .factory('messagingService', messagingService);

  /**
   * @ngdoc service
   * @name coyo.messaging.messagingService
   *
   * @description
   * This service makes messaging functionality available to the outside.
   *
   * @requires $rootScope
   * @requires authService
   * @requires MessageChannelModel
   */
  // TODO: Move to 'coyo.messaging' module (not currently possible due to circular dependencies)
  function messagingService($rootScope, authService, MessageChannelModel) {

    return {
      toggle: toggle,
      start: start,
      open: open
    };

    /**
     * @ngdoc method
     * @name coyo.messaging.messagingService#toggle
     * @methodOf coyo.messaging.messagingService
     *
     * @description
     * Toggles the compact view mode of the messaging sidebar.
     *
     * @param {boolean} compact True to switch to compact mode, false otherwise.
     *
     * @returns {object} An event object
     */
    function toggle(compact) {
      return $rootScope.$emit('messaging:toggle', compact);
    }

    /**
     * @ngdoc method
     * @name coyo.messaging.messagingService#start
     * @methodOf coyo.messaging.messagingService
     *
     * @description
     * Starts a new chat with another user.
     *
     * @param {string} partnerId The ID of the conversation partner.
     *
     * @returns {promise} An event object promise
     */
    function start(partnerId) {
      return authService.getUser().then(function (currentUser) {
        new MessageChannelModel({
          type: 'SINGLE',
          creatorId: currentUser.id,
          memberIds: [partnerId]
        }).save().then(function (channel) {
          return $rootScope.$emit('messaging:start', channel);
        });
      });
    }

    /**
     * @ngdoc method
     * @name coyo.messaging.messagingService#open
     * @methodOf coyo.messaging.messagingService
     *
     * @description
     * Opens given channel in the messaging sidebar.
     *
     * @param {string} channelId The ID of the channel that should be opened.
     *
     * @returns {promise} An event object promise
     */
    function open(channelId) {
      return new MessageChannelModel({id: channelId}).get().then(function (channel) {
        return $rootScope.$emit('messaging:start', channel);
      });
    }
  }

})(angular);

(function () {
  'use strict';

  angular
      .module('commons.sender')
      .directive('coyoAvatarImage', avatarImage)
      .controller('AvatarImageController', AvatarImageController);

  /**
   * @ngdoc directive
   * @name commons.sender.coyoAvatarImage:coyoAvatarImage
   * @element OWN
   * @restrict E
   * @scope
   *
   * @description
   * Renders the image element for a sender's avatar and takes care, that the correct image sizes is set as src. The
   * image size depends on the passed size for the avatar. The directive also takes into consideration whether the
   * display supports retina.
   *
   * @param {string} imageUrl
   * The base url for the avatar image without the imageSize parameter. This parameter is determined and set by the
   * directive.
   *
   * @param {string=} avatarSize
   * Desired size of the avatar. Possible values are xs, sm, md, lg and xl. Default: md.
   *
   * @requires $scope
   * @requires $rootScope
   * @requires commons.resource.backendUrlService
   */
  function avatarImage() {
    return {
      restrict: 'E',
      templateUrl: 'app/commons/sender/components/avatar-image/avatar-image.html',
      scope: {},
      bindToController: {
        imageUrl: '<?',
        avatarSize: '@?',
        avatarBackground: '@?',
        onError: '&'
      },
      controller: 'AvatarImageController',
      controllerAs: '$ctrl'
    };
  }

  function AvatarImageController($rootScope, $scope, backendUrlService) {
    var vm = this;

    /**
     * Defines the translation from avatar sizes (bootstrap style) to available image sizes in backend.
     */
    var imageSizes = {
      'xs': {'sd': 'XS', 'retina': 'S'},
      'sm': {'sd': 'XS', 'retina': 'S'},
      'md': {'sd': 'S', 'retina': 'M'},
      'lg': {'sd': 'M', 'retina': 'L'},
      'xl': {'sd': 'M', 'retina': 'L'}
    };

    vm.imageSrc = '';
    vm.imageSize = imageSizes[vm.avatarSize || 'md'];
    vm.imageStyle = vm.avatarBackground ? {'background-color': vm.avatarBackground} : {};

    (function _init() {
      $scope.$watch(function () {
        return vm.imageUrl;
      }, function () {
        if (vm.imageUrl) {
          var baseUrl = backendUrlService.getUrl() + vm.imageUrl;
          var size = ($rootScope.screenSize.isRetina) ? vm.imageSize.retina : vm.imageSize.sd;
          vm.imageSrc = baseUrl + (baseUrl.indexOf('?') < 0 ? '?' : '&') + 'imageSize=' + size;
        } else {
          vm.imageSrc = '';
        }
      });
    })();
  }
})();

(function () {
  'use strict';

  angular
      .module('commons.sender')
      .component('coyoFilePreview', filePreview())
      .controller('PreviewImageController', PreviewImageController);

  /**
   * @ngdoc directive
   * @name commons.sender.coyoFilePreview:coyoFilePreview
   * @element OWN
   * @restrict E
   * @scope
   *
   * @description
   * Renders the image element for a sender's document preview and takes care, that the correct image sizes is set as
   * src. The image size depends on the passed size for the preview. The directive also takes into consideration whether
   * the display supports retina.
   *
   * @param {object} file
   * The file to get the preview of.
   * the display supports retina.
   *
   * @param {string} url
   * The 'base' preview URL with placeholders for the group ID and the image ID
   *
   * @param {string} groupId
   * The group ID to be replaced in the URL
   *
   * @param {string=} size
   * The size of the preview. Possible values are xs, sm, md, lg and xl and original. Default: md.
   *
   * @param {string} options
   * The options.
   *
   * @requires $scope
   * @requires $rootScope
   * @requires commons.resource.backendUrlService
   */
  function filePreview() {
    return {
      restrict: 'E',
      templateUrl: 'app/commons/sender/components/file-preview/file-preview.html',
      scope: {},
      bindings: {
        file: '<',
        url: '<',
        groupId: '<',
        size: '@?',
        options: '<?'
      },
      controller: 'PreviewImageController',
      controllerAs: '$ctrl'
    };
  }

  function PreviewImageController($rootScope, $scope, $http, $sce, backendUrlService, socketService,
                                  CapabilitiesModel, errorService) {
    var vm = this;

    var isSuccess = false;
    var unsubscribePreviewStatusFn;
    var fileStatus;
    vm.conversionError = false;
    vm.options = angular.extend({
      hideNoPreviewAvailableMsg: false,
      hidePreviewGenerationInformation: false,
      backgroundImage: false,
      showPdfDesktop: false,
      showPdfMobile: false
    }, vm.options);

    vm.onError = onError;

    /**
     * Defines the translation from preview sizes (bootstrap style) to available image sizes in backend.
     */
    var imageSizes = {
      xs: {sd: 'XS', retina: 'S'},
      sm: {sd: 'S', retina: 'M'},
      md: {sd: 'M', retina: 'L'},
      lg: {sd: 'L', retina: 'XL'},
      xl: {sd: 'XL', retina: 'XXL'},
      original: {sd: '', retina: ''}
    };

    vm.imageSize = imageSizes[vm.size || 'md'];
    vm.isProcessing = false;

    /**
     * Called if an error occurred on image load
     */
    function onError() {
      if (fileStatus === 'FAILED' || fileStatus === 'CANNOT_PROCESS') {
        vm.loading = false;
        vm.isProcessing = false;
        vm.conversionError = true;
        vm.previewAvailable = false;
      } else {
        vm.isProcessing = true;
      }
    }

    /*****************************************************************************/

    /**
     * Unsubscribes from the file status socket connection.
     */
    function _unsubscribeFileStatus() {
      if (unsubscribePreviewStatusFn) {
        unsubscribePreviewStatusFn();
        unsubscribePreviewStatusFn = undefined;
      }
    }

    /**
     * Returns the image size (retina or sd).
     *
     * @returns {string} size The image size (retina or sd)
     */
    function _getImageSize() {
      return ($rootScope.screenSize.isRetina) ? vm.imageSize.retina : vm.imageSize.sd;
    }

    /**
     * No preview can be generated. The image will not be available.
     */
    function _setCannotProcess() {
      _unsubscribeFileStatus();
      vm.loading = false;
      vm.conversionError = false;
      vm.isProcessing = false;
      $rootScope.$emit('previewNotAvailable', vm.file.id);
    }

    /**
     * Preview generation is in progress. The image will be available.
     */
    function _setProcessing() {
      vm.loading = false;
      vm.conversionError = false;
      vm.isProcessing = true;
    }

    /**
     * Preview generation succeeded. The image is available.
     */
    function _setSuccess() {
      _unsubscribeFileStatus();
      isSuccess = true;
      vm.loading = false;
      vm.conversionError = false;
      vm.isProcessing = false;
      CapabilitiesModel.previewImageFormat(vm.file.contentType).then(function (format) {
        vm.imageSrc = _createPreviewUrl(vm.url, vm.groupId, vm.file.id, _getImageSize(), format, vm.file.modified);
      });
      vm.pdfSrc = _createPdfUrl(vm.url, vm.groupId, vm.file.id, vm.file.modified);
    }

    /**
     * Preview generation failed. The image will not be available.
     */
    function _setFailed() {
      _unsubscribeFileStatus();
      vm.loading = false;
      vm.conversionError = true;
      vm.isProcessing = false;
    }

    /**
     * On preview status change. Checks whether a preview could/can be generated and whether it is already generated.
     * Redirects to the specific status functions.
     *
     * @param {string} status The returned preview status from the backend
     */
    function _previewStatusChange(status) {
      if (!isSuccess) {
        switch (status) {
          case 'CANNOT_PROCESS':
            _setCannotProcess();
            break;
          case 'PROCESSING':
            _setProcessing();
            break;
          case 'SUCCESS':
            _setSuccess();
            break;
          case 'FAILED':
            _setFailed();
            break;
          default:
            _setCannotProcess();
            break;
        }
      }
    }

    /**
     * Creates the preview URL.
     *
     * @param {string} url the backend URL
     * @param {string} groupId the group ID
     * @param {string} documentId the document ID
     * @param {string} size the size of the preview image to be loaded
     * @param {string} format the mimetype of preview image
     * @param {string} modified the modified date
     * @return {string} a preview URL
     */
    function _createPreviewUrl(url, groupId, documentId, size, format, modified) {
      var baseUrl = backendUrlService.getUrl() + url.replace('{{groupId}}', groupId).replace('{{id}}', documentId);
      var _modified = modified ? ('&modified=' + modified) : '';
      var _type = size ? ('&type=' + size) : '';
      format = (!size && vm.file.contentType === 'image/gif') ? vm.file.contentType : format;

      return baseUrl + (baseUrl.indexOf('?') < 0 ? '?' : '&') + 'format=' + format + _type + _modified;
    }

    function _createPdfUrl(url, groupId, fileId, modified) {
      var baseUrl = backendUrlService.getUrl() + url.replace('{{groupId}}', groupId).replace('{{id}}', fileId);
      var _modified = modified ? ('modified=' + modified) : '';
      var _amp = modified && (vm.file.contentType !== 'application/pdf') ? '&' : '';
      var _format = (vm.file.contentType !== 'application/pdf') ? ('format=application/pdf') : '';

      return $sce.trustAsResourceUrl(baseUrl + (baseUrl.indexOf('?') < 0 ? '?' : '&') + _format + _amp + _modified);
    }

    /**
     * Returns the preview status.
     *
     * @param {string} url the backend URL
     * @param {string} groupId the group ID
     * @param {string} documentId the document ID
     * @return {string} the status of the preview generation of the given document
     */
    function _getPreviewStatus(url, groupId, documentId) {
      return backendUrlService.getUrl() + url.replace('{{groupId}}', groupId).replace('{{id}}', documentId) + '/preview-status';
    }

    /**
     * Checks whether a preview can be generated.
     *
     * @param {object} file The file to be checked
     * @param {boolean} true if a preview can be generated, false else
     */
    function _canGeneratePreview(file) {
      return CapabilitiesModel.imgAvailable(file.contentType).then(function (available) {
        return available;
      }).catch(function () {
        return false;
      });
    }

    /**
     * Subscribes to the preview status socket route of the file.
     * Register the unsubscription on scope destroy.
     */
    function _subscribeToPreviewStatus() {
      unsubscribePreviewStatusFn =
          socketService.subscribe('/topic/item.' + vm.groupId + '.' + vm.file.id + '.preview.status', function (data) {
            _previewStatusChange(data.content.status);
          });
      $scope.$on('$destroy', _unsubscribeFileStatus);
    }

    /**
     * Tries to load the preview image of the given file.
     */
    function _loadPreview() {
      vm.loading = true;
      _canGeneratePreview(vm.file).then(function (previewAvailable) {
        vm.previewAvailable = previewAvailable;
        if (vm.previewAvailable) {
          _subscribeToPreviewStatus();
          // Don't cache this request client-side!
          $http.get(_getPreviewStatus(vm.url, vm.groupId, vm.file.id)).then(function (statusData) {
            _previewStatusChange(statusData.data.status);
          }).catch(function (errorResponse) {
            errorService.suppressNotification(errorResponse);
            _setCannotProcess();
          });
        } else {
          _setCannotProcess();
        }
      });
    }

    function _isVideoTypeSupported(contentType) {
      // removed ogg and webm because it's not supported on every browser
      return contentType === 'video/mp4';
    }

    /**
     * Resets all status and tries to load the preview.
     */
    function _reset() {
      _unsubscribeFileStatus();
      isSuccess = false;

      vm.imageSrc = undefined;
      vm.conversionError = false;
      vm.previewAvailable = false;
      vm.isProcessing = false;
      vm.videoType = false;

      if (_isVideoTypeSupported(vm.file.contentType)) {
        vm.videoType = true;
        vm.previewAvailable = true;
        vm.videoUrl = backendUrlService.getUrl() +
            vm.url.replace('{{groupId}}', vm.groupId).replace('{{id}}', vm.file.id) +
            '/stream';
      } else {
        _loadPreview();
      }
    }

    (function _init() {
      // this is loaded once initially and then after each change of vm.file
      $scope.$watch(function () {
        return vm.file;
      }, _reset);

      _reset();

      CapabilitiesModel.pdfAvailable(vm.file.contentType).then(function (available) {
        vm.pdfPreviewAvailable = available;
      });
    })();
  }
})();

(function () {
  'use strict';

  angular
      .module('commons.sender')
      .directive('coyoSenderCoverImage', SenderCoverImage)
      .controller('senderCoverImageController', senderCoverImageController);

  /**
   * @ngdoc directive
   * @name commons.sender.coyoSenderCoverImage:coyoSenderCoverImage
   * @element OWN
   * @restrict E
   * @scope
   *
   * @description
   * This directive renders the cover image for a sender. Therefore it needs the url to the cover image and then takes
   * care that the correct image for the correct device width is requested from the server and rendered. It also takes
   * into consideration whether the display supports retina or not. This directive listens to a global resize event and
   * is notified of changes in the display's width.
   *
   * @param {string} imageUrl
   * The base url for the cover image without the imageSize parameter. This parameter is determined and set by the
   * directive.
   *
   * @param {boolean} showHighRes
   * Determines if cover image should be loaded in higher resolutions, mostly for detail views of senders, in contrast
   * to say lists, where it is sufficient to load smaller images to save bandwidth and keep response times fast.
   *
   * @requires $rootScope
   * @requires $scope
   * @requires commons.resource.backendUrlService
   */
  function SenderCoverImage() {
    return {
      restrict: 'E',
      templateUrl: 'app/commons/sender/components/sender-cover-image/sender-cover-image.html',
      scope: {},
      bindToController: {
        imageUrl: '<?',
        showHighRes: '<?'
      },
      controller: 'senderCoverImageController',
      controllerAs: '$ctrl'
    };
  }

  function senderCoverImageController($rootScope, $scope, backendUrlService) {
    var vm = this;
    vm.showHighRes = !_.isUndefined(vm.showHighRes) ? vm.showHighRes : true;
    vm.coverImageUrl = '';

    var backendUrl = backendUrlService.getUrl();

    // ---------------------- PRIVATE METHODS ------------------------

    function _setCover() {
      if (vm.showHighRes) {
        if ($rootScope.screenSize.isRetina && ($rootScope.screenSize.isLg || $rootScope.screenSize.isMd)) {
          vm.coverImageUrl = _createCoverUrl('ORIGINAL');
        } else {
          vm.coverImageUrl = _createCoverUrl('XXL');
        }
      } else {
        vm.coverImageUrl = _createCoverUrl('XL');
      }
    }

    function _createCoverUrl(size) {
      return vm.imageUrl ? 'url(' + backendUrl + vm.imageUrl + '&imageSize=' + size + ')' : 'none';
    }

    // ---------------------------- INIT ----------------------------

    (function _init() {
      // change cover image if image url changed
      $scope.$watch(function () {
        return vm.imageUrl;
      }, function () {
        _setCover();
      });

      // change cover if screen size changed
      var unsubscribe = $rootScope.$on('screenSize:changed', function () {
        _setCover();
      });
      $scope.$on('$destroy', unsubscribe);
    })();
  }
})();

(function (angular) {
  'use strict';

  angular
      .module('commons.startup')
      .directive('oyocCurtain', curtain)
      .controller('CurtainController', CurtainController);

  /**
   * @ngdoc directive
   * @name commons.startup.oyocCurtain:oyocCurtain
   * @element ANY
   *
   * @description
   * The curtain is displayed before all other DOM elements and only hides once all required components
   * are loaded.
   *
   * The required components are listed as an array in <em>$rootScope.curtain.waitingFor</em>, which is
   * initialized in the main app.
   *
   * @requires $translate
   */
  function curtain() {
    return {
      replace: true,
      templateUrl: 'app/commons/startup/components/curtain/curtain.html',
      controller: 'CurtainController',
      controllerAs: '$ctrl'
    };
  }

  function CurtainController($translate) {
    var vm = this;

    $translate('CURTAIN.LOADING.TIP_' + _.random(1, 30)).then(function (tip) {
      vm.tip = tip;
    });
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('commons.startup')
      .provider('curtainService', curtainServiceProvider);

  /**
   * @ngdoc service
   * @name commons.startup.curtainServiceProvider
   *
   * @description
   * Modules can use the curtain service provider to register themselves as component to wait for. The curtain gets
   * released once all registered modules have called {@link commons.startup.curtainService curtainService#loaded}.
   */
  function curtainServiceProvider() {
    var waitingFor = [];

    return {
      $get: CurtainService,
      register: register
    };

    /**
     * @ngdoc service
     * @name commons.startup.curtainService
     *
     * @description
     * Provides methods for marking a component as loaded and for displaying and hiding the application curtain.
     *
     * The service can be used to
     * * mark a component as loaded,
     * * show the curtain and
     * * hide the curtain.
     *
     * @requires $rootScope
     * @requires $log
     */
    function CurtainService($rootScope, $log) {
      $rootScope.curtain = {loading: false, blankOnly: true};

      var service = {
        loaded: loaded,
        hide: hide,
        show: show,
        blankOnly: blankOnly
      };

      return service;

      /**
       * @ngdoc function
       * @name commons.startup.curtainService#loaded
       * @methodOf commons.startup.curtainService
       *
       * @description
       * Marks a given component as loaded. The passed key must be the same that was used when registering the
       * component via {@link commons.startup.curtainServiceProvider curtainServiceProvider#register}.
       *
       * @param {string} key The key of the component to be marked as loaded
       */
      function loaded(key) {
        $log.debug('[curtainService] Loaded component with name "' + key + '"');
        waitingFor = _.without(waitingFor, key);
        if (!waitingFor.length) {
          hide();
        }
      }

      /**
       * @ngdoc function
       * @name commons.startup.curtainService#show
       * @methodOf commons.startup.curtainService
       *
       * @description
       * Displays the application curtain.
       */
      function show() {
        $log.debug('[curtainService] Showing');
        $rootScope.curtain.loading = true;
      }

      /**
       * @ngdoc function
       * @name commons.startup.curtainService#hide
       * @methodOf commons.startup.curtainService
       *
       * @description
       * Hides the application curtain.
       */
      function hide() {
        $log.debug('[curtainService] Hiding');
        $rootScope.curtain.loading = false;
      }

      /**
       * @ngdoc function
       * @name commons.startup.curtainService#blankOnly
       * @methodOf commons.startup.curtainService
       *
       * @description
       * Enable "blank" mode for the curtain, where it (temporarily) only displays a blank screen until the custom css
       * is loaded.
       *
       * @param {boolean} isBlank
       * If true the curtain will be blank, if false the curtain will be visible normally.
       */
      function blankOnly(isBlank) {
        $rootScope.curtain.blankOnly = isBlank;
      }
    }

    /**
     * @ngdoc method
     * @name commons.startup.curtainServiceProvider#register
     * @methodOf commons.startup.curtainServiceProvider
     *
     * @description
     * Register as a component to wait for until the curtain gets released. The curtain is hidden if all registered
     * components have called {@link commons.startup.curtainService curtainService#loaded}. The component gets
     * identified by a certain key passed to the register method and the loaded method. This must be the same key.
     *
     * @param {string} key
     * A unique key to identify the component. The very same key must be passed to
     * {@link commons.startup.curtainService curtainService#loaded} to mark the registered component as ready.
     */
    function register(key) {
      // console.log('[CurtainServiceProvider] Registering component with key [' + key + ']');
      waitingFor.push(key);
    }

  }

})(angular);

(function () {
  'use strict';

  angular.module('commons.tour')
      .factory('tourSelectionModal', tourSelectionModal)
      .controller('TourSelectionModalController', TourSelectionModalController);

  /**
   * @ngdoc service
   * @name commons.tour.tourSelectionModal
   *
   * @description
   * Shows a modal with all tour topics available on the current page. A click on one of the topics restart the
   * tour steps of the given topic.
   *
   * @requires modalService
   */
  function tourSelectionModal(modalService) {

    return {
      open: open
    };

    /**
     * @ngdoc method
     * @name commons.tour.tourSelectionModal#open
     * @methodOf commons.tour.tourSelectionModal
     *
     * @description
     * Opens the modal. The modal is closed again when a user chooses a topic or if he closes the modal manually.
     *
     * @param {Array} topics
     * An array of topics which are available on the current page.
     *
     * @returns {object} A promise to be called when the modal is closed.
     */
    function open(topics) {
      return modalService.open({
        templateUrl: 'app/commons/tour/components/tour-selection-modal/tour-selection-modal.html',
        controller: 'TourSelectionModalController',
        resolve: {
          topics: function () {
            return topics || [];
          }
        }
      }).result;
    }
  }

  function TourSelectionModalController(topics, tourService, $uibModalInstance) {
    var vm = this;
    vm.topics = topics;

    vm.startTour = startTour;

    function startTour(topic) {
      $uibModalInstance.close();
      tourService.restart(topic);
    }
  }

})();

(function () {
  'use strict';

  angular
      .module('commons.tour')
      .component('oyocTourProgress', tourProgress())
      .controller('TourProgressController', TourProgressController);

  /**
   * @ngdoc directive
   * @name commons.tour.oyocTourProgress:oyocTourProgress
   * @element OWN
   * @restrict E
   * @scope
   *
   * @description
   * Displays the progress with a bootstrap progress bar of the given ui-tour.
   *
   * @param {object} currentTour
   * The current ui-tour object to display the progress for.
   *
   * @requires $scope
   */
  function tourProgress() {
    return {
      templateUrl: 'app/commons/tour/components/tour-progress/tour-progress.html',
      bindings: {
        currentTour: '<'
      },
      controller: 'TourProgressController'
    };
  }

  function TourProgressController($scope) {
    var vm = this;
    var steps = [];

    vm.$onInit = _init;

    function _init() {
      _refreshProgress();
      $scope.$watchCollection(function () {
        return vm.currentTour._getSteps();
      }, _refreshProgress);
    }

    function _refreshProgress() {
      steps = vm.currentTour._getSteps();
      vm.maxSteps = steps.length;
      vm.stepIndex = _.findIndex(steps, vm.currentTour.getCurrentStep()) + 1;
    }

  }
})();

(function () {
  'use strict';

  angular
      .module('commons.tour')
      .component('coyoTourStepMobile', tourStepMobile())
      .controller('TourStepMobileController', TourStepMobileController);

  /**
   * @ngdoc directive
   * @name commons.tour.coyoTourStepMobile:coyoTourStepMobile
   * @element OWN
   * @restrict E
   * @scope
   *
   * @description
   * This directive adds a tour step to the mobile tour. It is only visible when the user starts the application in a
   * mobile resolution (xs or sm). The tour step is automatically registered when loaded. Place it in the template
   * where you like to explain something to the user. In comparison to {@link commons.tour.coyoTourStep:coyoTourStep}
   * the resulting tour step is not bound to any DOM element.
   *
   * @param {string} topic
   * Every tour step needs to be assigned to a topic, which determines what the tour step is describing. E.g. all tour
   * steps explaining menu items of the main navigation should have the topic "navigation". If one step of the a topic
   * was loaded and the user ends the tour, all steps of that topic are marked as seen.
   *
   * @param {number} order
   * Sets the order in which the step should be shown within the tour. For example a step with the order "1" is
   * displayed before a step with the order "5".
   *
   * @param {string} title
   * The title of the tour step.
   *
   * @param {string} content
   * The content of the tour step. This supports displaying HTML.
   *
   * @param {string=} imageUrl
   * An optional url of an image to be added to the content. This will be placed under the content.
   */
  function tourStepMobile() {
    return {
      templateUrl: 'app/commons/tour/components/tour-step-mobile/tour-step-mobile.html',
      bindings: {
        topic: '@',
        order: '@',
        title: '@',
        content: '@',
        imageUrl: '@?'
      },
      controller: 'TourStepMobileController'
    };
  }

  function TourStepMobileController($rootScope, $scope, $transitions, tourService) {
    var vm = this;
    var unregisterTransitionHook, unregisterRestart, unregisterEnd;

    vm.$onInit = _init;

    function _init() {
      vm.enabled = false;
      vm.stepContent = (vm.imageUrl) ? vm.content + '<img src="' + vm.imageUrl + '" />' : vm.content;

      tourService.isEnabled(vm.topic).then(function (enabled) {
        vm.enabled = enabled;
      });

      unregisterTransitionHook = $transitions.onSuccess({}, function () {
        tourService.isEnabled(vm.topic).then(function (enabled) {
          vm.enabled = enabled;
        });
      });

      unregisterRestart = $rootScope.$on('tour.restart', function (event, key) {
        if (key === 'mobile') {
          vm.enabled = true;
        }
      });

      unregisterEnd = $rootScope.$on('tour.ended', function () {
        vm.enabled = false;
      });

      $scope.$on('$destroy', _destroy);
    }

    function _destroy() {
      if (unregisterTransitionHook) {
        unregisterTransitionHook();
      }
      if (unregisterRestart) {
        unregisterRestart();
      }
      if (unregisterEnd) {
        unregisterEnd();
      }
    }

  }

})();

(function () {
  'use strict';

  angular
      .module('commons.tour')
      .component('coyoTourStep', tourStep())
      .controller('TourStepController', TourStepController);

  /**
   * @ngdoc directive
   * @name commons.tour.coyoTourStep:coyoTourStep
   * @element OWN
   * @restrict E
   * @scope
   *
   * @description
   * This directive adds a tour step to the desktop tour. It is not visible when the user starts the application in a
   * mobile resolution (xs or sm). The tour step is automatically registered and bound to the html element containing
   * the component.
   *
   * IMPORTANT NOTE: Make sure that the parent element of this directive has "position: relative" or add the class
   * "tour-placeholder-container" to it.
   *
   * @param {string} topic
   * Every tour step needs to be assigned to a topic, which determines what the tour step is describing. E.g. all tour
   * steps explaining menu items of the main navigation should have the topic "navigation". If one step of the a topic
   * was loaded and the user ends the tour, all steps of that topic are marked as seen.
   *
   * @param {number} order
   * Sets the order in which the step should be shown within the tour. For example a step with the order "1" is
   * displayed before a step with the order "5".
   *
   * @param {string} title
   * The title of the tour step.
   *
   * @param {string} content
   * The content of the tour step. This supports displaying HTML.
   *
   * @param {string} stepId
   * Identifies a tour step.
   *
   * @param {string=} imageUrl
   * An optional url of an image to be added to the content. This will be placed under the content.
   *
   * @param {string=auto} placement
   * A placement for the tour popover can be provided. By default the placement is determined automatically. See the
   * bootstrap-ui documentation for possible values.
   *
   * @param {boolean=false} noHighlight
   * By default an element is generated that fills the whole parent container of this directive to create a highlight
   * effect of the assigned DOM element. Sometimes this is not the desired effect or doesn't work properly. If this
   * is the case set this flag to "true".
   *
   * @param {boolean=false} fixed
   * This parameter needs to be set 'true' if the tour is attached to a fixed element.
   *
   * @param {string=} popupClass
   * Adds an additional class to the popover window of the tour step.
   *
   * @requires $log
   * @requires $rootScope
   * @requires $scope
   * @requires $element
   * @requires commons.tour.tourService
   *
   */
  function tourStep() {
    return {
      templateUrl: 'app/commons/tour/components/tour-step/tour-step.html',
      bindings: {
        topic: '@',
        order: '@',
        title: '@',
        content: '@',
        stepId: '@',
        imageUrl: '@?',
        placement: '@?',
        noHighlight: '<?',
        popupClass: '@?',
        fixed: '<?'
      },
      controller: 'TourStepController'
    };
  }

  function TourStepController($log, $rootScope, $scope, $element, $transitions, tourService) {
    var vm = this;
    var api = {};
    var unregisterTransitionHook, unregisterRestart, unregisterEnd;

    vm.$onInit = _init;

    function _init() {
      if (angular.isUndefined(vm.stepId)) {
        throw new Error('The parameter "stepId" is mandatory. Error when rendering tour step for topic "' + vm.topic + '"');
      }

      // create api object to register at service
      api = {id: vm.stepId, topic: vm.topic};

      vm.enabled = false;
      vm.isStepRegistered = tourService.isStepRegistered(vm.stepId);
      vm.placement = vm.placement || 'auto';
      vm.noHighlight = angular.isDefined(vm.noHighlight) ? !!vm.noHighlight : false;
      vm.fixed = angular.isDefined(vm.fixed) ? !!vm.fixed : false;
      vm.stepContent = (vm.imageUrl) ? vm.content + '<img src="' + vm.imageUrl + '" />' : vm.content;

      var placeholderClass = (vm.noHighlight) ? 'tour-placeholder' : 'tour-placeholder-highlight';
      $element.addClass(placeholderClass);

      tourService.isEnabled(vm.topic).then(_setEnabled);
      unregisterTransitionHook = $transitions.onSuccess({}, function () {
        tourService.isEnabled(vm.topic).then(_setEnabled);
      });

      unregisterRestart = $rootScope.$on('tour.restart', function (event, key) {
        if (key === vm.topic) {
          vm.enabled = true;
        }
      });

      unregisterEnd = $rootScope.$on('tour.ended', function () {
        vm.enabled = false;
      });

      _setVisibility($element.is(':visible'));
      $scope.$watch(function () {
        return $element.is(':visible');
      }, function () {
        _setVisibility($element.is(':visible'));
      });

      // clean up after scope was destroyed
      $scope.$on('$destroy', _destroy);
    }

    function _setVisibility(visibility) {
      vm.visible = visibility;
      if (vm.visible) {
        tourService.registerStep(api);
      }
      _logStateChanged();
    }

    function _setEnabled(enabled) {
      vm.enabled = enabled;
      _logStateChanged();
    }

    function _logStateChanged() {
      $log.debug('[TourStep] Tour step with id [' + vm.stepId + '] is enabled (' + vm.enabled + '), visible (' + vm.visible + ') and ID was already rendered (' + vm.isStepRegistered + ').');
    }

    function _destroy() {
      if (unregisterTransitionHook) {
        unregisterTransitionHook();
      }
      if (unregisterRestart) {
        unregisterRestart();
      }
      if (unregisterEnd) {
        unregisterEnd();
      }
      tourService.unregisterStep(api.id);
    }

  }

})();

