(function (angular) {
  'use strict';

  angular
      .module('commons.ui')
      .filter('boldify', boldifyFilter);

  /**
   * @ngdoc filter
   * @name commons.ui.boldify:boldify
   * @function
   *
   * @description
   * Replaces '*' encapsuled strings with <em>'strong'</em>-tags.
   *
   * @param {string} input An input string to be boldified
   */
  function boldifyFilter() {
    return function (input) {
      /* eslint-disable no-useless-escape */
      return input.replace(/\*([^\*]*)\*/g, '<strong>$1</strong>');
      /* eslint-enable no-useless-escape */
    };
  }

})(angular);

(function () {
  'use strict';

  angular
      .module('commons.ui')
      .component('coyoBtnAttachments', attachmentButton())
      .controller('attachmentButtonController', attachmentButtonController);

  /**
   * @ngdoc directive
   * @name commons.ui.coyoBtnAttachments:coyoBtnAttachments
   * @element OWN
   * @restrict E
   * @scope
   *
   * @description
   * Renders an attachment button with an context menu. The context menu has two options:
   * Upload File and Select from file Library.
   *
   * @param {expression} onSelectAttachments
   * Callback when files are selected to upload
   *
   * @param {expression} onSelectFileLibrary
   * Callback when file library files are selected
   *
   * @param {boolean} active
   * Flag that indicates if the button should have active class
   *
   * @param {object} author
   * The current author in which context the file library should be opened
   *
   * @param {string} contextId
   * The context in which the button is created (e.g. WS or Page id)
   *
   * @param {string} timelineType
   * The type of the timeline the attachment are supposed to be added. If not 'personal' the fileLibrary is opened with
   * the contextId as starting point. Default is 'personal'.
   *
   */
  function attachmentButton() {
    return {
      templateUrl: 'app/commons/ui/components/btn-attachments/btn-attachments.html',
      bindings: {
        onSelectAttachments: '&',
        onSelectFileLibrary: '&',
        active: '<',
        author: '<',
        contextId: '@',
        timelineType: '<'
      },
      controller: 'attachmentButtonController'
    };
  }

  function attachmentButtonController(fileLibraryModalService, utilService) {
    var vm = this;
    vm.$onInit = _init();
    vm.openFileLibrary = openFileLibrary;

    function openFileLibrary() {

      fileLibraryModalService.open(vm.timelineType === 'personal' ? vm.author : {id: vm.contextId},
          {selectMode: 'multiple'})
          .then(function (selectedFiles) {
            var attachments = _.map(selectedFiles, function (elem) {
              return {
                fileId: elem.id,
                senderId: elem.senderId,
                displayName: elem.displayName
              };
            });
            vm.onSelectFileLibrary({files: attachments});
          });
    }

    function _init() {
      vm.uuid = utilService.uuid();
      if (!vm.timelineType) {
        vm.timelineType = 'personal';
      }
    }
  }
})();

(function (angular) {
  'use strict';

  angular
      .module('commons.ui')
      .directive('coyoBtnLikes', btnLikes)
      .controller('BtnLikeController', BtnLikeController)
      .controller('BtnLikeModalController', BtnLikeModalController);

  /**
   * @ngdoc directive
   * @name commons.ui.coyoBtnLikes:coyoBtnLikes
   * @scope
   * @restrict 'E'
   * @element OWN
   *
   * @description
   * Renders a like button from the perspective of the current user. The button is rendered in a condensed style if the
   * directive defines a condensed attribute.
   *
   * @requires $scope
   * @requires $log
   * @requires authService
   * @requires LikeModel
   * @requires modalService
   *
   * @param {object} target The like target.
   * @param {string} target.id The ID of the target.
   * @param {string} target.typeName The type of the target.
   * @param {string} [init] Initial like data.
   * @param {string} authorId Optional author id, e.g. selected in timeline item.
   */
  function btnLikes() {
    return {
      restrict: 'E',
      replace: true,
      templateUrl: function (elem, attrs) {
        var isCondensed = angular.isDefined(attrs.condensed);
        return 'app/commons/ui/components/btn-likes/btn-likes' + (isCondensed ? '.condensed.html' : '.html');
      },
      scope: {},
      bindToController: {
        target: '<',
        init: '<',
        authorId: '<?'
      },
      controller: 'BtnLikeController',
      controllerAs: '$ctrl'
    };
  }

  function BtnLikeController($scope, $log, authService, modalService, LikeModel, likesService) {
    var vm = this;

    vm.tooltipUrl = 'app/commons/ui/components/btn-likes/btn-likes.tooltip.html';
    vm.tooltipUrlCondensed = 'app/commons/ui/components/btn-likes/btn-likes.tooltip.condensed.html';

    vm.$onInit = init;
    vm.like = like;
    vm.show = show;

    function init() {
      authService.onGlobalPermissions('LIKE', _initWithPermission);
    }

    function like() {
      if (_isCurrentlyLoading()) {
        return; // currently loading
      }

      vm.isLoading = true;
      var request = vm.likeData.self
        ? LikeModel.unlike(vm.target.id, vm.target.typeName, vm.authorId)
        : LikeModel.like(vm.target.id, vm.target.typeName, vm.authorId);
      request.then(_set).finally(function () {
        vm.isLoading = false;
        $scope.$broadcast('like:refresh');
      });
    }

    function show() {
      if (_isCurrentlyLoading()) {
        return; // currently loading
      }

      modalService.open({
        templateUrl: 'app/commons/ui/components/btn-likes/btn-likes.modal.html',
        controller: 'BtnLikeModalController',
        resolve: {
          target: function () {
            return vm.target;
          }
        }
      });
    }

    function _isCurrentlyLoading() {
      return vm.isLoading || !vm.currentUser || !vm.likeData;
    }

    function _set(likeInfo) {
      vm.likeData = {
        self: likeInfo.likedBySender,
        total: likeInfo.latest,
        totalCount: likeInfo.count,
        others: _.reject(likeInfo.latest, {id: vm.authorId}),
        othersCount: likeInfo.count - likeInfo.likedBySender
      };
    }

    function _initWithPermission(permission, currentUser) {
      vm.visible = permission;
      vm.currentUser = currentUser;
      if (angular.isUndefined(vm.authorId)) {
        vm.authorId = currentUser.id;
      }

      // load like info
      if (vm.init) {
        _set(vm.init);
      } else {
        loadLikes();
      }

      $scope.$watch(function () {
        return vm.authorId;
      }, function (newValue, oldValue) {
        if (newValue !== oldValue) {
          loadLikes();
        }
      });
    }

    function loadLikes() {
      vm.isLoading = true;
      likesService.getLikes(vm.target.id, vm.target.typeName, vm.authorId)
          .then(_set)
          .catch(function () {
            $log.error('[Likes] Could not load likes for target with id [' + vm.target.id + ']');
          })
          .finally(function () {
            vm.isLoading = false;
            $scope.$broadcast('like:refresh');
          });
    }
  }

  function BtnLikeModalController(LikeModel, Pageable, target) {
    var vm = this;

    vm.target = target;
    vm.likes = [];

    vm.$onInit = init;
    vm.loadMore = loadMore;

    function init() {
      loadMore();
    }

    function loadMore() {
      if (!vm.currentPage || !vm.currentPage.last) {
        vm.loading = true;

        var pageable = new Pageable((vm.currentPage ? vm.currentPage.number + 1 : 0), 20, 'created,desc');
        LikeModel.pagedQuery(pageable, {}, {
          targetId: vm.target.id,
          targetType: vm.target.typeName
        }).then(function (page) {
          vm.currentPage = page;
          vm.likes = vm.likes.concat(page.content);
        }).finally(function () {
          vm.loading = false;
        });
      }
    }
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('commons.ui')
      .factory('likesService', likesService);

  /**
   * @ngdoc service
   * @name commons.ui.likesService
   *
   * @description
   * Provides functions for bulk-load likes
   *
   * @requires coyo.domain.LikeModel
   * @requires $q
   * @requires $timeout
   * @requires $coyoEndpoints
   */
  function likesService(LikeModel, $q, $timeout, coyoEndpoints) {

    var pendingLikeInfoRequests = [];

    return {
      getLikes: getLikes
    };

    /**
     * @ngdoc function
     * @name commons.ui.likesService#getLikes
     * @methodOf commons.ui.likesService
     *
     * @description
     * Caches the like request for one digest cycle and sending a bulk request
     *
     * @param {string} targetId  the id of the target
     * @param {string} targetType  the type of the target
     * @param {string} senderId  the id of the sender which sends the request
     *
     * @returns {promise} Is fulfilled with the actual likes of the target
     */
    function getLikes(targetId, targetType, senderId) {

      var deferred = $q.defer();
      pendingLikeInfoRequests.push({targetId: targetId, targetType: targetType, promise: deferred});

      /*
       * Wait for one angular digest cycle and collect all requested target ids. Make one call (per user id) to the
       * backend and then assign the results to the designated promises.
       */
      $timeout(function () {
        if (!_.isEmpty(pendingLikeInfoRequests)) {
          var pendingCopy = angular.copy(pendingLikeInfoRequests);
          pendingLikeInfoRequests = [];

          angular.forEach(_.groupBy(pendingCopy, 'targetType'), function (entry, targetType) {
            _getLikeInfosForTargetType(entry, targetType, senderId);
          });
        }
      });
      return deferred.promise;
    }

    function _getLikeInfosForTargetType(likeRequests, targetType, senderId) {
      var url = coyoEndpoints.likes.infoBulk.replace('{{targetType}}', targetType);

      LikeModel.$get(url, {
        senderId: senderId,
        ids: _.map(likeRequests, 'targetId')
      }).then(function (likeInfos) {
        angular.forEach(likeRequests, function (likeRequest) {
          if (likeInfos[likeRequest.targetId]) {
            likeRequest.promise.resolve(likeInfos[likeRequest.targetId]);
          } else {
            likeRequest.promise.reject();
          }
        });
      }, function () {
        angular.forEach(likeRequests, function (request) {
          request.promise.reject();
        });
      });
    }
  }

})(angular);

