(function (angular) {
  'use strict';

  angular
      .module('commons.ui')
      .directive('coyoDivider', divider);

  /**
   * @ngdoc directive
   * @name commons.ui.coyoDivider:coyoDivider
   * @element ANY
   * @restrict E
   * @scope
   *
   * @description
   * Renders a horizontal divider with (optional) transcluded content.
   */
  function divider() {
    return {
      restrict: 'E',
      replace: true,
      transclude: true,
      template: '<div class="coyo-divider" ng-class="::{empty: isEmpty}"><span ng-transclude></span></div>',
      controller: function ($scope, $transclude) {
        $transclude(function (clone) {
          $scope.isEmpty = !clone.length;
        });
      }
    };
  }

})(angular);

(function () {
  'use strict';

  angular.module('commons.ui')
      .directive('coyoDelayForm', CoyoDelayForm);

  function CoyoDelayForm($parse) {
    return {
      restrict: 'A',
      require: 'form',
      link: function (scope, elem, attrs) {
        var snapshot = null;

        // watch model reference - no deep watch!
        // to manually trigger copy, use angular.extend({}, model)
        scope.$watch(attrs.coyoDelayForm, function (newVal) {
          if (newVal && newVal.restangularized === true) {
            // restangular can't handle angular.copy! :-(
            snapshot = newVal.clone();
          } else {
            snapshot = angular.copy(newVal);
          }
        });

        var unsubscribe = scope.$on('coyo.base:coyoDelayForm-refreshModel', function (e, newModel) {
          if (newModel && newModel.restangularized === true) {
            // restangular can't handle angular.copy! :-(
            snapshot = newModel.clone();
          } else {
            snapshot = angular.copy(newModel);
          }
        });

        scope.$on('$destroy', unsubscribe);

        elem.on('reset', function (event) {
          event.preventDefault();
          scope.$eval(attrs.ngReset);
          scope.$apply(function () {
            $parse(attrs.coyoDelayForm).assign(scope, snapshot);
          });
        });
      }
    };
  }

})();

(function () {
  'use strict';

  angular
      .module('commons.ui')
      .directive('coyoDownload', download);

  /**
   * @ngdoc directive
   * @name commons.ui.coyoDownload:coyoDownload
   * @element ANY
   * @restrict A
   *
   * @description
   * Adds a click handler to the given element that triggers the download of the provided url. Will ignore the
   * "leave page" browser alert when the current state is locked.
   * Important: do not use with non-download urls, otherwise it will break the page locking mechanism.
   *
   * @param {expr} coyoDownload expression that will be parsed against the current scope to get the download url
   *
   * @requires $parse
   * @requires $window
   * @requires $q
   * @requires $http
   * @requires $log
   * @requires commons.error.stateLockService
   *
   */
  function download($parse, $window, $q, $http, $log, stateLockService) {
    return {
      restrict: 'A',
      link: function (scope, element, attributes) {
        function clickHandler() {
          $q.when($parse(attributes.coyoDownload)(scope)).then(function (url) {
            stateLockService.ignoreLock(function () {
              $http.head(url).then(function () {
                $window.location = url;
              }).catch(function (message) {
                $log.debug('could not fetch file from server, must be deleted or there are insufficient permissions ' + message);
              });
            });
          });
        }
        element.bind('click', clickHandler);
        scope.$on('$destroy', function () {
          element.unbind('click', clickHandler);
        });
      }
    };
  }
})();

