(function (angular) {
  'use strict';

  angular
      .module('commons.ui')
      .filter('fileTypeName', fileTypeNameFilter);

  /**
   * @ngdoc filter
   * @name commons.ui.fileTypeName:fileTypeName
   * @function
   *
   * @description
   * Converts a content (mime) type into a type name.
   */
  function fileTypeNameFilter() {
    return function (contentType) {
      if (contentType) {
        // Microsoft Office document types
        if (contentType.indexOf('officedocument') >= 0) {
          if (contentType.indexOf('word') >= 0) {
            return 'FILE_TYPE_NAME.WORD';
          }

          if (contentType.indexOf('sheet') >= 0) {
            return 'FILE_TYPE_NAME.EXCEL';
          }

          if (contentType.indexOf('presentation') >= 0) {
            return 'FILE_TYPE_NAME.POWERPOINT';
          }
        }

        // Images
        if (contentType.indexOf('image') >= 0) {
          return 'FILE_TYPE_NAME.IMAGE';
        }

        // Videos
        if (contentType.indexOf('video') >= 0) {
          return 'FILE_TYPE_NAME.VIDEO';
        }

        // PDF
        if (contentType.indexOf('pdf') >= 0) {
          return 'FILE_TYPE_NAME.PDF';
        }

        // Text
        if (contentType.indexOf('plain') >= 0) {
          return 'FILE_TYPE_NAME.PLAIN';
        }
      }

      // Default
      return 'FILE_TYPE_NAME.DEFAULT';
    };
  }

})(angular);

(function () {
  'use strict';

  /**
   * @ngdoc directive
   * @name commons.ui.coyoFormSubmit
   * @scope
   * @restrict 'A'
   *
   * @description Directive for setting a 'loading' true/false flag on a form controller. You need to make sure that
   * your submit function returns a promise for this to work. Use this as a replacement for angular's 'ngSubmit' directive.
   *
   * @example <form coyo-form-submit="submitMyFormMethod">...</form>
   */
  angular
      .module('commons.ui')
      .directive('coyoFormSubmit', CoyoFormSubmit);

  function CoyoFormSubmit() {
    return {
      restrict: 'A',
      require: '^form',
      transclude: true,
      template: '<fieldset ng-disabled="_form.loading"><ng-transclude></ng-transclude></fieldset>',
      scope: {
        coyoFormSubmit: '&'
      },
      link: function (scope, element, attrs, formCtrl) {
        scope._form = formCtrl;
        element.on('submit', function () {
          var submitPromise = scope.coyoFormSubmit();
          if (submitPromise && angular.isFunction(submitPromise.finally)) {
            formCtrl.loading = true;
            submitPromise.finally(function () {
              formCtrl.loading = false;
            });
          }
        });
      }
    };
  }
})();

