(function (angular) {
  'use strict';

  angular
      .module('commons.ui')
      .filter('nl2br', nl2br);

  /**
   * @ngdoc filter
   * @name commons.ui:nl2br
   * @function
   *
   * @description
   * nl2br linky-compatible angular filter
   *
   * @see https://gist.github.com/habovh/3dcb42d18975375d5247
   */
  function nl2br($sanitize) {
    var tag = '<br />';
    return function (msg) {
      // ngSanitize's linky filter changes \r and \n to &#10; and &#13; respectively
      msg = (msg + '').replace(/(\r\n|\n\r|\r|\n|&#10;&#13;|&#13;&#10;|&#10;|&#13;)/g, tag + '$1');
      return $sanitize(msg);
    };
  }

})(angular);

(function () {
  'use strict';

  angular
      .module('commons.ui')
      .component('coyoOptionsField', optionsField())
      .controller('OptionsFieldController', OptionsFieldController);

  /**
   * @ngdoc directive
   * @name commons.ui.coyoOptionsField:coyoOptionsField
   * @element OWN
   * @restrict E
   * @scope
   *
   * @description
   * Renders an options field. The user can add, edit and delete options. The order can be changed with drag and drop.
   * At least one option with a none empty name must be set. Otherwise this field is not valid (setting a
   * $error.options validation error for the given form).
   *
   * @param {object} ngModel
   * Array of objects to store the options. An option consists of an id and a name.
   *
   * @requires $scope
   * @requires coyo.base.utilService
   */
  function optionsField() {
    return {
      templateUrl: 'app/commons/ui/components/options-field/options-field.html',
      bindings: {},
      require: {
        ngModel: '^ngModel'
      },
      controller: 'OptionsFieldController',
      controllerAs: '$ctrl'
    };
  }

  function OptionsFieldController($scope, utilService) {
    var vm = this;

    vm.options = [];
    vm.treeOptions = {};
    vm.focusedOption = '';

    vm.$onInit = init;
    vm.handleKeyPress = handleKeyPress;
    vm.addOption = addOptionAndFocus;
    vm.deleteOption = deleteOption;

    function init() {
      vm.ngModel.$render = function () {
        vm.options = angular.copy(vm.ngModel.$viewValue);
        if (vm.options.length <= 0) {
          _addOption();
        }

        // model is valid if at least one option was selected
        vm.ngModel.$validators.noOptions = function (modelValue, viewValue) {
          return viewValue.length > 0;
        };
      };

      $scope.$watch(function () {
        return vm.options;
      }, function (newValue, oldValue) {
        if (!_.isEqual(newValue, oldValue)) {
          vm.ngModel.$setViewValue(_.filter(newValue, function (value) {
            return !_.isEmpty(value.name);
          }));
        }
      }, true);
    }

    function addOptionAndFocus() {
      vm.focusedOption = _addOption().id;
    }

    function deleteOption(index) {
      if (index !== -1 && vm.options.length > 1) {
        vm.options.splice(index, 1);
      }
    }

    function handleKeyPress(event, option) {
      if (event && event.which === 13 && option.name) {
        event.preventDefault();
        event.stopPropagation();
        addOptionAndFocus();
      } else if (event && event.which === 8 && !option.name) {
        var previousOption = vm.options[vm.options.length - 2];
        if (previousOption) {
          deleteOption(vm.options.indexOf(option));
          vm.focusedOption = previousOption.id;
        }
      }
    }

    function _addOption() {
      var id = utilService.uuid();
      var option = {
        id: id,
        name: ''
      };
      vm.options.push(option);
      return option;
    }
  }
})();

(function () {
  'use strict';

  /**
   * A service for raising notifications.
   *
   * This service wraps all methods provided by the angular-ui-notification service and translates the
   * message key if needed.
   *
   * Provided message functions:
   *   * primary  - displays a primary notification
   *   * error    - displays an error notification
   *   * success  - displays a success notification
   *   * info     - displays an info notification
   *   * warning  - displays a warning notification
   *   * clearAll - clears all notifications that are currently displayed
   *
   * Usage:
   * coyoNotification.<function>('i18nKey'); // use i18n
   * coyoNotification.<function>('i18nKey', true); // use i18n
   * coyoNotification.<function>('i18nKey', false); // don't use i18n
   * coyoNotification.<function>('i18nKey', { i18nArg: 'someArg' }); // use i18n with i18n options
   * coyoNotification.<function>({ message: 'i18nKey', delay: 1000 }); // use 18n with notify options
   * coyoNotification.<function>({ message: 'i18nKey', delay: 1000 }, { i18nArg: 'someArg' }); // use 18n with i18n & notify options
   */
  angular
      .module('coyo.base')
      .factory('coyoNotification', NotificationService);

  function NotificationService($q, $translate, Notification) {
    var doFlash = function (args, i18n, notify) {
      // if (typeof args !== 'object') {
      if (!angular.isObject(args)) {
        args = {
          message: args
        };
      }

      if (i18n === false) {
        notify(args);
      } else {
        var promises = [];

        if (args.title) {
          promises.push($translate(args.title, i18n === true ? undefined : i18n));
        } else {
          promises.push(null);
        }

        if (args.message) {
          promises.push($translate(args.message, i18n === true ? undefined : i18n));
        } else {
          promises.push(null);
        }

        $q.all(promises).then(function (data) {
          notify(angular.extend(args, {title: data[0], message: data[1]}));
        }, function () {
          notify(args);
        });
      }
    };
    return {
      primary: function (args, i18n) {
        doFlash(args, i18n, function (args) {
          Notification.primary(args);
        });
      },
      error: function (args, i18n) {
        doFlash(args, i18n, function (args) {
          Notification.error(args);
        });
      },
      success: function (args, i18n) {
        doFlash(args, i18n, function (args) {
          Notification.success(args);
        });
      },
      info: function (args, i18n) {
        doFlash(args, i18n, function (args) {
          Notification.info(args);
        });
      },
      warning: function (args, i18n) {
        doFlash(args, i18n, function (args) {
          Notification.warning(args);
        });
      },
      clearAll: function () {
        Notification.clearAll();
      }
    };
  }

})();

(function () {
  'use strict';

  angular
      .module('commons.ui')
      .component('coyoPreviewList', previewList())
      .controller('PreviewListController', PreviewListController);

  /**
   * @ngdoc directive
   * @name commons.ui.coyoPreviewList:coyoPreviewList
   * @restrict E
   * @scope
   *
   * @description
   * Renders a list of preview images in a grid. Files without previews will be considered and displayed in a seprarate
   * listview. The first image of the grid will be displayed over the full width of the directive.
   *
   * @param {array} attachments
   * An array with all files {objects}
   *
   * @param {string} groupId
   * The group ID to be replaced in the URL
   *
   * @param {string} backendUrl
   * The backend URL to generate the download URL in the modal
   *
   * @param {object} author
   * The author to display the name of the author in the modal
   *
   * @param {string} previewUrl
   * The preview URL to generate the preview
   *
   * @param {string} size
   * The size of the preview. Possible values are xs, sm, md, lg and xl and original. Default: md.
   *
   * @requires $rootScope
   * @requires attachmentDetailsModalService
   */
  function previewList() {
    return {
      restrict: 'E',
      templateUrl: 'app/commons/ui/components/preview-list/preview-list.html',
      scope: {},
      bindings: {
        attachments: '<',
        groupId: '<',
        backendUrl: '<',
        author: '<',
        previewUrl: '<',
        size: '@?',
        sameSize: '='
      },
      controller: 'PreviewListController',
      controllerAs: '$ctrl'
    };
  }

  function PreviewListController($rootScope, attachmentDetailsModalService, CapabilitiesModel, coyoEndpoints,
                                 FileModel, fileDetailsModalService) {
    var vm = this;
    vm.imageSize = vm.size || 'xl';
    vm.showDetails = showDetails;
    vm.getPreviewUrl = getPreviewUrl;
    vm.getGroupId = getGroupId;

    angular.forEach(vm.attachments, function (attachment) {
      CapabilitiesModel.imgAvailable(attachment.contentType).then(function (available) {
        attachment.previewAvailable = available;
      });
      $rootScope.$on('previewNotAvailable', function (e, id) {
        if (attachment.id === id && attachment.previewAvailable) {
          attachment.previewAvailable = false;
        }
      });
    });

    // show attachment details
    function showDetails(attachment) {
      if (attachment.fileLibraryAttachment) {
        fileDetailsModalService.open(attachment.senderId, attachment.id, true);
      } else {
        openModal(attachment);
      }
    }

    function openModal(attachment) {
      attachmentDetailsModalService.open(getGroupId(attachment),
          attachment,
          vm.backendUrl,
          vm.author,
          getPreviewUrl(attachment));
    }

    function getGroupId(file) {
      if (file.fileLibraryAttachment) {
        return file.senderId;
      } else {
        return vm.groupId;
      }
    }

    function getPreviewUrl(file) {
      if (file.fileLibraryAttachment) {
        return coyoEndpoints.sender.preview;
      } else {
        return vm.previewUrl;
      }
    }

  }
})();

(function (angular) {
  'use strict';

  angular
      .module('commons.ui')
      .component('oyocReconnectWarningBar', reconnectWarningBar())
      .controller('ReconnectWarningBarController', ReconnectWarningBarController);

  /**
   * @ngdoc directive
   * @name commons.ui.oyocReconnectWarningBar:oyocReconnectWarningBar
   * @element OWN
   * @restrict E
   * @scope
   *
   * @description
   * Renders the warning bar when websockets reconnect. The bar displays a button to try reconnect immediately.
   *
   * @requires $rootScope
   * @requires $scope
   * @requires $interval
   * @requires $sessionStorage
   * @requires $timeout
   * @requires $window
   * @requires socketService
   * @requires reconnectWarningModalService
   */
  function reconnectWarningBar() {
    return {
      templateUrl: 'app/commons/ui/components/reconnect-warning/reconnect-warning-bar.html',
      scope: {},
      controller: 'ReconnectWarningBarController',
      controllerAs: '$ctrl'
    };
  }

  function ReconnectWarningBarController($rootScope, $scope, $interval, $sessionStorage, $timeout, $window,
                                         socketService, reconnectWarningModalService) {
    var vm = this;

    /* The interval handling the reconnect countdown */
    var countdownTimer = null;

    /* The number of silent reconnect attempts before showing the warning bar */
    var silentAttempts = 2;

    /* The current reconnection attempt number */
    var attempt = null;

    vm.state = 'CONNECTED';
    vm.isSilent = true;
    vm.countdown = 0;

    vm.reload = reload;
    vm.reconnect = reconnect;

    function reconnect() {
      socketService.connect();
    }

    function reload() {
      $window.location.reload();
    }

    function _startCountdown(sleep) {
      _resetCountdown();
      vm.countdown = sleep / 1000;
      countdownTimer = $interval(function () {
        vm.countdown--;
      }, 1000, sleep / 1000);
    }

    function _resetCountdown() {
      vm.countdown = 0;
      if (countdownTimer) {
        $interval.cancel(countdownTimer);
        countdownTimer = null;
      }
    }

    function _setState(state) {
      return $timeout(function () {
        vm.state = state;
        _resetCountdown();
      });
    }

    (function _init() {
      $scope.$watch(function () {
        return _.get($sessionStorage, 'messagingSidebar.compact', false);
      }, function (newVal) {
        vm.compact = newVal;
      });

      var unsubscribeSleepFn = $rootScope.$on('socketService:sleep', function (event, sleep) {
        if (attempt !== null) { // ignore silent reconnect
          _setState('SLEEP').then(function () {
            if (attempt >= silentAttempts) {
              vm.isSilent = false;
            }
            if (!vm.isSilent) {
              _startCountdown(sleep);
            }
          });
        }
      });

      var unsubscribeOfflineFn = $rootScope.$on('socketService:offline', function () {
        _setState('OFFLINE').then(function () {
          vm.isSilent = true;
          reconnectWarningModalService.open().finally(function () {
            vm.isSilent = false;
          });
        });
      });

      var unsubscribeConnectingFn = $rootScope.$on('socketService:connecting', function (event, newAttempt) {
        _setState('CONNECTING').then(function () {
          attempt = newAttempt;
        });
      });

      var unsubscribeConnectedFn = $rootScope.$on('socketService:connected', function () {
        _setState('CONNECTED').then(function () {
          attempt = null;
          vm.isSilent = true;
        });
      });

      $scope.$on('$destroy', function () {
        unsubscribeSleepFn();
        unsubscribeOfflineFn();
        unsubscribeConnectingFn();
        unsubscribeConnectedFn();
      });
    })();
  }

})(angular);

