(function () {
  'use strict';

  angular
      .module('commons.ui')
      .directive('coyoRefresh', Refresh)
      .controller('RefreshController', RefreshController);

  /**
   * @ngdoc directive
   * @name commons.ui.coyoRefresh:coyoRefresh
   * @restrict E
   *
   * @description
   * Directive to refresh (=recompile) it's content whenever the object provided via the watch attribute is changed.
   * This is useful when a larger number of bindings need to be refreshed, but all the data changes at once
   * (e.g. though a backend call). Therefore, all the bindings inside this directive can be one-time bindings
   * that do not create their own watch, replacing them with a single watch expression.
   *
   * @param {string=} watch
   * The expression to be watched for changes. Will be compared on every digest cycle, so instead of complex objects
   * or collections it may be advisable to use the timestamp of the last modification.
   *
   * @param {string=} onRootScopeEvent
   * Event to watch on the $rootScope which triggers a refresh. If the attribute value starts with a single quote
   * character the attribute is evaluated as an expression against the current scope.
   *
   * @param {string=} onScopeEvent
   * Event to watch on the current $scope which triggers a refresh. If the attribute value starts with a single quote
   * character the attribute is evaluated as an expression against the current scope.
   *
   * @requires $scope
   * @requires $transclude
   * @requires $attrs
   * @requires $element
   * @requires $rootScope
   */
  function Refresh() {
    return {
      transclude: true,
      controller: 'RefreshController'
    };
  }

  function RefreshController($scope, $transclude, $attrs, $element, $rootScope, $parse) {
    var childScope;

    function refresh() {
      $element.empty();
      if (childScope) {
        childScope.$destroy();
        childScope = null;
      }
      $transclude(function (clone, newScope) {
        childScope = newScope;
        $element.append(clone);
      });
    }

    function evaluateAttribute(attribute) {
      if (attribute && attribute.length > 0 && attribute.indexOf('\'') === 0) {
        return $parse(attribute)($scope);
      }
      return attribute;
    }

    if (angular.isString($attrs.watch)) {
      $scope.$watch($attrs.watch, refresh);
    } else {
      refresh();
    }

    if (angular.isString($attrs.onRootScopeEvent)) {
      var unsubscribeRootFn = $rootScope.$on(evaluateAttribute($attrs.onRootScopeEvent), refresh);
      $scope.$on('$destroy', unsubscribeRootFn);
    }

    if (angular.isString($attrs.onScopeEvent)) {
      var unsubscribeFn = $scope.$on(evaluateAttribute($attrs.onScopeEvent), refresh);
      $scope.$on('$destroy', unsubscribeFn);
    }
  }

})();

(function () {
  'use strict';

  angular
      .module('commons.ui')
      .directive('coyoRenderValue', renderValue);

  /**
   * @ngdoc directive
   * @name commons.ui:ui
   * @element OWN
   * @restrict E
   * @scope
   *
   * @description
   * Renders a given value of a field using a template specified by the passed config.
   *
   * @param {object} field
   * The field to render. the field is injected into the templates scope.
   *
   * @param {*} value
   * The value to render of the field. How the value is rendered is specified by the given template.
   *
   * @param {object} config
   * The config object specifying the template and optionally a controller to use for rendering a field with a given
   * type.
   *
   * @param {string} config.templateUrl
   * The url to the template to use for rendering the value.
   *
   * @param {string=} config.controller
   * Name of a controller to use along with the template. The field is automatically injected to the scope of this
   * controller. The controller is available as $ctrl within the template.
   *
   * @requires $compile
   *
   */
  function renderValue($compile) {
    return {
      restrict: 'E',
      scope: {
        field: '<',
        value: '<',
        config: '<'
      },
      link: function (scope, elem) {
        var html;
        if (!scope.config || !scope.config.templateUrl) {
          html = '<div>{{value}}</div>';
        } else {
          html = '<div ng-include="\'' + scope.config.templateUrl + '\'"';
          if (scope.config.controller) {
            html += 'ng-controller="' + scope.config.controller + ' as $ctrl"';
          }
          html += '></div>';
        }
        elem.append($compile(html)(scope));
      }
    };
  }

})();

(function (angular) {
  'use strict';

  angular
      .module('commons.ui')
      .filter('replace', replace);

  /**
   * @ngdoc filter
   * @name commons.ui.replace:replace
   * @function
   *
   * @description
   * Replaces a string on the given input.
   *
   * @param {string} str The string.
   * @param {string} pattern The search pattern.
   * @param {string} replacement The replacement string.
   * @param {boolean} global Search the string globally.
   */
  function replace() {
    return function (str, pattern, replacement, global) {
      global = angular.isDefined(global) ? global : true;
      try {
        if (!angular.isString(str)) {
          return '';
        }
        return (str || '').replace(new RegExp(pattern, global ? 'g' : ''), function () {
          return replacement || '';
        });
      } catch (e) {
        return (str || '');
      }
    };
  }

})(angular);

