(function (angular) {
  'use strict';

  angular
      .module('commons.ui')
      .factory('sidebarService', sidebarService);

  /**
   * @ngdoc service
   * @name commons.ui.sidebarService
   *
   * @description
   * Can be used to open, close or toggle sidebars.
   *
   * Sidebars which want to be managed by this service have to be registered first.
   * Therefore, an API has to be exposed containing a name and the methods <em>'open'</em> and <em>'close'</em>.
   *
   * @requires $rootScope
   * @requires $log
   * @requires commons.mobile.mobileEventsService
   */
  function sidebarService($rootScope, $log, mobileEventsService) {
    var sidebars = {};
    var lastState;

    $rootScope.$on('backdrop:hidden', function () {
      closeAll();
    });

    return {
      register: register,
      open: open,
      close: close,
      closeAll: closeAll
    };

    /**
     * @ngdoc function
     * @name commons.ui.sidebarService#register
     * @methodOf commons.ui.sidebarService
     *
     * @description
     * Registers a new sidebar to be managed with this service.
     *
     * The sidebar has to expose an api containing a name and the methods <em>'open'</em> and <em>'close'</em>.
     *
     * If one of the methods or the name are undefined, an error is logged and no service is registered.
     *
     * @param {object} api The api exposed by the sidebar.
     */
    function register(api) {
      $log.debug('[sidebarService] Register sidebar with name "' + api.name + '"');
      if (api && api.name && api.open && api.close && api.isOpen) {
        sidebars[api.name] = api;
      } else {
        $log.error('[sidebarService] Error while registering new sidebar. API not sufficient.', api);
      }
    }

    /**
     * @ngdoc function
     * @name commons.ui.sidebarService#open
     * @methodOf commons.ui.sidebarService
     *
     * @description
     * Opens the sidebar with the given name. If no sidebar with this name could be found, an error is logged.
     *
     * @param {string} name The name of the sidebar that should be openend. This may not be undefined or empty.
     */
    function open(name) {
      if (sidebars[name]) {
        sidebars[name].open();
        _notifyMobileApp();
      } else {
        $log.error('[sidebarService] Error while opening sidebar. Sidebar with name "' + name + '" could not be found.');
      }
    }

    /**
     * @ngdoc function
     * @name commons.ui.sidebarService#close2
     * @methodOf commons.ui.sidebarService
     *
     * @description
     * Closes the sidebar with the given name. If no sidebar with this name could be found, an error is logged.
     *
     * @param {string} name The name of the sidebar that should be closed. This may not be undefined or empty.
     */
    function close(name) {
      if (sidebars[name]) {
        sidebars[name].close();
        _notifyMobileApp();
      } else {
        $log.error('[sidebarService] Error while closing sidebar. Sidebar with name "' + name + '" could not be found.');
      }
    }

    /**
     * @ngdoc function
     * @name commons.ui.sidebarService#closeAll
     * @methodOf commons.ui.sidebarService
     *
     * @description
     * Closes all registered and open sidebars.
     */
    function closeAll() {
      _.each(sidebars, function (sidebar) {
        close(sidebar.name);
      });
      _notifyMobileApp();
    }

    function _notifyMobileApp() {
      var state = _.includes(_.map(sidebars, function (sidebar) {
        return sidebar.isOpen();
      }), true);
      if (state !== lastState) {
        mobileEventsService.propagate('pull-to-refresh:sidebar:' + (state ? 'disabled' : 'enabled'));
        lastState = state;
      }
    }
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('commons.ui')
      .directive('coyoStr2Int', str2int);

  /**
   * @ngdoc directive
   * @name commons.ui.str2int:str2int
   * @restrict 'A'
   * @scope
   *
   * @description
   * Convert strings to numeric values to simplify binding of a non-string value via ngModel parsing / formatting.
   *
   * @see https://code.angularjs.org/1.4.7/docs/api/ng/directive/select
   *
   * @example
   * <select ng-model="model.id" str-to-int>
   *   <option value="0">Zero</option>
   *   <option value="1">One</option>
   *   <option value="2">Two</option>
   * </select>
   */
  function str2int() {
    return {
      require: 'ngModel',
      link: function (scope, element, attrs, ngModel) {
        ngModel.$parsers.push(function (val) {
          var int = parseInt(val, 10);
          return isNaN(int) ? null : int;
        });
        ngModel.$formatters.push(function (val) {
          return val ? '' + val : null;
        });
      }
    };
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('commons.ui')
      .directive('coyoTablePagination', tablePagination)
      .controller('TablePaginationController', TablePaginationController);

  /**
   * @ngdoc directive
   * @name commons.ui.coyoTablePagination:coyoTablePagination
   * @restrict 'E'
   *
   * @description
   * Renders a pagination element backed by a mindsmash.resource.Page using ui-bootstrap.
   *
   * @param {object} page the current page
   * @param {function=} onChange an optional update function that is called when the current page is changed
   *
   * @requires $attrs
   */
  function tablePagination() {
    return {
      replace: true,
      restrict: 'E',
      templateUrl: 'app/commons/ui/components/table/table-pagination.html',
      scope: {},
      bindToController: {
        page: '<',
        onChange: '&'
      },
      controller: 'TablePaginationController',
      controllerAs: '$ctrl'
    };
  }

  function TablePaginationController($attrs) {
    var vm = this;
    vm.loading = false;

    // translate between zero and one based index
    Object.defineProperty(vm, 'currentPage', {
      get: function () {
        return vm.page._queryParams._page + 1;
      },
      set: function (page) {
        vm.page._queryParams._page = page - 1;
      }
    });

    vm.change = function () {
      var hasOnChange = angular.isDefined($attrs.onChange);
      if (vm.loading || (!hasOnChange && !vm.page.page)) {
        return;
      }

      vm.loading = true;
      (hasOnChange ? vm.onChange(vm.page) : vm.page.page(vm.page._queryParams._page)).finally(function () {
        vm.loading = false;
      });
    };
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('commons.ui')
      .directive('coyoTableSorting', tableSorting)
      .controller('TableSortingController', TableSortingController);

  /**
   * @ngdoc directive
   * @name commons.ui.coyoTableSorting:coyoTableSorting
   * @restrict 'A'
   *
   * @description
   * Renders a table column header with sorting links and indicator icons.
   *
   * Tables must be backed by mindsmash.resource.Page.
   *
   * Note: Must be used as attribute directive as otherwise the elements end up in the wrong place in the DOM.
   *
   * @requires lodash
   *
   * @param {object} page The mindsmash.resource.Page
   * @param {string} property The property to sort by
   * @param {string} title Text content for the th element
   */
  function tableSorting() {
    return {
      replace: true,
      restrict: 'A',
      templateUrl: 'app/commons/ui/components/table/table-sorting.html',
      scope: {},
      bindToController: {
        page: '=',
        property: '@',
        title: '@'
      },
      controller: 'TableSortingController',
      controllerAs: '$ctrl'
    };
  }

  function TableSortingController() {
    var vm = this;

    vm.sort = sort;
    vm.isDirection = isDirection;

    function sort() {
      var orderBy = vm.property + ',' + (vm.isDirection('ASC') ? 'desc' : 'asc');
      vm.page.sort(orderBy);
    }

    function isDirection(direction) {
      var sorting = _.get(vm, 'page.sorting');
      if (!sorting) {
        return false;
      }
      return _.join(_.map(sorting, 'property'), ',') === vm.property && sorting[0].direction === direction;
    }
  }

})(angular);

