(function (angular) {
  'use strict';

  angular
      .module('coyo.pages')
      .directive('coyoCheckPageName', checkPageName);

  /**
   * @ngdoc directive
   * @name coyo.pages.coyoCheckPageName:coyoCheckPageName
   * @element A
   *
   * @description
   * Validates a page name.
   *
   * @example
   * <example module="example">
   *   <file name="index.html">
   *     <div ng-controller="ExampleCtrl">
   *       <input type="text" coyo-check-page-name>
   *
   *       <input type="text" coyo-check-page-name="pageId">
   *     </div>
   *   </file>
   *   <file name="script.js">
   *     angular
   *       .module('example', [])
   *       .controller('ExampleCtrl', ExampleCtrl);
   *
   *     function ExampleCtrl($scope) {
   *       $scope.pageId = 'PageId';
   *     }
   *   </file>
   * </example>
   */
  function checkPageName($http, $q, $window, coyoEndpoints) {
    return {
      restrict: 'A',
      require: 'ngModel',
      link: function ($scope, element, attrs, ngModel) {
        ngModel.$asyncValidators.pageName = function (modelValue) {
          if (modelValue) {
            return $http({
              method: 'GET',
              url: coyoEndpoints.page.checkName.replace('{name}', $window.encodeURIComponent(modelValue))
                  .replace('{pageId}', attrs.coyoCheckPageName)
            }).then(function (r) {
              return r.data.taken ? $q.reject() : true;
            });
          } else {
            return $q.resolve(true);
          }
        };
      }
    };
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.pages')
      .directive('coyoPageAvatar', pageAvatar)
      .controller('PageAvatarController', PageAvatarController);

  /**
   * @ngdoc directive
   * @name coyo.pages.coyoPageAvatar:coyoPageAvatar
   * @restrict 'E'
   * @element OWN
   *
   * @description
   * Displays a page avatar.
   *
   * @param {object} page
   * The page to render the avatar for.
   *
   * @param {boolean=} noLink
   * Can be set to 'true' if the avatar image should not link to the page itself (default: 'false').
   *
   * @param {string=} avatarSize
   * Desired size of the avatar. Possible values are xs, sm, md, lg and xl.
   *
   * @requires $scope
   * @requires $state
   * @requires commons.target.targetService
   */
  function pageAvatar() {
    return {
      restrict: 'E',
      replace: true,
      templateUrl: 'app/modules/pages/components/page-avatar/page-avatar.html',
      scope: {},
      bindToController: {
        page: '=',
        noLink: '<?',
        avatarSize: '@?'
      },
      controller: 'PageAvatarController',
      controllerAs: '$ctrl'
    };
  }

  function PageAvatarController($scope, $state, targetService) {
    var vm = this;
    var canLinkToPage = false;

    vm.open = open;
    vm.errorHandler = errorHandler;

    function open(event) {
      if (vm.isLink) {
        event.stopPropagation();
        $state.go('main.page.show', {idOrSlug: vm.page.slug}, {inherit: false, reload: 'main.page.show'});
      }
    }

    function errorHandler() {
      vm.loadError = true;
    }

    (function _init() {
      // check whether user has permission to link to page
      targetService.onCanLinkTo(vm.page.target, function (canLink) {
        canLinkToPage = canLink;
        vm.isLink = !vm.noLink && canLinkToPage;
      });

      // check whether no link parameter is set or changed
      $scope.$watch(function () {
        return vm.noLink;
      }, function (newValue, oldValue) {
        if (newValue !== oldValue) {
          vm.isLink = !newValue && canLinkToPage;
        }
      });
    })();
  }

})(angular);

(function (angular) {
  'use strict';

  var component = {
    templateUrl: 'app/modules/pages/components/page-subscribe/page-subscribe.html',
    bindings: {
      page: '<',
      size: '@',
      darkTheme: '<?'
    },
    controller: 'PageSubscribeController'
  };

  /**
   * @ngdoc directive
   * @name coyo.pages.coyoPageSubscribe:coyoPageSubscribe
   * @restrict 'E'
   * @element OWN
   *
   * @description
   * Renders a page's subscribe/unsubscribe button.
   *
   * @requires $timeout
   * @requires commons.auth.authService
   *
   * @param {object} page The page
   * @param {string} size The size
   * @param {boolean=} darkTheme Whether to use a dark theme or not. Default false.
   */
  angular
      .module('coyo.pages')
      .component('coyoPageSubscribe', component)
      .controller('PageSubscribeController', PageSubscribeController);

  function PageSubscribeController($timeout, $scope, authService, subscriptionsService) {
    var vm = this;

    vm.loading = false;
    vm.subscribed = false;
    vm.autoSubscribe = false;

    vm.toggle = toggle;
    vm.$onInit = init;

    /* --- INIT --- */

    function init() {
      vm.show = false;
      authService.getUser().then(function (user) {
        vm.currentUser = user;

        vm.loading = true;
        var eventHandler = subscriptionsService.onSubscriptionChange(vm.currentUser.id, vm.page.id, _updateSubscription);
        $scope.$on('$destroy', eventHandler);

        $timeout(function () {
          vm.show = true;
        });
      });
    }

    /* --- PUBLIC METHODS --- */

    function toggle(event) {
      if (vm.loading) {
        return;
      }
      vm.loading = true;
      event.stopPropagation();

      if (vm.subscribed) {
        _unsubscribe();
      } else {
        _subscribe();
      }
    }

    /* --- PRIVATE METHODS --- */

    function _subscribe() {
      subscriptionsService.subscribe(vm.currentUser.id, vm.page.id, 'page', vm.page.id).then(function () {
        ++vm.page.userSubscriptionsCount;
      });
    }

    function _unsubscribe() {
      subscriptionsService.unsubscribe(vm.currentUser.id, vm.page.id).then(function () {
        --vm.page.userSubscriptionsCount;
      });
    }

    function _updateSubscription(subscription) {
      vm.subscribed = angular.isDefined(subscription);
      vm.autoSubscribe = vm.subscribed && _.get(subscription, 'autoSubscribe', false);
      vm.loading = false;
    }
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.pages')
      .directive('coyoSelectPageCategory', selectPageCategory);

  function selectPageCategory(selectFactoryModel, PageCategoryModel, Pageable) {
    return selectFactoryModel({
      refresh: _refresh,
      emptyText: 'PAGE.NO_CATEGORIES_FOUND',
      multiple: true
    });

    function _refresh(pageable, search) {
      var paramPageable = new Pageable(pageable.page, pageable.size);
      var paramSearch = {name: search};
      return PageCategoryModel.pagedQuery(paramPageable, paramSearch).then(function (response) {
        return angular.extend(_.map(response.content, function (category) {
          return {
            id: category.id,
            displayName: category.name
          };
        }), {
          meta: {
            last: response.last,
            number: response.number
          }
        });
      });
    }
  }

})(angular);

