(function (angular) {
  'use strict';

  angular
      .module('coyo.timeline')
      .controller('TimelineFormController', TimelineFormController)
      .controller('SelectStickyModalController', SelectStickyModalController)
      .controller('RestrictionSelectionModalController', RestrictionSelectionModalController)
      .controller('SelectAttachmentModalController', SelectAttachmentModalController);

  function TimelineFormController($scope, $rootScope, $log, $translate, $sce, $element, TimelineItemModel, authService,
                                  tempUploadService, webPreviewService, oembedVideoService, utilService, modalService) {
    var vm = this;
    var inModal = angular.isDefined($scope.vm);
    var POST_AS_TRANSLATION = $translate.instant('MODULE.TIMELINE.FORM.POST_AS');

    vm.screenSize = $rootScope.screenSize;
    vm.staticSenderOptions = [];
    vm.uuid = utilService.uuid();

    vm.stickyExpiryOptions = [
      {expiry: 1000 * 60 * 60 * 24, label: 'MODULE.TIMELINE.STICKY.EXPIRY.ONE_DAY'},
      {expiry: 1000 * 60 * 60 * 24 * 7, label: 'MODULE.TIMELINE.STICKY.EXPIRY.ONE_WEEK'},
      {expiry: 1000 * 60 * 60 * 24 * 30, label: 'MODULE.TIMELINE.STICKY.EXPIRY.ONE_MONTH'},
      {expiry: 1000 * 60 * 60 * 24 * 365, label: 'MODULE.TIMELINE.STICKY.EXPIRY.ONE_YEAR'}
    ];

    vm.restrictionOptions = [
      {restricted: true, label: 'YES', icon: 'zmdi-lock'},
      {restricted: false, label: 'NO', icon: 'zmdi-lock-open'}
    ];

    vm.$onInit = init;
    vm.save = save;
    vm.openModalSelectSticky = openModalSelectSticky;
    vm.openRestrictionSelectionModal = openRestrictionSelectionModal;
    vm.onBlur = onBlur;
    vm.onKeyDown = onKeyDown;
    vm.fetchWebPreviews = fetchWebPreviews;
    vm.findNewUrls = findNewUrls;
    vm.addAttachments = addAttachments;
    vm.selectSenderOptionGroups = selectSenderOptionGroups;
    vm.openModalSelectAttachments = openModalSelectAttachments;
    vm.addFileLibraryAttachments = addFileLibraryAttachments;

    // --------------------------------------------------------------------------------------------------------

    function init() {
      if (!vm.contextId) {
        throw new Error('[TimelineFormController] Context ID required but not found.');
      }

      if (!vm.timelineType) {
        throw new Error('[TimelineFormController] Timeline type required but not found.');
      }


      // load current user
      authService.getUser().then(function (user) {
        vm.currentUser = user;
        _reset();
      });

      authService.onGlobalPermissions('ACT_AS_SENDER', function (canActAsSender) {
        vm.canActAsSender = canActAsSender;
      });
    }

    function _reset() {
      vm.newItemAttachments = [];
      vm.newItemLinkPreviews = [];
      vm.newItemVideoPreviews = [];
      vm.failedWebPreviews = [];
      vm.author = vm.currentUser;
      vm.currentUser.typeLabelTranslated = $translate.instant('MODULE.TIMELINE.FORM.POST_AS_CURRENT_USER_SUBLINE');
      vm.staticSenderOptions = [vm.currentUser];
      vm.newItemModel = new TimelineItemModel({
        authorId: vm.currentUser.id,
        type: 'post',
        restricted: false,
        data: {
          message: ''
        }
      });
      vm.stickyExpiry = null;

      if (vm.form) {
        vm.form.$setPristine();
        vm.form.$setUntouched();
      }
    }

    // --------------------------------------------------------------------------------------------------------

    function save() {
      if (vm.form.$valid && _.filter(vm.newItemAttachments, {uploading: true}).length === 0) {
        vm.saving = true;

        vm.newItemModel.authorId = vm.author ? vm.author.id : vm.currentUser.id;
        vm.newItemModel.recipientIds = [vm.timelineType === 'personal' ? vm.author.id : vm.contextId];
        vm.newItemModel.stickyExpiry = vm.stickyExpiry ? new Date().getTime() + vm.stickyExpiry : null;
        vm.newItemModel.attachments = _.map(vm.newItemAttachments, function (attachment) {
          return {
            name: attachment.name,
            uid: attachment.uid,
            contentType: attachment.contentType
          };
        });

        vm.newItemModel.linkPreviews = vm.newItemLinkPreviews;
        vm.newItemModel.videoPreviews = vm.newItemVideoPreviews;
        webPreviewService.cancelGenerateRequest('user submitted form');

        return vm.newItemModel.create().then(_reset).finally(function () {
          vm.saving = false;

          if (inModal) {
            $scope.$dismiss('cancel'); //eslint-disable-line
          }
        });
      } else {
        return null;
      }
    }

    function addFileLibraryAttachments(files) {
      vm.newItemModel.fileLibraryAttachments = _.unionBy(vm.newItemModel.fileLibraryAttachments,
          files, 'fileId');

      vm.focusMessageFormField = true;
    }

    /**
     * Opens the modal for restriction selection
     */
    function openRestrictionSelectionModal() {
      modalService.open({
        templateUrl: 'app/modules/timeline/components/timeline-form/timeline-form-modal-select-restriction.html',
        controller: 'RestrictionSelectionModalController',
        controllerAs: '$ctrl',
        resolve: {
          restrictionOptions: function () {
            return vm.restrictionOptions;
          },
          restricted: function () {
            return vm.restricted;
          }
        }
      }).result.then(function (selection) {
        vm.restricted = selection;
      });
    }

    /**
     * Opens the modal for marking the post as sticky
     */
    function openModalSelectSticky() {
      modalService.open({
        templateUrl: 'app/modules/timeline/components/timeline-form/timeline-form-modal-select-sticky.html',
        controller: 'SelectStickyModalController',
        controllerAs: '$ctrl',
        resolve: {
          stickyExpiryOptions: function () {
            return vm.stickyExpiryOptions;
          },
          stickyExpiry: function () {
            return vm.stickyExpiry;
          }
        }
      }).result.then(function (selection) {
        vm.stickyExpiry = selection;
      });
    }

    /**
     * Opens the modal for adding attachments
     */
    function openModalSelectAttachments() {
      modalService.open({
        templateUrl: 'app/modules/timeline/components/timeline-form/timeline-form-modal-select-attachment.html',
        controller: 'SelectAttachmentModalController',
        controllerAs: '$ctrl',
        resolve: {
          context: function () {
            return vm.timelineType === 'personal' ? vm.author : {id: vm.contextId};
          }
        }
      }).result.then(function (result) {
        if (result.attachments) {
          addAttachments(result.attachments);
        }
        if (result.fileLibraryAttachments) {
          vm.newItemModel.fileLibraryAttachments = _.unionBy(vm.newItemModel.fileLibraryAttachments,
              result.fileLibraryAttachments, 'fileId');
        }
      });
    }

    function onBlur() {
      vm.focusMessageFormField = false;
      if (!_.isUndefined(vm.newItemModel.data.message)) {
        vm.fetchWebPreviews(vm.newItemModel.data.message);
      }
    }

    function onKeyDown($event) {
      if ($event.ctrlKey && $event.keyCode === 13) {
        $event.preventDefault();
        vm.save();
      } else if ($event.keyCode === 32 || $event.keyCode === 13 || $event.tab) {
        if (!_.isUndefined(vm.newItemModel.data.message)) {
          vm.fetchWebPreviews(vm.newItemModel.data.message);
        }
      }
    }

    function fetchWebPreviews(message) {
      if (!_.isEmpty(message)) {
        var newUrls = findNewUrls(message);

        if (!_.isEmpty(newUrls)) {
          webPreviewService.generateWebPreviews(newUrls).then(function (webPreviews) {
            addWebPreviews(newUrls, webPreviews);
          }).catch(function (error) {
            $log.error('An error occurred while retrieving the link preview image ', error);
          });
        }
      }
    }

    function findNewUrls(message) {
      var newUrls = new Array();

      _.forEach(webPreviewService.extractUrls(message), function (url) {
        if (!_.find(vm.newItemLinkPreviews, ['url', url]) && !_.includes(vm.failedWebPreviews, url)) {
          newUrls.push(url);
        }
      });

      return newUrls;
    }

    function addWebPreviews(urls, webPreviews) {
      angular.forEach(updatePositions(webPreviews), function (webPreview) {
        if (_.isEqual(webPreview.type, 'LINK')) {
          vm.newItemLinkPreviews.push(webPreview);
        } else if (_.isEqual(webPreview.type, 'VIDEO')) {
          var videoElement = getEmbedCode(webPreview);
          if (!_.isUndefined(videoElement)) {
            webPreview.embedCode = videoElement;
          }
          vm.newItemVideoPreviews.push(webPreview);
        }
      });

      /* failed previews are apparently those for which there is no matching url */
      angular.forEach(urls, function (url) {
        if (!_.find(vm.newItemLinkPreviews, ['url', url])) {
          vm.failedWebPreviews.push(url);
        }
      });

      vm.focusMessageFormField = true;
    }

    function updatePositions(linkPreviews) {
      var lpWithHighestPosition = _.maxBy(vm.newItemLinkPreviews, 'position');
      var nextHighestPosition = _.isUndefined(lpWithHighestPosition) ? 0 : lpWithHighestPosition.position + 1;

      angular.forEach(linkPreviews, function (linkPreview) {
        if (nextHighestPosition !== 0 && nextHighestPosition >= linkPreview.position) {
          linkPreview.position = linkPreview.position + nextHighestPosition;
        }
      });

      return linkPreviews;
    }

    function getEmbedCode(webPreview) {
      var videoElement = oembedVideoService.createEmbedCode(webPreview, $element[0]);
      if (videoElement) {
        return {
          url: webPreview.videoUrl,
          html: $sce.trustAsHtml(videoElement.prop('outerHTML')),
          heightPercentage: oembedVideoService.getHeightPercentage(videoElement)
        };
      }
      return undefined;
    }

    function addAttachments(files) {
      vm.newItemAttachments.push.apply(vm.newItemAttachments, files);

      angular.forEach(files, function (file) {
        tempUploadService.upload(file, 300).then(function (blob) {
          angular.extend(file, blob);
        }).catch(function () {
          vm.attachments = _.reject(vm.attachments, file);
        });
      });

      vm.focusMessageFormField = true;
    }

    function selectSenderOptionGroups(option) {
      return vm.staticSenderOptions.indexOf(option) > -1 ? POST_AS_TRANSLATION : undefined;
    }
  }

  function SelectStickyModalController($uibModalInstance, stickyExpiryOptions, stickyExpiry) {
    var vm = this;

    vm.stickyExpiryOptions = stickyExpiryOptions;
    vm.stickyExpiry = stickyExpiry;

    vm.setExpiryDate = setExpiryDate;

    /**
     * Returns the value for the expiry date and closes the modal.
     *
     * @param {number} stickyExpiry The sticky expiry date
     */
    function setExpiryDate(stickyExpiry) {
      vm.stickyExpiry = stickyExpiry;
      $uibModalInstance.close(vm.stickyExpiry);
    }
  }

  function RestrictionSelectionModalController($uibModalInstance, restrictionOptions, restricted) {
    var vm = this;

    vm.restrictionOptions = restrictionOptions;
    vm.restricted = restricted;

    vm.setRestriction = setRestriction;

    /**
     * Returns the value for the restriction and closes the modal.
     *
     * @param {number} restricted post restriction state
     */
    function setRestriction(restricted) {
      vm.restricted = restricted;
      $uibModalInstance.close(vm.restricted);
    }
  }

  function SelectAttachmentModalController(fileLibraryModalService, $uibModalInstance, context) {
    var vm = this;
    vm.context = context;

    vm.openFileLibrary = openFileLibrary;
    vm.addAttachments = addAttachments;

    function openFileLibrary() {
      fileLibraryModalService.open(vm.context,
          {selectMode: 'multiple'})
          .then(function (selectedFiles) {
            $uibModalInstance.close({
              fileLibraryAttachments: _.map(selectedFiles, function (elem) {
                return {
                  fileId: elem.id,
                  senderId: elem.senderId,
                  displayName: elem.displayName
                };
              })
            });
          });
    }

    function addAttachments(files) {
      $uibModalInstance.close({attachments: files});
    }

  }

})(angular);

(function () {
  'use strict';

  angular.module('coyo.timeline')
      .factory('timelineEditModalService', timelineEditModalService)
      .controller('TimelineEditModalController', TimelineEditModalController);

  /**
   * @ngdoc service
   * @name coyo.timeline.timelineEditModalService
   *
   * @description
   * Opens a modal with a textarea to edit a timeline post. Timeline posts can only be edited if they have no comments
   * or likes and if the current user is the author.
   *
   * @requires modalService
   */
  function timelineEditModalService(modalService) {
    return {
      open: open
    };

    /**
     * @ngdoc method
     * @name coyo.timeline.timelineEditModalService#open
     * @methodOf coyo.timeline.timelineEditModalService
     *
     * @description
     * Opens the modal to edit the given timeline item. Fetches a new up-to-date version of the item and works on it
     * - so the original item is only affected when the user hits the save button.
     *
     * @param {object} item
     * The timeline item to edit the text of.
     *
     * @param {object} item.data
     * The timeline items data object which contains the message to edit.
     *
     * @param {String} item.data.message
     * The message to edit. Must not be undefined.
     *
     * @returns {object} A promise which is called after the message was updated successfully.
     */
    function open(item) {
      return modalService.open({
        templateUrl: 'app/modules/timeline/components/timeline-edit/timeline-edit-modal.html',
        controller: 'TimelineEditModalController',
        resolve: {
          item: /*@ngInject*/ function (TimelineItemModel) {
            // resolve a fresh copy of the item
            return new TimelineItemModel({id: item.id}).getWithPermissions(false, {}, ['delete', 'manage', 'accessoriginalauthor', 'like', 'comment', 'share']);
          }
        }
      }).result;
    }

  }

  function TimelineEditModalController($uibModalInstance, oembedVideoService, $sce, item) {
    var vm = this;

    vm.item = item;
    vm.error = !item._permissions.manage;

    vm.save = save;

    function save() {
      _.set(vm.item, 'data.edited', true);
      vm.item.update().then(function (updatedItem) {
        $uibModalInstance.close(updatedItem);

        _.forEach(updatedItem.videoPreviews, function (videoPreview) {
          var videoElement = oembedVideoService.createEmbedCode(videoPreview, angular.element('<div>'));
          if (videoElement) {
            videoPreview.embedCode = {
              url: videoPreview.videoUrl,
              html: $sce.trustAsHtml(videoElement.prop('outerHTML')),
              heightPercentage: oembedVideoService.getHeightPercentage(videoElement)
            };
          }
        });
      }).catch(function () {
        vm.error = true;
      });
    }

  }

})();

(function (angular) {
  'use strict';

  angular
      .module('coyo.timeline')
      .directive('coyoTimelineItem', timelineItem)
      .controller('TimelineItemDirectiveController', TimelineItemDirectiveController);

  /**
   * @ngdoc directive
   * @name coyo.timeline.coyoTimelineItem:coyoTimelineItem
   * @restrict 'E'
   * @element OWN
   *
   * @description
   * Renders a timeline item.
   *
   * @param {object}  ngModel The timeline item
   * @param {string}  contextSenders Optionally, the ID of the context senders, that determine if a post is displayed as a share.
   * @param {boolean} isNew Show the new ribbon
   */
  function timelineItem() {
    return {
      restrict: 'E',
      replace: true,
      templateUrl: 'app/modules/timeline/components/timeline-item/timeline-item.html',
      scope: {},
      bindToController: {
        ngModel: '<',
        contextSenders: '@',
        isNew: '<'
      },
      controller: 'TimelineItemDirectiveController',
      controllerAs: 'tlItem'
    };
  }

  function TimelineItemDirectiveController($scope, authService, backendUrlService, coyoEndpoints,
                                           reportService, timelineEditModalService, $translate, socketService) {
    var vm = this,
        TRANSLATION_POST_AS = $translate.instant('MODULE.TIMELINE.FORM.LIKE_COMMENT_AS');

    vm.backendUrl = backendUrlService.getUrl();
    vm.preview = coyoEndpoints.timeline.preview;
    vm.tplPrefix = 'app/modules/timeline/components/timeline-item/types/';
    vm.showOriginalAuthor = false;
    vm.originalAuthor = null;
    vm.hasShares = false;

    vm.edit = edit;
    vm.report = _.partial(reportService.report, vm.ngModel.id, vm.ngModel.typeName);
    vm.remove = remove;
    vm.markAsRead = markAsRead;
    vm.toggleOriginalAuthor = toggleOriginalAuthor;
    vm.getTooltip = getTooltip;
    vm.selectSenderOptionGroups = selectSenderOptionGroups;
    vm.$onInit = init;

    function remove() {
      vm.ngModel.remove();
    }

    function edit() {
      timelineEditModalService.open(vm.ngModel).then(function (updatedItem) {
        angular.extend(vm.ngModel, updatedItem);
      });
    }

    function markAsRead() {
      vm.loadingMarkAsRead = true;
      vm.ngModel.markAsRead().then(function (item) {
        vm.ngModel.unread = item.unread;
      }).finally(function () {
        vm.loadingMarkAsRead = false;
      });
    }

    function toggleOriginalAuthor() {
      if (!vm.originalAuthor) {
        vm.ngModel.getOriginalAuthor().then(function (originalAuthor) {
          vm.originalAuthor = originalAuthor;
          vm.showOriginalAuthor = !vm.showOriginalAuthor;
        });
      } else {
        vm.showOriginalAuthor = !vm.showOriginalAuthor;
      }
    }

    function getTooltip(recipient) {
      return $translate.instant('MODULE.TIMELINE.TOOLTIP.' + recipient.typeName.toUpperCase(),
          {displayName: recipient.displayName}, null, null, null);
    }

    function onUpdate(event) {
      $scope.$apply(function () {
        vm.ngModel.data = event.content.data;
        vm.ngModel.modified = event.content.modified;
        $scope.$broadcast('timeline-item:refresh');
      });
    }

    function selectSenderOptionGroups() {
      return TRANSLATION_POST_AS;
    }

    function init() {
      authService.getUser().then(function (user) {
        vm.currentUser = user;
        vm.author = angular.copy(user);
        vm.staticSenderOptions = [user];
      });

      // calculate share information for header display
      $scope.$watch(function () {
        return vm.ngModel.shares;
      }, function (newVal) {
        vm.hasShares = (newVal !== null) && (newVal.length > 0);
        vm.publicShare = _.includes(_.map(newVal, function (s) {
          return s.recipient !== null ? s.recipient.public : false;
        }), true);
        vm.share = _.chain(newVal).filter(function (share) {
          return share.recipient && (_.includes(vm.contextSenders, share.recipient.id) || vm.contextSenders === null);
        }).orderBy(['created'], ['desc']).head().value();
      });

      // calculate recipients for header display
      $scope.$watch(function () {
        return vm.ngModel.recipients;
      }, function (newVal) {
        var authorId = vm.ngModel.author && vm.ngModel.author.id;
        vm.recipients = _.chain(newVal)
            .reject({id: authorId})
            .slice(0, 3).value();
      });

      // watch global permissions
      authService.onGlobalPermissions('CREATE_REPORTS', function (canCreateReports) {
        vm.canCreateReports = canCreateReports;
        $scope.$broadcast('timeline-item:refresh');
      });
      authService.onGlobalPermissions('DELETE_TIMELINE_ITEM', function (canDeletePosts) {
        vm.canDeletePost = canDeletePosts && vm.ngModel._permissions.delete;
        $scope.$broadcast('timeline-item:refresh');
      });
      authService.onGlobalPermissions('ACT_AS_SENDER', function (canActAsSender) {
        vm.selectSenderDisplayed = canActAsSender;
        $scope.$broadcast('timeline-item:refresh');
      });

      var unsubscribeUpdateFn = socketService.subscribe('/topic/timeline.item.' + vm.ngModel.id, onUpdate, 'updated');
      $scope.$on('$destroy', unsubscribeUpdateFn);
    }
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.timeline')
      .directive('coyoTimelineShare', timelineShare)
      .controller('TimelineShareDirectiveController', TimelineShareDirectiveController);

  /**
   * @ngdoc directive
   * @name coyo.timeline.coyoTimelineShare:coyoTimelineShare
   * @restrict 'E'
   * @element OWN
   *
   * @description
   * Renders a timeline share.
   *
   * @param {object} ngModel The timeline share
   */
  function timelineShare() {
    return {
      restrict: 'E',
      replace: true,
      templateUrl: 'app/modules/timeline/components/timeline-share/timeline-share.html',
      scope: {},
      bindToController: {
        accessOriginalAuthor: '<',
        ngModel: '<'
      },
      controller: 'TimelineShareDirectiveController',
      controllerAs: 'tlShare'
    };
  }

  function TimelineShareDirectiveController($scope, TimelineShareModel, authService) {
    var vm = this;
    vm.originalAuthor = false;

    vm.$onInit = init;
    vm.toggleOriginalAuthor = toggleOriginalAuthor;

    function init() {
      var unregister = $scope.$watch(function () {
        return vm.ngModel;
      }, function () {
        setHeadline();
        $scope.$broadcast('timelineShare:refresh');
      });

      $scope.$on('$destroy', unregister);
      setHeadline();
    }

    // load current user and determine headline
    function setHeadline() {
      authService.getUser().then(function (user) {
        vm.currentUser = user;

        var authorPart = 'NONE';
        if (vm.ngModel.author) {
          authorPart = vm.ngModel.author.id === user.id ? 'YOU' : 'OTHER';
        }

        var recipientPart = 'NONE';
        if (vm.ngModel.recipient) {
          recipientPart = vm.ngModel.recipient.id === user.id ? 'YOU' : 'OTHER';
        }

        vm.headline = 'MODULE.TIMELINE.SHARE.HEADLINE.' + authorPart + '_WITH_' + recipientPart;
        vm.headlineSuffix = vm.headline + '.SUFFIX';
      });
    }

    function toggleOriginalAuthor() {
      if (!vm.originalAuthor) {
        new TimelineShareModel(vm.ngModel).getOriginalAuthor().then(function (originalAuthor) {
          vm.originalAuthor = originalAuthor;
          vm.showOriginalAuthor = !vm.showOriginalAuthor;
        });
      } else {
        vm.showOriginalAuthor = !vm.showOriginalAuthor;
      }
    }
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.timeline')
      .directive('coyoTimelineStream', timelineStream)
      .controller('TimelineStreamDirectiveController', TimelineStreamDirectiveController);

  /**
   * @ngdoc directive
   * @name coyo.timeline.coyoTimelineStream:coyoTimelineStream
   * @restrict 'E'
   * @element OWN
   *
   * @description
   * Creates a stream of timeline items (i.e. posts).
   *
   * @param {string} type The type of stream to render
   * @param {string} contextId The ID of the sender from whose perspective the stream should be rendered (the context)
   */
  function timelineStream() {
    return {
      restrict: 'E',
      replace: true,
      templateUrl: 'app/modules/timeline/components/timeline-stream/timeline-stream.html',
      scope: {},
      bindToController: {
        type: '@',
        contextId: '@',
        canPost: '<'
      },
      controller: 'TimelineStreamDirectiveController',
      controllerAs: '$ctrl'
    };
  }

  function TimelineStreamDirectiveController($scope, $filter, TimelineItemModel, Pageable, socketService, authService, modalService, subscriptionsService, errorService) {
    var vm = this;

    var pageSize = 8;
    var pageOffset = 0;
    var currentUser;
    var currentPage;
    var newItemIds = [];
    var lastRefreshDate;

    vm.loadMore = loadMore;
    vm.loadNew = loadNew;
    vm.openFormModal = openFormModal;

    var permission = _isPersonal() ? 'ACCESS_PERSONAL_TIMELINE' : 'ACCESS_SENDER_TIMELINE';
    authService.onGlobalPermissions(permission, _init);
    $scope.$on('$destroy', function () {
      _unsubscribeItemsCreated();
      _unsubscribeItemsDeleted();
      _unsubscribeItemsShared();
    });

    // --------------------------------------------------------------------------------------------------------

    function loadMore() {
      if (vm.loading) {
        return;
      }

      if (!currentPage || !currentPage.last) {
        vm.loading = true;

        var offset = (currentPage ? currentPage.offset + pageSize : 0) + pageOffset;
        var pageable = new Pageable(null, pageSize, null, offset);
        var params = {type: vm.type, contextId: vm.contextId};
        TimelineItemModel.pagedQueryWithPermissions(pageable, params, {}, ['manage', 'delete', 'accessoriginalauthor', 'like', 'comment', 'share'])
            .then(function (page) {
              currentPage = page;
              currentPage.offset = offset;
              pageOffset = 0;
              vm.items = _setNew(_.concat(vm.items, page.content));
              vm.itemsLast = page.last;
              _.forEach(page.content, _subscribeItemDeleted);
            }).finally(function () {
              vm.loading = false;
            });
      }
    }

    function loadNew() {
      if (vm.loadingNew) {
        return;
      }
      vm.loadingNew = true;

      var pageable = new Pageable(0, pageSize);
      var params = {type: vm.type, contextId: vm.contextId};
      TimelineItemModel.pagedQueryWithPermissions(
          pageable, params, {}, ['manage', 'delete', 'accessoriginalauthor', 'like', 'comment', 'share'], 'new', 'page'
      ).then(function (richPage) {
        newItemIds = [];
        vm.newItemsCount = 0;

        if (!angular.isNumber(lastRefreshDate)) {
          lastRefreshDate = richPage.data.lastRefreshDate;
        }

        var page = richPage.page;
        if (page.last) {
          var removed = _.remove(vm.items, function (item) {
            return _.some(page.content, {id: item.id});
          });
          _.forEach(removed, function (item) {
            _unsubscribeItemDeleted(item.id);
          });

          pageOffset += page.numberOfElements;
          vm.items = _orderItems(_.concat(_setNew(page.content), vm.items));
        } else {
          currentPage = page;
          currentPage.offset = 0;
          pageOffset = 0;
          vm.items = _setNew(page.content);
          vm.itemsLast = page.last;
        }

        _.forEach(page.content, _subscribeItemDeleted);
      }).finally(function () {
        vm.loadingNew = false;
      });
    }

    function openFormModal() {
      modalService.open({
        templateUrl: 'app/modules/timeline/components/timeline-form/timeline-form-modal-wrapper.html',
        controller: /*@ngInject*/ function (contextId, timelineType) {
          var vm = this;
          vm.contextId = contextId;
          vm.timelineType = timelineType;
        },
        resolve: {
          contextId: function () {
            return vm.contextId;
          },
          timelineType: function () {
            return vm.type;
          }
        }
      });
    }

    // --------------------------------------------------------------------------------------------------------

    function _isPersonal() {
      return vm.type === 'personal';
    }

    function _getSortDate(item) {
      return _.chain(item.shares).filter(function (share) {
        return share.recipient && _.includes(vm.contextSenders, share.recipient.id);
      }).map('created').max().value() || item.created;
    }

    function _orderItems(items) {
      return $filter('orderBy')(items, ['-unread', function (item) {
        return -1 * _getSortDate(item);
      }]);
    }

    function _isNew(item) {
      return angular.isNumber(lastRefreshDate) && lastRefreshDate < _getSortDate(item);
    }

    function _setNew(items) {
      return _.map(items, function (item) {
        item.isNew = _isNew(item);
        return item;
      });
    }

    var unsubscribeItemsCreatedFn;
    function _subscribeItemsCreated() {
      var route = '/topic/timeline.' + vm.type + '.' + vm.contextId + '.item.created';
      unsubscribeItemsCreatedFn = socketService.subscribe(route, function (event) {
        _addItem(event.content);
      });
    }

    var unsubscribeItemsSharedFn;
    function _subscribeItemsShared() {
      var route = '/topic/timeline.' + vm.type + '.' + vm.contextId + '.item.shared';
      unsubscribeItemsSharedFn = socketService.subscribe(route, function (event) {
        if (_isPersonal()) {
          if (!_.includes(vm.contextSenders, event.content.recipient)) {
            var existing = _.findIndex(vm.items, {id: event.content.id});
            if (existing !== -1) {
              TimelineItemModel.get(event.content.id).then(function (item) {
                item.isNew = false;
                vm.items[existing] = item;
              });
            }
          } else {
            _addItem(event.content);
          }
        } else if (event.content.recipient === vm.contextId || _.some(vm.items, {id: event.content.id})) {
          _addItem(event.content);
        }
      });
    }

    var unsubscribeItemDeletedFns = {};
    function _subscribeItemDeleted(item) {
      var route = '/topic/timeline.item.' + item.id;
      unsubscribeItemDeletedFns[item.id] = socketService.subscribe(route, function (event) {
        $scope.$apply(function () {
          _removeItem(event.content);
        });
      }, 'deleted');
    }

    function _unsubscribeItemsCreated() {
      if (angular.isFunction(unsubscribeItemsCreatedFn)) {
        unsubscribeItemsCreatedFn();
        unsubscribeItemsCreatedFn = undefined;
      }
    }

    function _unsubscribeItemsShared() {
      if (angular.isFunction(unsubscribeItemsSharedFn)) {
        unsubscribeItemsSharedFn();
        unsubscribeItemsSharedFn = undefined;
      }
    }

    function _unsubscribeItemDeleted(itemId) {
      var unsubscribe = unsubscribeItemDeletedFns[itemId];
      if (angular.isFunction(unsubscribe)) {
        unsubscribe();
        delete unsubscribeItemDeletedFns[itemId];
      }
    }

    function _unsubscribeItemsDeleted() {
      _.forEach(unsubscribeItemDeletedFns, function (unsubscribeFn, itemId) {
        _unsubscribeItemDeleted(itemId);
      });
    }

    function _addItem(item) {
      if (!_isPersonal()) {
        // show new posts directly on sender timelines
        TimelineItemModel.getWithPermissions(item.id, {}, ['manage', 'delete', 'accessoriginalauthor', 'like', 'comment', 'share'])
            .then(function (newItem) {
              var wasOnTimeline = _.remove(vm.items, {id: newItem.id}).length > 0;
              if (wasOnTimeline) {
                _unsubscribeItemDeleted(newItem.id);
              } else {
                ++pageOffset;
              }
              vm.items.unshift(_.set(newItem, 'isNew', item.recipient === vm.contextId));
              vm.items = _orderItems(vm.items);
              _subscribeItemDeleted(newItem);
            })
            .catch(function (errorResponse) {
              if (_.get(errorResponse, 'data.errorStatus') === 'NOT_FOUND') {
                errorService.suppressNotification(errorResponse);
              }
            });
      } else if (currentUser.id === _.get(item, 'authorId')) {
        // show new posts directly on personal timeline if current user is author
        loadNew();
      } else if (!_.includes(newItemIds, item.id)) {
        $scope.$apply(function () {
          newItemIds.push(item.id);
          ++vm.newItemsCount;
          _subscribeItemDeleted(item);
        });
      }
    }

    function _removeItem(itemId) {
      _unsubscribeItemDeleted(itemId);

      // update new button
      vm.newItemsCount -= _.remove(newItemIds, {id: itemId}).length;

      // remove post from list
      _.remove(vm.items, {id: itemId});
      --pageOffset;
    }

    function _loadContext(user) {
      if (_isPersonal()) {
        subscriptionsService.getSubscriptionsByType(user.id, ['user', 'workspace', 'page'])
            .then(function (subscriptions) {
              vm.contextSenders = _.concat(_.map(subscriptions, 'targetId'), user.id);
            });
      } else {
        vm.contextSenders = [vm.contextId];
      }
    }

    function _defineCanPost(user) {
      if (_.isNil(vm.canPost)) {
        vm.canPost = user.hasGlobalPermissions('CREATE_TIMELINE_ITEM');
      }
    }

    function _init(canAccess) {
      vm.visible = canAccess;
      _unsubscribeItemsCreated();
      _unsubscribeItemsDeleted();

      if (canAccess) {
        authService.getUser().then(function (user) {
          currentUser = user;
          _loadContext(user);
          _defineCanPost(user);
          _subscribeItemsCreated();
          _subscribeItemsShared();
        });

        pageOffset = 0;
        currentPage = undefined;

        vm.items = [];
        vm.itemsLast = false;
        vm.newItemsCount = 0;

        if (_isPersonal()) {
          var params = {type: vm.type};
          TimelineItemModel.get('new/count', params).then(function (cnt) {
            vm.newItemsCount = cnt;
          });
        }

        loadMore();
      }
    }
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.workspaces')
      .directive('coyoCheckWorkspaceName', workspaceName);

  /**
   * @ngdoc directive
   * @name coyo.workspaces.coyoCheckWorkspaceName:coyoCheckWorkspaceName
   * @element A
   *
   * @description
   * Validates a workspace name.
   *
   * @example
   * <example module="example">
   *   <file name="index.html">
   *     <div ng-controller="ExampleCtrl">
   *       <input type="text" coyo-check-workspace-name>
   *       <input type="text" coyo-check-workspace-name="workspaceId">
   *     </div>
   *   </file>
   *   <file name="script.js">
   *     angular
   *       .module('example', [])
   *       .controller('ExampleCtrl', ExampleCtrl);
   *
   *     function ExampleCtrl($scope) {
   *       $scope.workspaceId = 'WorkspaceId';
   *     }
   *   </file>
   * </example>
   */
  function workspaceName($http, $q, $window, coyoEndpoints) {
    return {
      restrict: 'A',
      require: 'ngModel',
      link: function ($scope, elem, attrs, ngModel) {
        ngModel.$asyncValidators.workspaceName = function (modelValue) {
          if (modelValue) {
            return $http({
              method: 'GET',
              url: coyoEndpoints.workspace.checkName.replace('{name}', $window.encodeURIComponent(modelValue))
                  .replace('{workspaceId}', attrs.coyoCheckWorkspaceName)
            }).then(function (r) {
              if (r.data.taken) {
                return $q.reject();
              }
              return true;
            });
          } else {
            return $q.resolve(true);
          }
        };
      }
    };
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.workspaces')
      .directive('oyocWorkspaceActions', workspaceActions)
      .controller('WorkspaceActionsController', WorkspaceActionsController);

  /**
   * @ngdoc directive
   * @name coyo.workspaces.oyocWorkspaceActions:oyocWorkspaceActions
   * @restrict 'E'
   * @element OWN
   *
   * @description
   * Displays the membership actions for a workspace
   *
   * @requires $state
   */
  function workspaceActions() {
    return {
      restrict: 'E',
      templateUrl: 'app/modules/workspaces/components/workspace-actions/workspace-actions.html',
      scope: {},
      bindToController: {
        workspace: '='
      },
      controller: 'WorkspaceActionsController',
      controllerAs: '$ctrl'
    };
  }

  function WorkspaceActionsController($state) {
    var vm = this;
    vm.loading = false;

    vm.joinIcon = _getJoinIcon();

    vm.join = join;
    vm.leave = leave;

    function join() {
      if (vm.loading || vm.workspace.membershipStatus === 'REQUESTED') {
        return;
      }
      vm.loading = true;

      vm.workspace.join().then(function (response) {
        vm.workspace.membershipStatus = response.status;
        if (vm.workspace.membershipStatus === 'APPROVED') {
          $state.go('main.workspace.show', {idOrSlug: vm.workspace.slug});
        }
      }).finally(function () {
        vm.loading = false;
      });
    }

    function leave() {
      if (vm.loading) {
        return;
      }
      vm.loading = true;

      vm.workspace.leave().then(function () {
        vm.workspace.membershipStatus = 'NONE';
        vm.joinIcon = _getJoinIcon();
      }).finally(function () {
        vm.loading = false;
      });
    }

    function _getJoinIcon() {
      if (vm.workspace.membershipStatus === 'REQUESTED') {
        return ''; // no join button for pending requests
      }

      if (vm.workspace.membershipStatus === 'INVITED') {
        return 'zmdi-check';
      }
      if (vm.workspace.visibility === 'PROTECTED') {
        return 'zmdi-lock';
      }

      if (vm.workspace.visibility === 'PUBLIC') {
        return 'zmdi-plus';
      }
      return '';
    }

  }

})(angular);

(function (angular) {
  'use strict';

  /**
   * @ngdoc directive
   * @name coyo.workspaces.coyoWorkspaceAvatar:coyoWorkspaceAvatar
   * @restrict 'E'
   * @element OWN
   *
   * @description
   * Displays a workspace avatar.
   *
   * @param {object} workspace
   * The workspace to render the avatar for.
   *
   * @param {boolean=} noLink
   * Can be set to 'true' if the avatar image should not link to the workspace itself (default: 'false').
   *
   * @param {string=} avatarSize
   * Desired size of the avatar. Possible values are xs, sm, md, lg and xl.
   *
   * @requires $scope
   * @requires $state
   * @requires commons.target.targetService
   */
  angular
      .module('coyo.workspaces')
      .directive('coyoWorkspaceAvatar', workspaceAvatar)
      .controller('WorkspaceAvatarController', WorkspaceAvatarController);

  function workspaceAvatar() {
    return {
      restrict: 'E',
      replace: true,
      templateUrl: 'app/modules/workspaces/components/workspace-avatar/workspace-avatar.html',
      scope: {},
      bindToController: {
        workspace: '<',
        noLink: '<?',
        avatarSize: '@?'
      },
      controller: 'WorkspaceAvatarController',
      controllerAs: '$ctrl'
    };
  }

  function WorkspaceAvatarController($scope, $state, targetService) {
    var vm = this;
    var canLinkToWorkspace = false;

    vm.open = open;
    vm.errorHandler = errorHandler;

    function open(event) {
      if (vm.isLink) {
        event.stopPropagation();
        $state.go('main.workspace.show', {idOrSlug: vm.workspace.slug}, {inherit: false, reload: 'main.workspace.show'});
      }
    }

    function errorHandler() {
      vm.loadError = true;
    }

    (function _init() {
      // check whether user has permission to link to workspace
      targetService.onCanLinkTo(vm.workspace.target, function (canLink) {
        canLinkToWorkspace = canLink;
        vm.isLink = !vm.noLink && canLinkToWorkspace;
      });

      // check whether no link parameter is set or changed
      $scope.$watch(function () {
        return vm.noLink;
      }, function (newValue, oldValue) {
        if (newValue !== oldValue) {
          vm.isLink = !newValue && canLinkToWorkspace;
        }
      });
    })();

  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.workspaces')
      .directive('coyoWorkspaceMemberAvatars', workspaceMemberAvatars)
      .controller('WorkspaceMemberAvatarsController', WorkspaceMemberAvatarsController);

  /**
   * @ngdoc directive
   * @name coyo.workspaces.coyoWorkspaceMembers:coyoWorkspaceMembers
   * @element OWN
   * @restrict E
   * @scope
   *
   * @description
   * Renders an excerpt (max. 10 user) of the members of the given workspace. Members are listed by their user avatar.
   * The avatar images are overlapping each other. In addition the directive displays the overall member count of the
   * workspace.
   *
   * @param {object} workspace
   * The workspace to display the members of.
   */
  function workspaceMemberAvatars() {
    return {
      restrict: 'E',
      templateUrl: 'app/modules/workspaces/components/workspace-member-avatars/workspace-member-avatars.html',
      scope: {},
      bindToController: {
        workspace: '<'
      },
      controller: 'WorkspaceMemberAvatarsController',
      controllerAs: '$ctrl'
    };
  }

  function WorkspaceMemberAvatarsController($scope, $rootScope, Pageable) {
    var vm = this;

    vm.members = [];
    vm.memberCount = 0;
    vm.loading = false;

    function _loadMembers() {
      vm.loading = true;
      var pageable = new Pageable(0, 10);
      vm.workspace.getMembers(pageable).then(function (page) {
        vm.members = _.map(page.content, 'user');
        vm.memberCount = page.totalElements;
      }).finally(function () {
        vm.loading = false;
      });
    }

    (function _init() {
      _loadMembers();

      var addMember = $rootScope.$on('workspace.member:added', function () {
        if (vm.members.length < 10) {
          _loadMembers();
        } else { // only correct count
          ++vm.memberCount;
        }
      });
      $scope.$on('$destroy', addMember);

      var removeMember = $rootScope.$on('workspace.member:removed', function (event, userId) {
        if (_.some(vm.members, {id: userId})) {
          _loadMembers();
        } else { // only correct count
          --vm.memberCount;
        }
      });
      $scope.$on('$destroy', removeMember);
    })();
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.workspaces')
      .directive('coyoSelectWorkspaceCategory', selectWorkspaceCategory);

  function selectWorkspaceCategory(selectFactoryModel, WorkspaceCategoryModel, Pageable) {
    return selectFactoryModel({
      refresh: _refresh,
      emptyText: 'WORKSPACE.NO_CATEGORIES_FOUND',
      multiple: true
    });

    function _refresh(pageable, search) {
      var paramPageable = new Pageable(pageable.page, pageable.size);
      var paramSearch = {name: search};
      return WorkspaceCategoryModel.pagedQuery(paramPageable, paramSearch).then(function (response) {
        return angular.extend(_.map(response.content, function (category) {
          return {
            id: category.id,
            displayName: category.name
          };
        }), {
          meta: {
            last: response.last,
            number: response.number
          }
        });
      });
    }
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.workspaces')
      .directive('coyoWorkspaceSubscribe', WorkspaceSubscribe)
      .controller('WorkspaceSubscribeController', WorkspaceSubscribeController);

  /**
   * @ngdoc directive
   * @name coyo.workspaces.coyoWorkspaceSubscribe:coyoWorkspaceSubscribe
   * @element OWN
   * @restrict E
   * @scope
   *
   * @description
   * Renders a list item toggle button which a user can use to subscribe or unsubscribe to a workspace. If the user is
   * already subscribed, he can unsubscribe by clicking the link. If he is not subscribed and a member of the workspace,
   * he can subscribe to the workspace.
   *
   * @param {object} workspace
   * The workspace to subscribe or unsubscribe to.
   *
   * @param {object} user
   * The current user which should subscribe or unsubscribe.
   */
  function WorkspaceSubscribe() {
    return {
      restrict: 'E',
      replace: true,
      templateUrl: 'app/modules/workspaces/components/workspace-subscribe/workspace-subscribe.html',
      scope: {},
      bindToController: {
        workspace: '<',
        user: '<'
      },
      controller: 'WorkspaceSubscribeController',
      controllerAs: '$ctrl'
    };
  }

  function WorkspaceSubscribeController($scope, subscriptionsService) {
    var vm = this;

    vm.loading = false;
    vm.subscribed = false;

    vm.toggle = toggle;

    function toggle() {
      if (vm.loading) {
        return;
      }
      vm.loading = true;

      if (vm.subscribed) {
        _unsubscribe();
      } else {
        _subscribe();
      }
    }

    function _subscribe() {
      subscriptionsService.subscribe(vm.user.id, vm.workspace.id, 'workspace', vm.workspace.id);
    }

    function _unsubscribe() {
      subscriptionsService.unsubscribe(vm.user.id, vm.workspace.id);
    }

    function _updateSubscription(subscription) {
      vm.subscribed = angular.isDefined(subscription);
      vm.loading = false;
    }

    (function _init() {
      vm.loading = true;
      var eventHandler = subscriptionsService.onSubscriptionChange(vm.user.id, vm.workspace.id, _updateSubscription);
      $scope.$on('$destroy', eventHandler);
    })();

  }

})(angular);

