(function (angular) {
  'use strict';

  angular.module('coyo.admin.apps-widgets')
      .controller('AdminWidgetListController', AdminWidgetListController);

  function AdminWidgetListController($rootScope, $scope, $q, $timeout, widgetRegistry, WidgetConfigurationModel,
                                     widgetConfigs, adminWidgetSettingsModal) {
    var vm = this;

    vm.$onInit = onInit;
    vm.openSettings = openSettings;

    function toggle(widget) {
      var deferred = $q.defer();

      $timeout(function () { // wait for coyoCheckbox to update model
        if (widget.enabled) {
          WidgetConfigurationModel.enable(widget.key).finally(deferred.resolve);
        } else {
          widget.moderatorsOnly = false;
          WidgetConfigurationModel.disable(widget.key).finally(deferred.resolve);
        }
      });

      return deferred.promise;
    }

    function toggleModerator(widget) {
      $timeout(function () { // wait for coyoCheckbox to update model
        if (widget.moderatorsOnly) {
          WidgetConfigurationModel.restrict(widget.key);
        } else {
          WidgetConfigurationModel.derestrict(widget.key);
        }
      });
    }

    function openSettings(widget) {
      adminWidgetSettingsModal
          .open(widget)
          .then(_saveModalChanges);
    }

    function _saveModalChanges(changedWidget) {
      var widget = _.find(vm.widgets, {key: changedWidget.key});
      if (widget) {
        if (changedWidget.enabled !== widget.enabled) {
          widget.enabled = changedWidget.enabled;
          toggle(widget).then(function () {
            _checkAndToggleModerator(widget, changedWidget);
          });
        } else {
          _checkAndToggleModerator(widget, changedWidget);
        }
      }
    }

    function _checkAndToggleModerator(widget, changedWidget) {
      if (widget.enabled && changedWidget.moderatorsOnly !== widget.moderatorsOnly) {
        widget.moderatorsOnly = changedWidget.moderatorsOnly;
        toggleModerator(widget);
      }
    }

    function _loadWidgets() {
      vm.widgets = _.map(widgetRegistry.getAll(), function (widget) {
        return angular.extend(widget, {enabled: false, moderatorsOnly: false}, _.find(widgetConfigs, {key: widget.key}));
      });
    }

    function onInit() {
      vm.widgets = [];
      vm.toggle = toggle;
      vm.toggleModerator = toggleModerator;
      vm.isMobile = $rootScope.screenSize.isXs || $rootScope.screenSize.isSm;

      var unsubscribe = $rootScope.$on('screenSize:changed', function (event, screenSize) {
        vm.isMobile = screenSize.isXs || screenSize.isSm;
      });
      $scope.$on('$destroy', unsubscribe);

      _loadWidgets();
    }
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.admin.authenticationProviders')
      .component('oyocAuthenticationProviderSettings', authenticationProviderSettings())
      .controller('AuthenticationProviderSettingsController', AuthenticationProviderSettingsController);

  /**
   * @ngdoc directive
   * @name coyo.admin.authenticationProviders.oyocAuthenticationProviderSettings:oyocAuthenticationProviderSettings
   * @scope
   * @restrict 'E'
   * @element OWN
   *
   * @description
   * Renders the settings form of the given authentication provider.
   *
   * @param {string} type
   * The type of the authentication provider to display the settings for.
   * @param {object} form
   * The authentication provider form.
   * @param {object} settings
   * The settings of the authentication provider to display the settings for.
   * @param {object} extendedInfo
   * Any additional (read-only) properties calculated by the backend that are needed (e.g. for displaying urls etc)
   *
   * @requires $compile
   * @requires coyo.admin.authenticationProviders.api.authenticationProviderTypeRegistry
   */
  function authenticationProviderSettings() {
    return {
      scope: {},
      controller: 'AuthenticationProviderSettingsController',
      controllerAs: '$ctrl',
      template: '<div></div>',
      bindings: {
        form: '<',
        provider: '='
      }
    };
  }

  function AuthenticationProviderSettingsController($compile, $element, $scope, authenticationProviderTypeRegistry) {
    var vm = this;

    var initSettings = angular.copy(vm.provider.properties);
    var allSettings = {};

    $scope.$watch(function () {
      return vm.provider.type;
    }, function (newVal, oldVal) {
      if (!newVal) {
        return;
      }
      if (oldVal) {
        allSettings[oldVal] = angular.copy(vm.provider.properties);
      }
      _.set(vm, 'provider.properties', allSettings[newVal] || angular.copy(initSettings));

      var authenticationProvider = authenticationProviderTypeRegistry.get(newVal);
      var childDirective = angular.element('<' + authenticationProvider.directive + ' ng-model="$ctrl.provider" form="$ctrl.form"></' + authenticationProvider.directive + '>');
      $element.html(childDirective);
      $compile($element.contents())($scope);
    });
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.admin.authenticationProviders')
      .component('coyoSelectAuthenticationProviderType', selectAuthenticationProviderType())
      .controller('SelectAuthenticationProviderTypeController', SelectAuthenticationProviderTypeController);

  /**
   * @ngdoc directive
   * @name coyo.admin.authenticationProviders.coyoSelectAuthenticationProviderType:coyoSelectAuthenticationProviderType
   * @restrict E
   * @scope
   *
   * @description
   * Renders a UI select field for authentication provider type selection.
   *
   * @param {string} placeholder The placeholder for the input field
   * @param {boolean} ngDisabled Marks this field as disabled
   * @param {boolean} allowClear Marks this field as clearable
   *
   * @requires $rootScope
   * @requires $translate
   * @requires coyo.admin.authenticationProviders.api.authenticationProviderTypeRegistry
   */
  function selectAuthenticationProviderType() {
    return {
      scope: {},
      require: 'ngModel',
      controller: 'SelectAuthenticationProviderTypeController',
      controllerAs: '$ctrl',
      templateUrl: 'app/modules/admin/authentication-providers/components/select-authentication-provider-type/select-authentication-provider-type.html',
      bindings: {
        placeholder: '@?',
        ngDisabled: '<?',
        allowClear: '<?'
      }
    };
  }

  function SelectAuthenticationProviderTypeController($rootScope, $scope, $element, $translate, authenticationProviderTypeRegistry) {
    var vm = this;
    var ngModelCtrl = $element.controller('ngModel');

    vm.data = {
      providerTypes: [],
      selected: null
    };

    vm.$onInit = _init;

    function _translate() {
      var providerTypes = authenticationProviderTypeRegistry.getAll();
      return $translate(_.flatMap(providerTypes, function (providerType) {
        return [providerType.name, providerType.description];
      })).then(function (data) {
        vm.data.providerTypes = _.map(providerTypes, function (providerType) {
          return angular.extend(providerType, {
            name: data[providerType.name],
            description: data[providerType.description]
          });
        });
      });
    }

    function _init() {
      _translate();

      // translate on language change
      var deregisterFn = $rootScope.$on('$translateChangeSuccess', function () {
        _translate();
      });
      $scope.$on('$destroy', deregisterFn);

      // model -> select
      ngModelCtrl.$render = function () {
        vm.data.selected = ngModelCtrl.$viewValue;
      };

      // select -> model
      $scope.$watch(function () {
        return vm.data.selected;
      }, function (newVal, oldVal) {
        if (newVal !== oldVal) {
          ngModelCtrl.$setViewValue(newVal);
        }
      });
    }
  }

})(angular);

