(function (angular) {
  'use strict';

  angular
      .module('coyo.admin.themes')
      .controller('AdminThemeDetailsController', AdminThemeDetailsController);

  function AdminThemeDetailsController($state, $q, Upload, tempUploadService, coyoEndpoints, coyoNotification,
                                       themeService, backendUrl, themes, theme) {
    var vm = this;
    var tempUploadExpirySeconds = 1800;

    vm.themes = themes;
    vm.theme = theme;
    vm.theme.settings = vm.theme.settings || {};
    vm.theme.variables = vm.theme.variables || {};
    vm.variables = []; // temp. storage for variables
    vm.images = {}; // temp. storage for images
    vm.activeTab = 'colors';

    vm.settingConfigs = [
      {key: 'color-primary', displayName: 'ADMIN.THEMES.COLORS.COLOR_PRIMARY', defaultValue: '#374555'},
      {key: 'color-secondary', displayName: 'ADMIN.THEMES.COLORS.COLOR_GRADIENT', defaultValue: '#242f39'},
      {key: 'color-navbar-border', displayName: 'ADMIN.THEMES.COLORS.COLOR_NAVBAR_BORDER', defaultValue: '#9bbf29'},
      {key: 'coyo-navbar-text', displayName: 'ADMIN.THEMES.COLORS.COLOR_NAVBAR_TEXT', defaultValue: '#fdfdfd'},
      {key: 'coyo-navbar', displayName: 'ADMIN.THEMES.COLORS.COLOR_NAVBAR', defaultValue: '#374555'},
      {key: 'coyo-navbar-active', displayName: 'ADMIN.THEMES.COLORS.COLOR_NAVBAR_ACTIVE', defaultValue: '#26303B'},
      {key: 'btn-primary-color', displayName: 'ADMIN.THEMES.COLORS.COLOR_BTN_PRIMARY_COLOR', defaultValue: '#fff'},
      {key: 'btn-primary-bg', displayName: 'ADMIN.THEMES.COLORS.COLOR_BTN_PRIMARY_BG', defaultValue: '#374555'},
      {key: 'color-background-main', displayName: 'ADMIN.THEMES.COLORS.COLOR_BACKGROUND_MAIN', defaultValue: '#e8e8e8'},
      {key: 'text-color', displayName: 'ADMIN.THEMES.COLORS.COLOR_TEXT', defaultValue: '#333'},
      {key: 'link-color', displayName: 'ADMIN.THEMES.COLORS.COLOR_LINK', defaultValue: '#317DC1'}
    ];

    vm.imageConfigs = [{
      key: 'image-coyo-front',
      retinaKey: 'image-coyo-front-hd',
      displayName: 'ADMIN.THEMES.LOGOS.IMAGE_COYO_FRONT.DISPLAY_NAME',
      help: 'ADMIN.THEMES.LOGOS.IMAGE_COYO_FRONT.HELP',
      defaultUrl: '/assets/images/logos/coyo/logo-coyo-inversed-front-hd.png',
      width: 476,
      // workaround for bug in chrome, where image/* causes the file select dialog to open very slowly
      // http://stackoverflow.com/questions/39187857/inputfile-accept-image-open-dialog-so-slow-with-chrome
      acceptTypes: 'image/png, image/jpeg, image/gif',
      // variables are set to the result of the given fn (must be a promise), after a new image has been selected
      variables: {
        'height-image-front': function (originalFile, resizedFile) {
          return Upload.imageDimensions(resizedFile).then(function (dimensions) {
            return dimensions.height + 'px';
          });
        }
      }
    }, {
      key: 'image-coyo-nav',
      retinaKey: 'image-coyo-nav-hd',
      displayName: 'ADMIN.THEMES.LOGOS.IMAGE_COYO_NAV.DISPLAY_NAME',
      help: 'ADMIN.THEMES.LOGOS.IMAGE_COYO_NAV.HELP',
      defaultUrl: '/assets/images/logos/coyo/logo-coyo-inversed-nav-hd.png',
      height: 50,
      acceptTypes: 'image/png, image/jpeg, image/gif',
      // variables are set to the result of the given fn (must be a promise), after a new image has been selected
      variables: {
        'width-navbar-brand': function (originalFile, resizedFile) {
          return Upload.imageDimensions(resizedFile).then(function (dimensions) {
            return dimensions.width + 'px';
          });
        }
      }
    }, {
      key: 'image-coyo-favicon',
      displayName: 'ADMIN.THEMES.LOGOS.IMAGE_COYO_FAVICON.DISPLAY_NAME',
      help: 'ADMIN.THEMES.LOGOS.IMAGE_COYO_FAVICON.HELP',
      defaultUrl: '/assets/images/logos/coyo/favicon.ico',
      acceptTypes: 'image/x-icon, image/vnd.microsoft.icon'
    },
    {
      key: 'image-coyo-appleicon',
      displayName: 'ADMIN.THEMES.LOGOS.IMAGE_COYO_APPLE_ICON.DISPLAY_NAME',
      help: 'ADMIN.THEMES.LOGOS.IMAGE_COYO_APPLE_ICON.HELP',
      defaultUrl: '/assets/images/logos/coyo/apple-touch-icon.png',
      width: 180,
      height: 180,
      acceptTypes: 'image/x-icon, image/png'
    }];

    // ====================

    vm.addVariable = addVariable;
    vm.removeVariable = removeVariable;
    vm.uploadImage = uploadImage;
    vm.removeImage = removeImage;
    vm.getImgUrl = getImgUrl;
    vm.save = save;

    // ====================

    function uploadImage(imageKey) {
      var imageConfig = _.find(vm.imageConfigs, {key: imageKey});
      var imageData = vm.images[imageKey];
      if (!imageData.fileToUpload) {
        return;
      }

      // regular size
      _resize(imageData.fileToUpload, imageConfig, false).then(function (file) {
        tempUploadService.upload(file, tempUploadExpirySeconds).then(function (blob) {
          imageData.file = file;
          imageData.uid = blob.uid;
          vm.theme.settings[imageConfig.key] = _createUrl(blob.uid);
          _.forEach(_.get(imageConfig, 'variables', {}), function (fn, key) {
            $q.when(fn(imageData.fileToUpload, file)).then(function (value) {
              vm.theme.settings[key] = value;
            });
          });
        });
      });

      // retina size
      if (imageConfig.retinaKey) {
        _resize(imageData.fileToUpload, imageConfig, true).then(function (file) {
          tempUploadService.upload(file, tempUploadExpirySeconds).then(function (blob) {
            imageData.retinaFile = file;
            imageData.retinaUid = blob.uid;
            vm.theme.settings[imageConfig.retinaKey] = _createUrl(blob.uid);
          });
        });
      }
    }

    function removeImage(imageKey) {
      vm.images[imageKey] = {};
      var imageConfig = _.find(vm.imageConfigs, {key: imageKey});
      _.concat(imageConfig.key, imageConfig.retinaKey, _.keys(imageConfig.variables)).forEach(function (key) {
        delete vm.theme.settings[key];
      });
    }

    function addVariable() {
      vm.variables.push({key: '', value: ''});
    }

    function removeVariable(index) {
      vm.variables.splice(index, 1);
    }

    function getImgUrl(url) {
      return url ? url.replace(new RegExp('\'', 'g'), '') : undefined;
    }

    function save() {
      var isNew = vm.theme.isNew();
      vm.theme.variables = _.pickBy(_toThemeVariables(vm.variables), _.identity);
      vm.theme.fileIds = _collectFileIds();

      return vm.theme.save().then(function () {
        vm.errorMessage = undefined;
        themeService.applyTheme();
        if (isNew || vm.themes.length > 1) {
          $state.go('^.list', {}, {reload: true});
        } else {
          coyoNotification.success('ADMIN.THEMES.SAVE.SUCCESS');
          vm.images = {};
        }
      }, function (error) {
        vm.errorMessage = error.data.message;
      });
    }

    // ====================

    function _resize(file, config, isRetina) {
      var factor = isRetina ? 2 : 1;
      if (!config.width && !config.height) {
        return $q.resolve(file);
      }

      var targetWidth = config.width ? config.width * factor : null;
      var targetHeight = config.height ? config.height * factor : null;
      return Upload.resize(file, targetWidth, targetHeight, null, null, null, null, function resizeIf(width, height) {
        if (config.width && config.width * factor > width) {
          return false;
        } else if (config.height && config.height * factor > height) {
          return false;
        }
        return true;
      });
    }

    function _collectFileIds() {
      var keys = _.map(vm.imageConfigs, 'key');
      return _.flatMap(keys, function (key) {
        return _(vm.images[key]).pick('uid', 'retinaUid').values().value();
      });
    }

    function _createUrl(uid) {
      return '\'' + backendUrl + coyoEndpoints.theme.files.replace('{id}', uid) + '\'';
    }

    function _toThemeVariables(variables) {
      var result = {};
      _.forEach(variables, function (variable) {
        result[variable.key] = variable.value;
      });
      return result;
    }

    (function _init() {
      // init vm.variables
      _.forEach(theme.variables, function (value, key) {
        vm.variables.push({key: key, value: value});
      });
      // init vm.imageConfigs
      _.forEach(vm.imageConfigs, function (imageConfig) {
        vm.images[imageConfig.key] = {};
      });
    })();
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.admin.themes')
      .controller('AdminThemeListController', AdminThemeListController);

  function AdminThemeListController($rootScope, $scope, $sessionStorage, ThemeModel, UserModel, GroupModel, modalService, filterFilter, themes) {
    var vm = this;

    vm.themes = themes;
    vm.isMobile = $rootScope.screenSize.isXs || $rootScope.screenSize.isSm;
    vm.actions = _buildActions();
    vm.treeOptions = _buildOptions();

    vm.onChangeName = onChangeName;

    vm.users = {};
    vm.loadUsers = _loadCached(vm.users, 'userIds', 5, function (userIds) {
      return UserModel.query({userIds: userIds});
    });
    vm.groups = {};
    vm.loadGroups = _loadCached(vm.groups, 'userGroupIds', 5, function (groupIds) {
      return GroupModel.query({groupIds: groupIds});
    });

    // ----------

    function onChangeName(term) {
      $sessionStorage.themeList = {displayName: term};
      vm.isFiltered = !!term;
      vm.themes = term ? filterFilter(themes, $sessionStorage.themeList) : themes;
    }

    // ----------

    function _buildActions() {
      return {
        deleteTheme: function (theme) {
          modalService.confirm({
            title: 'ADMIN.THEMES.OPTIONS.DELETE.MODAL.TITLE',
            text: 'ADMIN.THEMES.OPTIONS.DELETE.MODAL.TEXT',
            close: {icon: 'delete', title: 'DELETE', style: 'btn-danger'},
            dismiss: {title: 'CANCEL'}
          }).result.then(function () {
            vm.userAction = true;
            theme.delete().then(function () {
              _.remove(themes, {id: theme.id});
              _.remove(vm.themes, {id: theme.id});
            }).finally(function () {
              vm.userAction = false;
            });
          });
        }
      };
    }

    function _buildOptions() {
      var cellWidths;
      return {
        dropped: function (event) {
          // persist new sort order
          if (event.source.index !== event.dest.index) {
            if (event.dest.index === vm.themes.length - 1) {
              var theme = vm.themes[vm.themes.length - 1];
              vm.themes[vm.themes.length - 1] = vm.themes[vm.themes.length - 2];
              vm.themes[vm.themes.length - 2] = theme;
            }
            ThemeModel.order(_.map(vm.themes, 'id'));
          }
        },
        beforeDrag: function (scope) {
          // save original cell widths
          cellWidths = [];
          scope.$element.children().each(function () {
            cellWidths.push(angular.element(this).width()); // eslint-disable-line angular/controller-as-vm
          });
          return !scope.theme.default;
        },
        dragStart: function (event) {
          // set dragging cell widths
          event.elements.dragging.find('td').each(function (i) {
            angular.element(this).width(cellWidths[i]); // eslint-disable-line angular/controller-as-vm
          });
        },
        beforeDrop: function (event) {
          // remove dragging cell widths
          event.elements.dragging.find('td').each(function () {
            angular.element(this).width(''); // eslint-disable-line angular/controller-as-vm
          });
        },
        accept: function (sourceNodeScope, destNodesScope, destIndex) {
          return destIndex < themes.length - 1;
        }
      };
    }

    function _loadCached(cache, attr, limit, resolver) {
      return function (theme) {
        if (!cache[theme.id]) {
          cache[theme.id] = true;
          var ids = _.get(theme, attr, []).slice(0, limit + 1);
          if (!ids.length) {
            cache[theme.id] = [];
          } else {
            resolver(ids).then(function (result) {
              cache[theme.id] = _.map(result, 'displayName');
            }).catch(function () {
              cache[theme.id] = false;
            });
          }
        }
      };
    }

    // ----------

    (function _init() {
      var unsubscribe = $rootScope.$on('screenSize:changed', function (event, screenSize) {
        vm.isMobile = screenSize.isXs || screenSize.isSm;
      });
      $scope.$on('$destroy', unsubscribe);

      vm.queryParams = angular.extend({displayName: ''}, $sessionStorage.themeList);
      onChangeName(vm.queryParams.displayName);
    })();
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.admin.userDirectories')
      .component('coyoCheckUserDirectoryConnection', checkUserDirectoryConnection())
      .controller('CheckUserDirectoryConnectionController', CheckUserDirectoryConnectionController);

  /**
   * @ngdoc directive
   * @name coyo.admin.userDirectories.coyoCheckUserDirectoryConnection:coyoCheckUserDirectoryConnection
   * @restrict E
   * @scope
   *
   * @description
   * Renders a button to check the connection settings for the user directory with the given type.
   *
   * @param {string} type
   * The type of the directory.
   * @param {object} settings
   * The connection settings of the directory.
   *
   * @requires $timeout
   * @requires coyo.domain.UserDirectoryModel
   */
  function checkUserDirectoryConnection() {
    return {
      scope: {},
      controller: 'CheckUserDirectoryConnectionController',
      controllerAs: '$ctrl',
      templateUrl: 'app/modules/admin/user-directories/components/check-user-directory-connection/check-user-directory-connection.html',
      bindings: {
        directory: '<',
        type: '@',
        settings: '<'
      }
    };
  }

  function CheckUserDirectoryConnectionController($timeout, UserDirectoryModel) {
    var vm = this;
    var stateDefault = {iconClass: 'zmdi-wifi-alt', btnClass: 'btn-default'};
    var stateLoading = {iconClass: 'zmdi-spinner zmdi-hc-spin', btnClass: 'btn-default'};
    var stateSuccess = {iconClass: 'zmdi-check', btnClass: 'btn-success'};
    var stateError = {iconClass: 'zmdi-alert-circle', btnClass: 'btn-danger'};

    vm.state = stateDefault;
    vm.error = null;

    vm.checkConnection = checkConnection;

    function checkConnection() {
      if (vm.state !== stateDefault) {
        return;
      }

      vm.loading = true;
      vm.state = stateLoading;
      if (vm.directory.id) {
        checkForExistingDirectory();
      } else {
        checkForNewDirectory();
      }
    }

    function checkForExistingDirectory() {
      vm.directory.checkConnection(vm.settings)
          .then(handleSuccess)
          .catch(handleError)
          .finally(finalize);
    }

    function checkForNewDirectory() {
      UserDirectoryModel.checkConnection(vm.type, vm.settings)
          .then(handleSuccess)
          .catch(handleError)
          .finally(finalize);
    }

    function handleError(msg) {
      vm.state = stateError;
      vm.error = msg;
    }
    function finalize() {
      vm.loading = false;
      $timeout(function () {
        vm.state = stateDefault;
      }, 1000);
    }

    function handleSuccess() {
      vm.state = stateSuccess;
      vm.error = null;
    }
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.admin.userDirectories')
      .component('coyoSelectUserDirectoryType', selectUserDirectoryType())
      .controller('SelectUserDirectoryTypeController', SelectUserDirectoryTypeController);

  /**
   * @ngdoc directive
   * @name coyo.admin.userDirectories.coyoSelectUserDirectoryType:coyoSelectUserDirectoryType
   * @restrict E
   * @scope
   *
   * @description
   * Renders a UI select field for user directory type selection.
   *
   * @param {string} placeholder
   * The placeholder for the input field.
   * @param {boolean} ngDisabled
   * Marks this field as disabled.
   * @param {boolean} allowClear
   * Marks this field as clearable.
   *
   * @requires $rootScope
   * @requires $translate
   * @requires coyo.admin.userDirectories.api.userDirectoryTypeRegistry
   */
  function selectUserDirectoryType() {
    return {
      scope: {},
      require: 'ngModel',
      controller: 'SelectUserDirectoryTypeController',
      controllerAs: '$ctrl',
      templateUrl: 'app/modules/admin/user-directories/components/select-user-directory-type/select-user-directory-type.html',
      bindings: {
        placeholder: '@?',
        ngDisabled: '<?',
        allowClear: '<?'
      }
    };
  }

  function SelectUserDirectoryTypeController($rootScope, $scope, $element, $translate, userDirectoryTypeRegistry) {
    var vm = this;
    var ngModelCtrl = $element.controller('ngModel');

    vm.data = {
      directoryTypes: [],
      selected: null
    };

    vm.$onInit = _init;

    function _translate() {
      var directoryTypes = userDirectoryTypeRegistry.getAll();
      return $translate(_.flatMap(directoryTypes, function (directoryType) {
        return [directoryType.name, directoryType.description];
      })).then(function (data) {
        vm.data.directoryTypes = _.map(directoryTypes, function (directoryType) {
          return angular.extend(directoryType, {
            name: data[directoryType.name],
            description: data[directoryType.description]
          });
        });
      });
    }

    function _init() {
      _translate();

      // translate on language change
      var deregisterFn = $rootScope.$on('$translateChangeSuccess', function () {
        _translate();
      });
      $scope.$on('$destroy', deregisterFn);

      // model -> select
      ngModelCtrl.$render = function () {
        vm.data.selected = ngModelCtrl.$viewValue;
      };

      // select -> model
      $scope.$watch(function () {
        return vm.data.selected;
      }, function (newVal, oldVal) {
        if (newVal !== oldVal) {
          ngModelCtrl.$setViewValue(newVal);
        }
      });
    }
  }

})(angular);

(function (angular) {
  'use strict';

  angular
      .module('coyo.admin.userDirectories')
      .directive('coyoSelectUserDirectory', selectUserDirectory);
  /**
   * @ngdoc directive
   * @name coyo.admin.userDirectories.coyoSelectUserDirectory:coyoSelectUserDirectory
   * @element ANY
   * @restrict E
   * @scope
   *
   * @description
   * Renders a UI select field for a user directory.
   *
   */
  function selectUserDirectory(selectFactoryModel, UserDirectoryModel, userDirectoryTypeRegistry, $translate, $filter) {

    var userDirectories = null;

    return selectFactoryModel({
      refresh: _refresh,
      sublines: ['type'],
      emptyText: 'ADMIN.USER_DIRECTORIES.NO_USER_DIRECTORIES_FOUND'
    });

    function _refresh(pageable, search) {
      if (userDirectories === null) {
        userDirectories = UserDirectoryModel.query().then(function (response) {
          return _.map(response, function (userDirectory) {
            var type = userDirectoryTypeRegistry.get(userDirectory.type);
            return {
              id: userDirectory.id,
              displayName: userDirectory.name,
              type: $translate.instant(type.name)
            };
          });
        });
      }

      return userDirectories.then(function (data) {
        var filtered = search ? $filter('filter')(data, function (item) {
          return item.displayName.toLowerCase().includes(search.toLowerCase()) ||
              item.type.toLowerCase().includes(search.toLowerCase());
        }) : data;
        return angular.extend(filtered, {
          meta: {
            last: true,
            number: 0
          }
        });
      });
    }
  }

})(angular);

